import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  ResponsavelServiceProxy,
  CreateResponsavelDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { CreatePessoaDialogBaseComponent } from '@app/pessoas/create-pessoa-dialog-base/create-pessoa-dialog-base.component';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './create-responsavel-dialog.component.html'
})
export class CreateResponsavelDialogComponent extends CreatePessoaDialogBaseComponent implements OnInit {
  constructor(
    injector: Injector,
    public _responsavelService: ResponsavelServiceProxy,
    public bsModalRef: BsModalRef
  ) {
    super(injector, bsModalRef);
  }
  pessoa : CreateResponsavelDto;
  protected override initEmptyPessoaDto(): void {
    this.pessoa = new CreateResponsavelDto();
    super.initEmptyPessoaDto();
    this.pessoa.dependentes = [];
  }

  protected create(): Observable<any> {
    return this._responsavelService.create(this.pessoa);
  }
}
