import { AppComponentBase } from '@shared/app-component-base';
import { ChangeDetectorRef, Component, EventEmitter, Injector, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ColaboradorServiceProxy,
  ColaboradorDto,
  FuncaoOrgDto,
  ColabFuncaoOrgDto,
  CasoDto,
  PaisServiceProxy,
  CreateUserPessoaDto,
  UserDto
} from '@shared/service-proxies/service-proxies';
import { PessoaDetailsBaseComponent } from '@app/pessoas/pessoa-details-base/pessoa-details-base.component';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { HistoricoFuncaoTableComponent } from './historico-funcoes-table/historico-funcoes-table.component';

@Component({
  templateUrl: './colaborador-details.component.html',
  animations: [appModuleAnimation()]
})
export class ColaboradorDetailsComponent extends PessoaDetailsBaseComponent implements OnInit {
  public pessoa: ColaboradorDto;
  public funcoes: FuncaoOrgDto[];
  public historicoFuncoes: ColabFuncaoOrgDto[];
  public casos: CasoDto[];

  public gerenciados: ColaboradorDto[];
  public gerenciadosFuncoes: ColabFuncaoOrgDto[];

  protected initEmptyPessoa() : ColaboradorDto { return new ColaboradorDto() }
  
  public refreshHistorico: EventEmitter<undefined> = new EventEmitter<undefined>();
  constructor(injector: Injector,
    protected _colaboradorService: ColaboradorServiceProxy,
    protected _paisService: PaisServiceProxy,
    protected _modalService: BsModalService,
    protected _changeDetector: ChangeDetectorRef,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector, _paisService, _modalService, _router, _route);
  }
  getPessoa(): Observable<ColaboradorDto> {
    const params = this._route.snapshot.paramMap;
    this.id = Number(params.get("colaboradorId"));
    return this._colaboradorService.get(this.id);
  }

  protected getTipoPessoa(): string {
    return "Colaborador";
  }

  busy: boolean = false;

  getFuncaoOrg(id) {return this.funcoes?.find(f => f.id == id) }
  cfGerencia(cf: ColabFuncaoOrgDto): boolean { return (this.getFuncaoOrg(cf.funcaoOrgId)?.funcoesGeridasDiretamente?.length??0)>0 }
  showAbaCasos() {
    if (this.permission.isGranted("Business.Admin.People"))
      return this.historicoFuncoes?.some(cf => this.getFuncaoOrg(cf.funcaoOrgId)?.acessoCasoNome == "Caso a Caso");
    return false;
  }
  showAbaGerenciados() { return this.historicoFuncoes?.some(this.cfGerencia.bind(this))}
  gerentesCF: ColabFuncaoOrgDto[];
  gerentes: ColaboradorDto[];

  override createUser(input: CreateUserPessoaDto): Observable<UserDto> {
    return this._colaboradorService.createUserParaPessoa(input);
  }
}
