import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  ProtocoloAvaliacaoServiceProxy,
  DominioDto,
  UpdateDominioDto,
  UpdateProtocoloAvaliacaoDto,
  ProtocoloAvaliacaoDto,
  CreateHabilidadeDto,
  TipoConfirmacaoHabilidadeDto
} from '@shared/service-proxies/service-proxies';

@Component({
  templateUrl: './add-habilidade-dialog.component.html'
})
export class AddHabilidadeDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean;
  
  tiposConfirmacao: TipoConfirmacaoHabilidadeDto[] = [];
  constructor(injector: Injector,
    public _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit(): void {
    this.habilidade = new CreateHabilidadeDto();
    this.habilidade.isPersonalizada = false;
  }

  get codificada(): boolean  { return !this.habilidade.isPersonalizada }
  set codificada(v: boolean) { this.habilidade.isPersonalizada = !v }
  protocoloAvaliacao: ProtocoloAvaliacaoDto;
  dominio : DominioDto;
  habilidade : CreateHabilidadeDto;
  @Output() onSave = new EventEmitter();
  protected add() {
    this.saving = true;
    this.habilidade.dominioId = this.dominio.id;
    this._protocoloAvaliacaoService.createHabilidade(this.habilidade).subscribe(
      () => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.bsModalRef.hide();
        this.saving = false;
        this.onSave.emit();
      }
    );
  }
}
