import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output,
    Input
} from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    AlunoServiceProxy,
    AlunoDto,
    CasoServiceProxy,
    RegistroSupervisaoDto,
    AtuacaoDto,
    ColaboradorDto,
    ColabFuncaoOrgDto
} from '@shared/service-proxies/service-proxies';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import { PagedListingComponentBase, PagedRequestDto } from '@shared/paged-listing-component-base';
import { finalize } from 'rxjs';
import { RegistroSupervisaoDialogComponent } from './registro-supervisao-dialog/registro-supervisao-dialog.component';

@Component({
    selector: 'supervisoes-caso',
    templateUrl: './supervisoes-caso.component.html',
    animations: [appModuleAnimation()]
})
export class SupervisoesCasoComponent extends PagedListingComponentBase<RegistroSupervisaoDto> implements OnInit {
    constructor(injector: Injector,
        public _alunoService: AlunoServiceProxy,
        private _casoService: CasoServiceProxy,
        private _modalService: BsModalService,
        public bsModalRef: BsModalRef) {
        super(injector);
    }
    
    private _aluno: AlunoDto;
    public get aluno(): AlunoDto  { return this._aluno }
    @Input()
    public set aluno(v: AlunoDto) { this._aluno = v; this.refresh(); }

    @Input()
    public colaboradores: ColaboradorDto[];
    @Input()
    public funcoesColaborador: ColabFuncaoOrgDto[];
    @Input()
    public staff: AtuacaoDto[];

    @Output() registroCallback = new EventEmitter<undefined>();

    ngOnInit(): void { }

    private _zeroList(pageNumber) { this.supervisoes = []; this.showPaging({ 'items': [], 'totalCount': 0 }, pageNumber) }
    
    isTableLoading: boolean;
    supervisoes: RegistroSupervisaoDto[];
    protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
        if (this.aluno?.casoId != undefined) {
            this.isTableLoading = true;
            this._casoService.getAllRegistrosSupervisao(this.aluno.casoId, undefined, undefined, undefined, undefined)
            .pipe(finalize(() => {
                finishedCallback();
            }))
            .subscribe({
                'next': (res) => { 
                    this.supervisoes = res; 
                    this.showPaging({ 'items': res, 'totalCount': res.length }, pageNumber);
                    this.isTableLoading = false; 
                },
                'error': () => { this._zeroList(pageNumber); this.isTableLoading = false; }
            });
        } else {
            finishedCallback();
            this._zeroList(pageNumber);
        }
    }
    protected delete(entity: RegistroSupervisaoDto): void {
        
    }
    getAtuacao(id): AtuacaoDto { return this.staff?.find(a => a.id == id) }
    getColabFuncao(id): ColabFuncaoOrgDto { return this.funcoesColaborador?.find(cf => cf.id == id) }
    getColaborador(id): ColaboradorDto { return this.colaboradores?.find(c => c.id == id) }
    getColaboradorByCFId(id): ColaboradorDto { return this.getColaborador(this.getColabFuncao(id).id) }
    getColaboradorByAtuacaoId(id): ColaboradorDto { return this.getColaboradorByCFId(this.getAtuacao(id).colabFuncaoId) }
    getNomeSupervisor(id: number):string {
        return this.getColaboradorByAtuacaoId(id)?.nome??"";
    }
    getParticipacoesString(supervisao: RegistroSupervisaoDto) : string {
        let nomes = []
        supervisao?.participacoes?.forEach((p) => nomes.push(this.getColaborador(this.getColabFuncao(this.getAtuacao(p.atuacaoParticipanteId)?.colabFuncaoId)?.colaboradorId)?.nome))
        if (nomes.length == 0) return "";
        if (nomes.length == 1) return nomes[0];
        let s = " e " + nomes.splice(nomes.length - 1, 1)[0];
        s = nomes.splice(nomes.length - 1, 1)[0] + s;
        while (nomes.length > 0)
            s = nomes.splice(nomes.length - 1, 1)[0] + ", " + s
        return s;
    }
    registrarSupervisao() {
        let registroSupervisaoDialog: BsModalRef;
        registroSupervisaoDialog = this._modalService.show(
            RegistroSupervisaoDialogComponent,
            {
                class: 'modal-lg',
                initialState: {
                    aluno: this.aluno,
                    colaboradores: this.colaboradores,
                    funcoesColaborador: this.funcoesColaborador,
                    staff: this.staff
                }
            }
        );

        registroSupervisaoDialog.content.onSave.subscribe((result) => {
            if (result != undefined) {
                this.supervisoes.push(result)
                this.showPaging({ 'items': this.supervisoes, 'totalCount': this.supervisoes.length }, this.pageNumber);
                this.registroCallback.emit()
            }   
        });
    }
}
