import {
    Component,
    Injector,
    OnInit,
    Input,
    EventEmitter,
    Output
} from '@angular/core';
import { ControlContainer, NgModelGroup} from '@angular/forms'
import { AppComponentBase } from '@shared/app-component-base';
import { CreateNivelDto, CreateDominioDto, CreateHabilidadeDto, NivelDto, DominioDto, HabilidadeDto, ProtocoloAvaliacaoDto, TipoConfirmacaoHabilidadeDto, UpdateNivelDto } from '@shared/service-proxies/service-proxies';

@Component({
    selector: 'nivel-form',
    templateUrl: './nivel-form.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class NivelFormComponent extends AppComponentBase implements OnInit{
    @Input() disabled: boolean = false;
    @Input() nivel: CreateNivelDto | NivelDto;
    @Input() allDominios: DominioDto[];
    @Input() allHabilidades: HabilidadeDto[];
    @Input() tiposConfirmacao: TipoConfirmacaoHabilidadeDto[] = [];
    public constructor(
        injector: Injector
    ) {
        super(injector);
    }

    ngOnInit(): void {
        if (this.nivel.mesIniFaixaEtaria != undefined) {
            this._faixaIniAnos = Math.floor(this.nivel.mesIniFaixaEtaria / 12);
            this._faixaIniMeses = this.nivel.mesIniFaixaEtaria % 12;
        }
        if (this.nivel.mesFimFaixaEtaria != undefined) {
            this._faixaFimAnos = Math.floor(this.nivel.mesFimFaixaEtaria / 12);
            this._faixaFimMeses = this.nivel.mesFimFaixaEtaria % 12;
        }
    }


    get isNovo() { return this.nivel instanceof CreateNivelDto }
    createDominio() {
        if (this.nivel instanceof CreateNivelDto)
            this.nivel.dominios.push(new CreateDominioDto());
    }

    removeDominio(i: number) {
        if (this.nivel instanceof CreateNivelDto)
            this.nivel.dominios.splice(i,1);
    }
    get dominios(): DominioDto[] | CreateDominioDto[] {
        if (this.nivel instanceof CreateNivelDto)
            return this.nivel.dominios;
        else if (this.nivel instanceof NivelDto) {
            let nivId = this.nivel.id;
            return this.allDominios?.filter(d => d.nivelId == nivId)??[];
        }
    }

    private _faixaIniAnos: number; 
    private _faixaIniMeses: number; 
    private _faixaFimAnos: number; 
    private _faixaFimMeses: number; 
    private _updateFaixaIni() {
        if (this._faixaIniAnos == undefined && this._faixaIniMeses == undefined) {
            this.nivel.mesFimFaixaEtaria = undefined;
            return;
        }
        this.nivel.mesIniFaixaEtaria = (this._faixaIniAnos??0)*12+(this._faixaIniMeses??0)
    }
    private _updateFaixaFim() {
        if (this._faixaFimAnos == undefined && this._faixaFimMeses == undefined) {
            this.nivel.mesFimFaixaEtaria = undefined;
            return;
        }
        this.nivel.mesFimFaixaEtaria = (this._faixaFimAnos??0)*12+(this._faixaFimMeses??0)
    }
    get faixaIniAnos(): string  { return this._faixaIniAnos?.toString()??''; }
    set faixaIniAnos(s: string) { 
        if ((s??'') == '') {
            this._faixaIniAnos = undefined;
        } else {
            this._faixaIniAnos = Number(s);
        }
        this._updateFaixaIni();
    }
    get faixaIniMeses(): string  { return this._faixaIniMeses?.toString()??''; }
    set faixaIniMeses(s: string) { 
        if ((s??'') == '') {
            this._faixaIniMeses = undefined;
        } else {
            this._faixaIniMeses = Number(s);
        }
        this._updateFaixaIni();
    }
    get faixaFimAnos(): string  { return this._faixaFimAnos?.toString()??''; }
    set faixaFimAnos(s: string) { 
        if ((s??'') == '') {
            this._faixaFimAnos = undefined;
        } else {
            this._faixaFimAnos = Number(s);
        }
        this._updateFaixaFim();
    }
    get faixaFimMeses(): string  { return this._faixaFimMeses?.toString()??''; }
    set faixaFimMeses(s: string) { 
        if ((s??'') == '') {
            this._faixaFimMeses = undefined;
        } else {
            this._faixaFimMeses = Number(s);
        }
        this._updateFaixaFim();
    }
}