import { AppComponentBase } from '@shared/app-component-base';
import { Component, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  FormatoEnsinoServiceProxy,
  FormatoEnsinoDto
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { EditFormatoEnsinoDialogComponent } from '../edit-formato-ensino-dialog/edit-formato-ensino-dialog.component';

@Component({
  templateUrl: './formato-ensino-details.component.html',
  animations: [appModuleAnimation()]
})
export class FormatoEnsinoDetailsComponent extends AppComponentBase implements OnInit {
  public formatoEnsino: FormatoEnsinoDto;
  
  constructor(injector: Injector,
    protected _formatoEnsinoService: FormatoEnsinoServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  private id: number;
  ngOnInit() { 
    const params = this._route.snapshot.paramMap;
    this.id = Number(params.get("formatoEnsinoId"));
    this.refresh();
  }

  refresh() {
    this._formatoEnsinoService.get(this.id).subscribe((r: FormatoEnsinoDto) => { 
      this.formatoEnsino = r;
    });
  }
  
  goBack() {
    this._router.navigate(["../.."], {relativeTo:this._route})
  }

  editInfo() {
    let editFormatoEnsinoDialog: BsModalRef;
    let dto = new FormatoEnsinoDto(this.formatoEnsino);
    editFormatoEnsinoDialog = this._modalService.show(
      EditFormatoEnsinoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          formatoEnsino: dto
        }
      }
    );

    editFormatoEnsinoDialog.content.onSave.subscribe(() => {
      this.refresh();
    });
  }
  toRefBibDetails() {
    // this._router.navigate(["app", "ref-bibliograficas", "details", this.formatoEnsino.refBibliografica.id])
  }
}
