import {
    Component,
    Injector,
    OnInit,
    Input,
    EventEmitter,
    Output
} from '@angular/core';
import { ControlContainer, NgModelGroup} from '@angular/forms'
import { AppComponentBase } from '@shared/app-component-base';
import { ProtocoloAvaliacaoDto } from '@shared/service-proxies/service-proxies';

@Component({
    selector: 'info-protocolo-avaliacao-form',
    templateUrl: './info-protocolo-avaliacao-form.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class InfoProtocoloAvaliacaoFormComponent extends AppComponentBase {
    @Input() disabled: boolean = false;
    @Input() protocoloAvaliacao: ProtocoloAvaliacaoDto;
    public constructor(
        injector: Injector
    ) {
        super(injector);
    }
}