import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  AlunoServiceProxy,
  ProtocoloIntervencaoServiceProxy,
  ProtocoloIntervencaoDto,
  VersaoProtIntervencaoDto,
  ProtocoloIntervencaoDtoPagedResultDto,
  UpdateAlunoDto,
  AlunoDto,
  EntityDto,
  VersaoProtIntervencaoDtoPagedResultDto,
  CasoServiceProxy,
  CreateAplicacaoProtocoloIntervencaoDto,
  AplicacaoProtocoloIntervencaoDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './select-protocolo-intervencao-dialog.component.html'
})
export class SelectProtocoloIntervencaoDialogComponent extends AppComponentBase implements OnInit {
  loading: boolean = false;
  saving:  boolean = false;
  @Input() aluno: AlunoDto;
  constructor(injector: Injector,
    private _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    private _alunoService: AlunoServiceProxy,
    private _casoService: CasoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit() {
    this.loading = true;
    this.protocolosIntervencao = [];
    this._protocoloIntervencaoService.getAll(undefined, undefined).subscribe((p: ProtocoloIntervencaoDtoPagedResultDto) => {
      this.protocolosIntervencao = p.items;
      this.loading = false;
    });
  }

  protocolosIntervencao: ProtocoloIntervencaoDto[] = [];
  private _protocoloIntervencaoIdx: number;
  get protocoloIntervencaoIdx(): string  { return this._protocoloIntervencaoIdx?.toString()??""; }
  set protocoloIntervencaoIdx(v: string) { 
    let prevv = this._protocoloIntervencaoIdx;
    this._protocoloIntervencaoIdx = (v != "" ? Number(v) : undefined);
    if (prevv != this._protocoloIntervencaoIdx) {
      this._versaoIdx = undefined;
      this.updateVersoes();
    }
  }
  get protocoloIntervencaoSelected(): ProtocoloIntervencaoDto {
    if (this._protocoloIntervencaoIdx == undefined) return undefined; 
    return this.protocolosIntervencao[this._protocoloIntervencaoIdx] 
  }

  versoes: VersaoProtIntervencaoDto[];
  updateVersoes() {
    if (this._protocoloIntervencaoIdx == undefined) {
      this.versoes = undefined;
      return;
    }
    this.loading = true;
    this.versoes = [];
    this._protocoloIntervencaoService.getAllVersoes(this.protocoloIntervencaoSelected.id, 
      undefined, 
      undefined, 
      undefined)
    .subscribe((p: VersaoProtIntervencaoDtoPagedResultDto) => {
      this.versoes = p.items;
      this.loading = false;
    });
  }
  private _versaoIdx: number;
  get versaoIdx(): string  { return this._versaoIdx?.toString()??""; }
  set versaoIdx(v: string) { 
    this._versaoIdx = (v != "" ? Number(v) : undefined);
    this.updateVersoes();
  }
  get versaoSelected(): VersaoProtIntervencaoDto {
    if (this._versaoIdx == undefined) return undefined; 
    return this.versoes[this._versaoIdx] 
  }

  @Output() onSave: EventEmitter<AplicacaoProtocoloIntervencaoDto> = new EventEmitter<AplicacaoProtocoloIntervencaoDto>();
  save() {
    this.saving = true;

    let updateDto = new CreateAplicacaoProtocoloIntervencaoDto({'casoId': this.aluno.casoId, 'protocoloIntervencaoId': this.versaoSelected.id });
    this._casoService.createAplicacaoProtocoloIntervencao(updateDto).subscribe(
      (result: AplicacaoProtocoloIntervencaoDto) => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.bsModalRef.hide();
        this.onSave.emit(result);
        this.saving = false;
      }
    );
  }
}
