import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input,
  ChangeDetectorRef
} from '@angular/core';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { 
  AlunoDto,
  AplicacaoProtocoloIntervencaoDto,
  AlunoServiceProxy,
  ColaboradorServiceProxy,
  RegistroAtendimentoDto,
  Int64Int32KeyValuePair,
  TipoAjudaDto,
  PassoProtIntervencaoDto,
  TipoServicoPrestadoServiceProxy,
  AtuacaoDto,
  CasoServiceProxy,
  VersaoProtIntervencaoDto,
  HierarquiaAjudaDto,
  HierarquiaAjudaServiceProxy,
  UnidadeEnsinoDto,
  ProtocoloIntervencaoDto,
  ProtocoloIntervencaoServiceProxy,
  VersaoProtIntervencaoDtoPagedResultDto,
  CreateRegistroAtendimentoDto,
  ColabFuncaoOrgDto,
  ColaboradorDto,
  FuncaoOrgDto,
  FuncaoOrgServiceProxy
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable, Observer } from 'rxjs';
import { ActivatedRoute, Router } from '@node_modules/@angular/router';
import { LocalService } from '@shared/service-proxies/service-proxy.module';
import { UnidadeIntervencaoDialogComponent } from './unidade-intervencao-dialog/unidade-intervencao-dialog.component';
import { appModuleAnimation } from '@shared/animations/routerTransition';

@Component({
  templateUrl: './registro-atendimento-page.component.html',
  animations: [appModuleAnimation()]
})
export class RegistroAtendimentoPageComponent extends AppComponentBase implements OnInit {
  public readonly NOME_SERVICO_ATENDIMENTO = "Atendimento";
  loading: boolean = false;
  saving: boolean = false;
  get disabled(): boolean { return this.loading || this.saving }

  aluno: AlunoDto;
  atendimento: CreateRegistroAtendimentoDto;
  constructor(injector: Injector,
    private _casoService: CasoServiceProxy,
    private _alunoService: AlunoServiceProxy,
    private _colaboradorService: ColaboradorServiceProxy,
    private _tipoServicoService: TipoServicoPrestadoServiceProxy,
    private _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    private _hierarquiaAjudaService: HierarquiaAjudaServiceProxy,
    private _funcaoService: FuncaoOrgServiceProxy,
    private _changeDetector: ChangeDetectorRef,
    private _bsModalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute,
    private _localService: LocalService) {
    super(injector);
  }
  
  goBack() {
    this._router.navigate(['..'], {relativeTo:this._route});
  }

  getOptionString(a: AtuacaoDto): string {
    let cf = this.getColabFuncao(a ?.colabFuncaoId);
    let c  = this.getColaborador(cf?.colaboradorId);
    let f  = this.getFuncao(cf?.funcaoOrgId);
    return c?.nome + " (" + f?.nome + ")";
  }

  getFuncao(id): FuncaoOrgDto { return this.funcoes?.find(f => f.id == id) }
  getAtuacao(id): AtuacaoDto { return this.ats?.find(a => a.id == id) }
  getColabFuncao(id): ColabFuncaoOrgDto { return this.funcoesColaborador.get(id) }
  getColaborador(id): ColaboradorDto { return this.colaboradores.get(id) }
  
  private _funcoesApropriadas: number[];
  ats: AtuacaoDto[];
  funcoesColaborador: Map<number, ColabFuncaoOrgDto>;
  colaboradores: Map<number, ColaboradorDto>;
  funcoes: FuncaoOrgDto[];

  loadingProtocolosIntervencao: boolean;
  protocolosIntervencao: VersaoProtIntervencaoDto[];
  loadingAplicacoes: boolean;
  aplicacoes: AplicacaoProtocoloIntervencaoDto[];
  loadingHierarquias: boolean;
  hierarquiasAjuda: HierarquiaAjudaDto[];
  ngOnInit() {
    this.loading = true;
    this.aluno = this._localService.aluno;
    if (this.aluno == undefined) {
      const params = this._route.snapshot.paramMap;
      const id = Number(params.get("alunoId"));
      this._alunoService.get(id).subscribe((a) => {
        this.aluno = a;
        this.refresh();
      });
    } else {
      this.refresh();
    }
  }
  refresh() {
    this.funcoes = [];
    this._funcaoService.getAll(undefined, undefined, undefined)
    .subscribe((result) => { this.funcoes = result.items });
    
    this._tipoServicoService
    .getAll(this.NOME_SERVICO_ATENDIMENTO, undefined, undefined, undefined, undefined)
    .subscribe((result) => {
      this._funcoesApropriadas = [];
      result.items.forEach((t) => {
        t.funcoesOrg.forEach((f) => {
          if (!this._funcoesApropriadas.includes(f.id))
            this._funcoesApropriadas.push(f.id);
        });
      });

      this.ats = [];
      this._casoService.getAllAtuacoes(this.aluno.casoId, true, undefined, undefined, this._funcoesApropriadas)
      .subscribe((atuacoes) => {
        this.ats = atuacoes;
        if (!this.permission.isGranted("Business.Personal.Colaborador.Supervisor")) {
          this.ats = this.ats.filter((a) => this.getColabFuncao(a.colabFuncaoId).colaboradorId == this.appSession.user.colaborador.id);
        }
        this.loading = false;
        this._changeDetector.detectChanges();
      });

      this.funcoesColaborador = new Map<number, ColabFuncaoOrgDto>();
      this._colaboradorService
      .getAllColabFuncoes(undefined, undefined, undefined, this._funcoesApropriadas, true, this.aluno.casoId, undefined, undefined, undefined)
      .subscribe((cfs) => {
        cfs.items.forEach((cf) => { this.funcoesColaborador.set(cf.id, cf) });
        this._changeDetector.detectChanges();
      });

      this.colaboradores = new Map<number, ColaboradorDto>();
      this._colaboradorService.getAll(undefined, undefined, this._funcoesApropriadas, true, this.aluno.casoId, undefined, undefined, undefined)
      .subscribe((colaboradores) => {
        colaboradores.items.forEach((c) => { this.colaboradores.set(c.id, c) });
        this._changeDetector.detectChanges();
      });
    });
    this.loadingHierarquias = true;
    this.hierarquiasAjuda = [];
    this._hierarquiaAjudaService.getAll(undefined, undefined)
    .subscribe((result) => {
      this.hierarquiasAjuda = result.items;
      this.loadingHierarquias = false;
    });
    this.loadingProtocolosIntervencao = true;
    this.protocolosIntervencao = [];
    this._protocoloIntervencaoService
    .getAllVersoes(undefined, this.aluno.casoId, undefined, undefined)
    .subscribe((result: VersaoProtIntervencaoDtoPagedResultDto) => {
      this.protocolosIntervencao = result.items;
      this.loadingProtocolosIntervencao = false;
    });

    this.loadingAplicacoes = true;
    this.aplicacoes = [];
    this._casoService
    .getAllAplicacoesProtocoloIntervencao(this.aluno.casoId, undefined)
    .subscribe((result: AplicacaoProtocoloIntervencaoDto[]) => {
      this.aplicacoes = result;
      this.loadingAplicacoes = false;
    });
    this.atendimento = new CreateRegistroAtendimentoDto();
    this.atendimento.casoId = this.aluno.casoId;
    this.atendimento.inicio = moment();
    this.atendimento.fim    = moment();
    this.atendimento.unidadesEnsino = [];
  }


  protocolosByUnidade: VersaoProtIntervencaoDto[] = [];
  passosByUnidade: PassoProtIntervencaoDto[] = [];
  hierarquiasAjudaByUnidade: HierarquiaAjudaDto[] = [];
  addUnidade() {
    // Unidade Intervencao dialog, 
    // put lista atendimentos em aluno details, put lista unidades em apli details, tirar ordenacao de etapas do apli details
    // atendimento details dialog
    let unidadeIntervencaoDialog: BsModalRef;
    unidadeIntervencaoDialog = this._bsModalService.show(
      UnidadeIntervencaoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          aluno: this.aluno,
          protocolosIntervencao: this.protocolosIntervencao,
          aplicacoes: this.aplicacoes,
          hierarquiasAjuda: this.hierarquiasAjuda
        }
      }
    );

    unidadeIntervencaoDialog.content.onSave.subscribe((result: UnidadeEnsinoDto) => {
      this.atendimento.unidadesEnsino.push(result);
      var passo: PassoProtIntervencaoDto;
      var proto = this.protocolosIntervencao.find((p) => {
        passo = p.passosProtIntervencao.find(pa => pa.id == result.passoId);
        return passo != undefined;
      });
      this.protocolosByUnidade.push(proto);
      this.passosByUnidade.push(passo);
      this.hierarquiasAjudaByUnidade.push(this.hierarquiasAjuda.find(h => h.id == proto.hierarquiaAjudaId));
    });
  }
  
  rmvUnidade(i: number) {
    this.atendimento.unidadesEnsino.splice(i, 1);
    this.protocolosByUnidade.splice(i, 1);
    this.passosByUnidade.splice(i, 1);
    this.hierarquiasAjudaByUnidade.splice(i, 1);
  }
  
  getNoAjudas(unidade: UnidadeEnsinoDto, tipoAjuda: TipoAjudaDto): number {
    return unidade.ajudas.find(a => a.key == tipoAjuda.id).value;
  }

  get data(): string  { return this.atendimento?.inicio?.format("YYYY-MM-DD") }
  set data(v: string) { 
    let ini = moment(v), fim = moment(v); 
    ini = ini.hour  (this.atendimento?.inicio?.hour()  ??0); 
    fim = fim.hour  (this.atendimento?.fim?.   hour()  ??0); 
    ini = ini.minute(this.atendimento?.inicio?.minute()??0); 
    fim = fim.minute(this.atendimento?.fim?.   minute()??0);
    this.atendimento.inicio = ini; 
    this.atendimento.fim    = fim; 
  }
  get horarioInicio(): string  { return this.atendimento?.inicio?.format("HH:mm") }
  set horarioInicio(v: string) {
    let hora   = Number(v.substring(0, 2)); 
    let minuto = Number(v.substring(3, 5)); 
    this.atendimento.inicio = this.atendimento?.inicio?.hour  (hora); 
    this.atendimento.inicio = this.atendimento?.inicio?.minute(minuto); 
  }
  get horarioFim(): string  { return this.atendimento?.fim?.format("HH:mm") }
  set horarioFim(v: string) {
    let hora   = Number(v.substring(0, 2)); 
    let minuto = Number(v.substring(3, 5)); 
    this.atendimento.fim = this.atendimento?.fim?.hour  (hora); 
    this.atendimento.fim = this.atendimento?.fim?.minute(minuto); 
  }

  get atId(): string  { return this.atendimento?.atuacaoTerapeutaId?.toString()??"" }
  set atId(v: string) { this.atendimento.atuacaoTerapeutaId = (v != "" && v != undefined ? Number(v) : undefined) }

  save() {
    this.saving = true;
    let o: Observer<RegistroAtendimentoDto> = {
      next: (a) => {
        this.saving = false;
        this.notify.success(this.l("Salvo com sucesso!"));
        this._router.navigate(['..'], {relativeTo: this._route});
      },
      error: (e) => {
        console.log(e)
        this.saving = false;
      },
      complete: () => {}
    };
    this.atendimento.inicio = this.atendimento.inicio.utcOffset(0, true)
    this.atendimento.fim    = this.atendimento.fim   .utcOffset(0, true)
    this._casoService.registrarAtendimento(this.atendimento).subscribe(o);
  }
}
