import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output,
    Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { TecnicaDto } from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';

@Component({
    templateUrl: './tecnica-details-dialog.component.html'
})
export class TecnicaDetailsDialogComponent extends AppComponentBase implements OnInit {
    tecnica: TecnicaDto;

    constructor(injector: Injector,
        private _router: Router,
        private _route: ActivatedRoute,
        public bsModalRef: BsModalRef) {
        super(injector);
    }

    ngOnInit(): void {

    }
    moreInfo() {
        this._router.navigate(['app', 'tecnicas', 'details', this.tecnica.id]);
        this.bsModalRef.hide();
    }
}
  