import { AppComponentBase } from '@shared/app-component-base';
import { Component, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  TecnicaServiceProxy,
  TecnicaDto,
  UpdateTecnicaDto,
  RefBibliograficaDto,
  RefBibliograficaServiceProxy,
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { EditTecnicaDialogComponent } from '../edit-tecnica-dialog/edit-tecnica-dialog.component';

@Component({
  templateUrl: './tecnica-details.component.html',
  animations: [appModuleAnimation()]
})
export class TecnicaDetailsComponent extends AppComponentBase implements OnInit {
  public tecnica: TecnicaDto;
  public refBibliografica: RefBibliograficaDto;
  
  constructor(injector: Injector,
    protected _tecnicaService: TecnicaServiceProxy,
    protected _refBibService: RefBibliograficaServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  private id: number;
  ngOnInit() { 
    const params = this._route.snapshot.paramMap;
    this.id = Number(params.get("tecnicaId"));
    this.refresh();
  }

  refresh() {
    this._tecnicaService.get(this.id).subscribe((t: TecnicaDto) => { 
      this.tecnica = t;
      if (this.tecnica.refBibliograficaId != undefined) {
        this._refBibService.get(this.tecnica.refBibliograficaId).subscribe((r: RefBibliograficaDto) => {
          this.refBibliografica = r;
        });
      }
    });
  }
  
  goBack() {
    this._router.navigate(["../.."], {relativeTo:this._route})
  }

  editInfo() {
    let editTecnicaDialog: BsModalRef;
    let dto = new UpdateTecnicaDto(this.tecnica as any);
    dto.refBibliograficaId = this.tecnica.refBibliograficaId;
    editTecnicaDialog = this._modalService.show(
      EditTecnicaDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          tecnica: dto
        }
      }
    );

    editTecnicaDialog.content.onSave.subscribe(() => {
      this.refresh();
    });
  }
  toRefBibDetails() {
    if (this.tecnica.refBibliograficaId != undefined)
      this._router.navigate(["app", "ref-bibliograficas", "details", this.tecnica.refBibliograficaId])
  }
}
