import { AppComponentBase } from '@shared/app-component-base';
import { Component, Injector, Input, Output, OnInit, EventEmitter } from "@angular/core";
import { PessoaDto, AlunoDto, ColaboradorDto,  ResponsavelDto, CidadeDto, UFDto, PaisDto } from '@shared/service-proxies/service-proxies';
import { Observable } from 'rxjs';

@Component({
    selector:'info-pessoal-details',
    templateUrl: './info-pessoal-details.component.html'
})
export class InfoPessoalDetailsComponent extends AppComponentBase implements OnInit {
    @Input() pessoa: PessoaDto;
    @Input() cidade: CidadeDto;
    @Input() uf: UFDto;
    @Input() pais: PaisDto;
    @Output() onClickEdit = new EventEmitter<any>();
    constructor(injector: Injector) {
        super(injector);
    }

    ngOnInit() {
        
    }
    @Input() pessoal: boolean = false;
    podeEditar() {
      return this.pessoal || this.permission.isGranted("Business.Admin.People")
    }

    getEnderecoString(): string[] {
        let s = "";
        if (this.pessoa?.endereco_Linha1 != undefined)
            s += this.pessoa?.endereco_Linha1 + "\n";
        if (this.pessoa?.endereco_Linha2 != undefined) 
            s += this.pessoa?.endereco_Linha2 + "\n";
        
        if (this.pessoa?.endereco_Bairro != undefined) {
            s += this.pessoa?.endereco_Bairro; 
            if (this.cidade?.nome != undefined)
                s += ", ";
        }
        if (this.cidade?.nome != undefined) {
            s += this.cidade?.nome;
            if (this.uf?.abreviacao != undefined)
                s += " - ";
        } 
        if (this.uf?.abreviacao != undefined) {
            s += this.uf?.abreviacao;
        }
        if (this.pessoa?.endereco_Bairro != undefined || 
            this.cidade?.nome != undefined || 
            this.uf?.abreviacao != undefined)
            s += "\n" 
        if (this.pais?.nome != undefined) {
            s += this.pais?.nome; 
        }

        if (s == "") return ["-"];
        return s.split('\n');
    }
}