import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './single-selector-dialog.component.html'
})
export class SingleSelectorDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean = false;
  title: string = "";
  options: string[] = [];
  constructor(injector: Injector,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit() { }

  private _idxSelected: number;
  public get idxSelected(): string  { return this._idxSelected?.toString()??"" }
  public set idxSelected(v: string) { this._idxSelected = (v == "" || v == undefined)?(undefined):(Number(v)) }

  public onSubmit: EventEmitter<number> = new EventEmitter<number>();
  save() {
    this.saving = true;
    this.onSubmit.emit(this._idxSelected);
    this.bsModalRef.hide();
    this.saving = false;
  }
}
