import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { TecnicaDto } from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './select-tecnica-dialog.component.html'
})
export class SelectTecnicaDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean = false;
  @Input()
  tecnicas: TecnicaDto[] = [];
  constructor(injector: Injector,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit() { }

  tecnicaIdx: number;
  get tecnicaSelected(): TecnicaDto {
    if (this.tecnicaIdx == undefined) return undefined; 
    return this.tecnicas[Number(this.tecnicaIdx)] 
  }
  @Output() onSubmit: EventEmitter<TecnicaDto> = new EventEmitter<TecnicaDto>();
  save() {
    this.saving = true;
    this.onSubmit.emit(this.tecnicaSelected);
    this.bsModalRef.hide();
    this.saving = false;
  }
}
