import { Component, EventEmitter, Injector, OnInit } from "@angular/core";
import { appModuleAnimation } from "@shared/animations/routerTransition";
import { AppComponentBase } from "@shared/app-component-base";
import { 
    ColaboradorServiceProxy, 
    TipoServicoPrestadoServiceProxy, 
    AtuacaoDto,
    ColaboradorDto,
    ColabFuncaoOrgDto,
    TipoServicoPrestadoDto,
    FuncaoOrgServiceProxy,
    FuncaoOrgDto,
    EntityDto
} from "@shared/service-proxies/service-proxies";
import * as moment from "moment";
import { BsModalRef } from "ngx-bootstrap/modal";

@Component({
    templateUrl: './edit-tipo-servico-prestado-dialog.component.html',
    animations: [appModuleAnimation()]
})
export class EditTipoServicoPrestadoDialogComponent extends AppComponentBase implements OnInit {
    constructor(injector: Injector,
        private _tipoServicoPrestadoService: TipoServicoPrestadoServiceProxy,
        private _funcaoOrgService: FuncaoOrgServiceProxy,
        public bsModalRef: BsModalRef
    ) {
        super(injector);
    }

    tipoServico: TipoServicoPrestadoDto;    
    ngOnInit(): void { }
    
    onSave: EventEmitter<TipoServicoPrestadoDto> = new EventEmitter<TipoServicoPrestadoDto>();
    saving: boolean = false;
    save() {
        this.saving = true;
        this.tipoServico.registravelAvulso = true;
        this._tipoServicoPrestadoService.update(this.tipoServico).subscribe((result) => {
            this.saving = false;
            this.notify.info(this.l('SavedSuccessfully'));
            this.bsModalRef.hide();
            this.onSave.emit(result);
        });
    }
}