import { Component, Injector } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  ResponsavelServiceProxy,
  ResponsavelDto,
  ResponsavelDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
import { CreateResponsavelDialogComponent } from './create-responsavel-dialog/create-responsavel-dialog.component';
// import { EditResponsavelDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';


class PagedResponsaveisRequestDto extends PagedRequestDto {
  keyword: string;
}

@Component({
  templateUrl: './responsaveis.component.html',
  animations: [appModuleAnimation()]
})
export class ResponsaveisComponent extends PagedListingComponentBase<ResponsavelDto> {
  responsaveis: ResponsavelDto[] = [];
  keyword = '';
  isActive: boolean | null;
  advancedFiltersVisible = false;

  constructor(
    injector: Injector,
    private _responsaveisService: ResponsavelServiceProxy,
    private _modalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }
  
  createResponsavel(): void {
    let createResponsavelDialog: BsModalRef;
    createResponsavelDialog = this._modalService.show(
      CreateResponsavelDialogComponent,
      {
        class: 'modal-lg',
      }
    );

    createResponsavelDialog.content.onSave.subscribe(() => {
      this.refresh();
    });
  }

  detailsResponsavel(responsaveis: ResponsavelDto): void {
    this._router.navigate(['details', responsaveis.id], { relativeTo : this._route });
  }

  clearFilters(): void {
    this.keyword = '';
    this.isActive = undefined;
    this.getDataPage(1);
  }

  protected list(
    request: PagedResponsaveisRequestDto,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    request.keyword = this.keyword;

    this._responsaveisService
      .getAll(
        request.keyword,
        request.skipCount,
        request.maxResultCount
      )
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: ResponsavelDtoPagedResultDto) => {
        this.responsaveis = result.items;
        this.showPaging(result, pageNumber);
      });
  }

  protected delete(pesssoa: ResponsavelDto): void {
    abp.message.confirm(
      "O seguinte endereo ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._responsaveisService.delete(pesssoa.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
