import { ChangeDetectorRef, Component, Injector, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { appModuleAnimation } from "@shared/animations/routerTransition";
import { PagedListingComponentBase, PagedRequestDto } from "@shared/paged-listing-component-base";
import { ServicoPrestadoDto, TipoServicoPrestadoDtoPagedResultDto, ServicoPrestadoServiceProxy, TipoServicoPrestadoDto, TipoServicoPrestadoServiceProxy } from "@shared/service-proxies/service-proxies";
import { BsModalRef, BsModalService } from "ngx-bootstrap/modal";
import { finalize } from "rxjs";
import { CreateTipoServicoPrestadoDialogComponent } from "./create-tipo-servico-prestado-dialog/create-tipo-servico-prestado-dialog.component";
import { EditTipoServicoPrestadoDialogComponent } from "./edit-tipo-servico-prestado-dialog/edit-tipo-servico-prestado-dialog.component";
// import { CreateServicoPrestadoDialogComponent } from "./create-servico-prestado-dialog/create-servico-prestado-dialog.component";

@Component({
    templateUrl: './tipos-servico-prestado.component.html',
    animations: [appModuleAnimation()]
})
export class TiposServicoPrestadoComponent extends PagedListingComponentBase<TipoServicoPrestadoDto> implements OnInit {
    tiposServico: TipoServicoPrestadoDto[] = [];
    keyword = '';
    isActive: boolean | null;
    advancedFiltersVisible = false;
    constructor(
        injector: Injector,
        private _servicoPrestadoService: ServicoPrestadoServiceProxy,
        private _tipoServicoPrestadoService: TipoServicoPrestadoServiceProxy,
        private _modalService: BsModalService,
        private _changeDectetor: ChangeDetectorRef,
        private _router: Router,
        private _route: ActivatedRoute) {
        super(injector)
    }
    ngOnInit(): void {
        this.refresh();
    }
    getTipoServicoNome(id: number): string {
        return this.tiposServico?.find(t => t.id == id)?.nome??"-";
    }
    createTipoServicoPrestado() {
        let createTipoServicoPrestadoDialog: BsModalRef;
        createTipoServicoPrestadoDialog = this._modalService.show(
          CreateTipoServicoPrestadoDialogComponent,
          {
            class: 'modal-lg',
            initialState: { }
          }
        );
    
        createTipoServicoPrestadoDialog.content.onSave.subscribe(() => {
          this.refresh();
        });
    }
    editTipoServicoPrestado(t: TipoServicoPrestadoDto) {
        let editTipoServicoPrestadoDialog: BsModalRef;
        editTipoServicoPrestadoDialog = this._modalService.show(
          EditTipoServicoPrestadoDialogComponent,
          {
            class: 'modal-lg',
            initialState: { tipoServico: t }
          }
        );
    
        editTipoServicoPrestadoDialog.content.onSave.subscribe(() => {
          this.refresh();
        });
    }
    protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
        this._tipoServicoPrestadoService
        .getAll(
            undefined,
            undefined,
            undefined,
            request.skipCount,
            request.maxResultCount
        )
        .pipe(
            finalize(() => {
                finishedCallback();
            })
        )
        .subscribe((result: TipoServicoPrestadoDtoPagedResultDto) => {
            this.tiposServico = result.items;
            this.showPaging(result, pageNumber);
        });
    }
    clearFilters(): void {
        this.keyword = '';
        this.getDataPage(1);
    }
    protected delete(entity: TipoServicoPrestadoDto): void {
        abp.message.confirm(
          "O seguinte tecnica ser excludo do banco",
          undefined,
          (result: boolean) => {
            if (result) {
              this._servicoPrestadoService.delete(entity.id).subscribe(() => {
                abp.notify.success(this.l('SuccessfullyDeleted'));
                this.refresh();
              });
            }
          }
        );
    }

}