import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, Input, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ProtocoloIntervencaoServiceProxy,
  ProtocoloIntervencaoDto,
  UpdateProtocoloIntervencaoDto,
  VersaoProtIntervencaoDto,
  VersaoProtIntervencaoDtoPagedResultDto,
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
// import { EditProtocoloIntervencaoDialogComponent } from '../edit-protocolo-intervencao-dialog/edit-protocolo-intervencao-dialog.component';

@Component({
  templateUrl: './protocolo-intervencao-details.component.html',
  animations: [appModuleAnimation()]
})
export class ProtocoloIntervencaoDetailsComponent extends AppComponentBase implements OnInit {
  public protocoloIntervencao: ProtocoloIntervencaoDto;
  public versaoPadrao: VersaoProtIntervencaoDto;
  
  constructor(injector: Injector,
    protected _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  public id: number;
  ngOnInit() { 
    const params = this._route.snapshot.paramMap;
    this.id = Number(params.get("protocoloIntervencaoId"));
    this.refresh();
  }

  refresh() {
    this._protocoloIntervencaoService.get(this.id).subscribe((r: ProtocoloIntervencaoDto) => { 
      this.protocoloIntervencao = r;
      this._protocoloIntervencaoService.getVersao(r.versaoPadraoId).subscribe((v: VersaoProtIntervencaoDto) => {
        this.versaoPadrao = v;
      });
    });
  }
  
  goBack() {
    this._router.navigate(["app", "protocolos-intervencao"])
  }

  delete() {
    abp.message.confirm(
      "O seguinte protocolo de interveno ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._protocoloIntervencaoService.delete(this.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.goBack();
          });
        }
      }
    );
  }
  editNome() {
    if (this.protocoloIntervencao == undefined) return;
    let up = new UpdateProtocoloIntervencaoDto({
      'id': this.protocoloIntervencao.id,
      'nome': this.protocoloIntervencao.nome,
      'versaoPadraoId': this.protocoloIntervencao.versaoPadraoId
    });
    let editNomeProtIntervencao = this._modalService.show(
      EditNomeProtIntervencaoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          protIntervencao: up
        },
      }
    );

    editNomeProtIntervencao.content.onSave.subscribe(() => {
      this.refresh();
    });
  }
  editVersaoPadrao() {
    if (this.protocoloIntervencao == undefined) return;
    let up = new UpdateProtocoloIntervencaoDto({
      'id': this.protocoloIntervencao.id,
      'nome': this.protocoloIntervencao.nome,
      'versaoPadraoId': this.protocoloIntervencao.versaoPadraoId
    });
    let selectVersaoPadraoDialog = this._modalService.show(
      SelectVersaoPadraoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          protIntervencao: up
        },
      }
    );

    selectVersaoPadraoDialog.content.onSave.subscribe(() => {
      this.refresh();
    });
  }
}

@Component({
  templateUrl: './edit-nome-prot-intervencao-dialog.component.html',
  animations: [appModuleAnimation()]
})
export class EditNomeProtIntervencaoDialogComponent extends AppComponentBase implements OnInit {
  public protIntervencao: UpdateProtocoloIntervencaoDto;
  public saving: boolean = false;
  constructor(injector: Injector,
    protected _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    public bsModalRef: BsModalRef) {
      super(injector);
  }
  ngOnInit(): void {

  }

  public onSave: EventEmitter<ProtocoloIntervencaoDto> = new EventEmitter<ProtocoloIntervencaoDto>();
  save() {
    this.saving = true;
    this._protocoloIntervencaoService.update(this.protIntervencao).subscribe((p) => {
      this.onSave.emit(p);
      this.bsModalRef.hide();
      this.saving = false;
    });
  }
}

@Component({
  templateUrl: './select-versao-padrao-dialog.component.html'
})
export class SelectVersaoPadraoDialogComponent extends AppComponentBase implements OnInit {
  public protIntervencao: UpdateProtocoloIntervencaoDto;
  loading: boolean = false;
  saving: boolean = false;
  @Input() versoes: VersaoProtIntervencaoDto[] = [];
  constructor(injector: Injector,
    protected _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit() {
    this.loading = true; 
    this._protocoloIntervencaoService
      .getAllVersoes(
        this.protIntervencao.id,
        undefined,
        undefined,
        undefined)
      .subscribe((result: VersaoProtIntervencaoDtoPagedResultDto) => {
        this.versoes = result.items;
        this.loading = false; 
      });
  }

  get versaoId(): string  { return this.protIntervencao.versaoPadraoId.toString(); }
  set versaoId(v: string) { this.protIntervencao.versaoPadraoId = Number(v); }
  public onSave: EventEmitter<ProtocoloIntervencaoDto> = new EventEmitter<ProtocoloIntervencaoDto>();
  save() {
    this.saving = true;
    this._protocoloIntervencaoService.update(this.protIntervencao).subscribe((p) => {
      this.onSave.emit(p);
      this.bsModalRef.hide();
      this.saving = false;
    });
  }
}
