import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  ProtocoloAvaliacaoServiceProxy,
  CreateProtocoloAvaliacaoDto,
  CreateDominioDto,
  TipoConfirmacaoHabilidadeDto,
  CreateNivelDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './create-protocolo-avaliacao-dialog.component.html'
})
export class CreateProtocoloAvaliacaoDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean;
  
  tiposConfirmacao: TipoConfirmacaoHabilidadeDto[] = [];
  get tipoConfDefault(): number { return this.tiposConfirmacao?.find(t => t.nome.toLowerCase().startsWith("obs"))?.id; }
  constructor(injector: Injector,
    public _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
    this._protocoloAvaliacaoService.getAllTiposConfirmacaoHabilidade().subscribe((result) => { 
        this.tiposConfirmacao = result.sort((a, b) => {
          let pred_a = a.nome.toLowerCase().startsWith("obs");
          let pred_b = b.nome.toLowerCase().startsWith("obs");
          if (pred_a == pred_b)
            return  0;
          else if (pred_a)
            return -1;
          else
            return  1;
        });
    });
  }

  ngOnInit(): void {

  }

  
  @Output() onSave = new EventEmitter<any>();
  protocoloAvaliacao : CreateProtocoloAvaliacaoDto = new CreateProtocoloAvaliacaoDto();
  protected create() {
    this.saving = true;
    console.log(this.protocoloAvaliacao);
    this._protocoloAvaliacaoService.create(this.protocoloAvaliacao).subscribe(
      () => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.bsModalRef.hide();
        this.saving = false;
        this.onSave.emit();
      }
    );
  }
  createNivel(){
    this.protocoloAvaliacao.niveis.push(new CreateNivelDto());
  }
  removeNivel(i: number) {
    this.protocoloAvaliacao.niveis.splice(i, 1);
  }
}
