import { Component, EventEmitter, Injector, Input, OnInit } from "@angular/core";
import { ControlContainer, NgModelGroup } from "@node_modules/@angular/forms";
import { appModuleAnimation } from "@shared/animations/routerTransition";
import { AppComponentBase } from "@shared/app-component-base";
import { 
    ColaboradorServiceProxy, 
    TipoServicoPrestadoServiceProxy, 
    TipoServicoPrestadoDto,
    FuncaoOrgServiceProxy,
    FuncaoOrgDto,
    EntityDto
} from "@shared/service-proxies/service-proxies";
import * as moment from "moment";
import { BsModalRef } from "ngx-bootstrap/modal";

@Component({
    selector: 'tipo-servico-prestado-form',
    templateUrl: './tipo-servico-prestado-form.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class TipoServicoPrestadoFormComponent extends AppComponentBase implements OnInit {
    @Input()
    disabled: boolean = false;

    constructor(injector: Injector,
        private _tipoServicoPrestadoService: TipoServicoPrestadoServiceProxy,
        private _funcaoOrgService: FuncaoOrgServiceProxy,
        public bsModalRef: BsModalRef
    ) {
        super(injector);
    }

    funcoes: FuncaoOrgDto[];
    funcoesSelected: FuncaoOrgDto[] = [];
    funcaoSelectIdx: string = "";

    @Input()
    tipoServico: TipoServicoPrestadoDto;
    
    loadingFuncoes: boolean = false;
    get loading(): boolean { return this.loadingFuncoes }
    
    ngOnInit(): void {
        if (this.tipoServico.funcoesOrg == undefined)
            this.tipoServico.funcoesOrg = [];
        this.loadingFuncoes = true;
        this._funcaoOrgService.getAll(undefined, undefined, undefined).subscribe((result) => {
            this.funcoes = result.items;
            this.funcoesSelected = [];
            let notSelected = [];
            this.tipoServico.funcoesOrg.forEach((f) => { this.funcoesSelected.push(undefined) });
            this.funcoes.forEach((func) => {
                let i = this.tipoServico.funcoesOrg.findIndex(f => f.id == func.id);
                if (i != -1) 
                    this.funcoesSelected[i] = func; 
                else
                    notSelected.push(func); 
            });
            this.funcoes = notSelected;
            this.loadingFuncoes = false;
        });
    }
    select() {
        if (this.funcaoSelectIdx != "") {
            let i = this.funcaoSelectIdx;
            this.funcaoSelectIdx = '';
            let func = this.funcoes.splice(Number(i), 1)[0];
            this.funcoesSelected.push(func);
            this.tipoServico.funcoesOrg.push(new EntityDto({ id: func.id }));
        }
    }
    unselect(i: number) {
        this.funcoes.push(this.funcoesSelected.splice(i, 1)[0]);
        this.tipoServico.funcoesOrg.splice(i, 1);
    }
}