import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  AlunoServiceProxy,
  AlunoDto
} from '@shared/service-proxies/service-proxies';

@Component({
  templateUrl: './select-aluno-dialog.component.html'
})
export class SelectAlunoDialogComponent extends AppComponentBase implements OnInit {
  constructor(injector: Injector,
    public _alunoService: AlunoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  loading = false;
  alunos: AlunoDto[];
  ngOnInit(): void {
    this.loading = true;
    this._alunoService.getAll(undefined, undefined, undefined)
      .subscribe((result) => { this.alunos = result.items; this.loading = false; });
  }
  private _selected: number;
  get selected(): string  { return this._selected?.toString()??"" }
  set selected(v: string) { if (v == undefined || v == "") { this._selected = undefined } else { this._selected = Number(v) } }
  @Output() onSubmit: EventEmitter<AlunoDto> = new EventEmitter<AlunoDto>();
  submit() {
    this.onSubmit.emit(this.alunos[this._selected]);
    this.bsModalRef.hide();
  }
}
