import {
  Component,
  Injector,
  OnInit,
  Input,
  EventEmitter,
  Output,
  ChangeDetectorRef
} from '@angular/core';
import { ControlContainer, NgModelGroup} from '@angular/forms'
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  TecnicaServiceProxy,
  CreateTecnicaDto,
  UpdateTecnicaDto,
  EntityDto,
  RefBibliograficaDto,
  RefBibliograficaServiceProxy
} from '@shared/service-proxies/service-proxies';
import { CustomFormBaseComponent } from '@app/bases/custom-form-base.component';

@Component({
    selector: 'tecnica-form',
    templateUrl: './tecnica-form.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class TecnicaFormComponent extends CustomFormBaseComponent implements OnInit {
  @Input() hasPresetRefBib : boolean = false;
  @Input() tecnica : CreateTecnicaDto | UpdateTecnicaDto;

  get refBibId(): number {
    return this.tecnica?.refBibliograficaId??-1;
  }
  set refBibId(v: number) {
    if (v == -1) v = undefined;
    this.tecnica.refBibliograficaId = v
  }

  constructor(injector: Injector, 
    private refBibService: RefBibliograficaServiceProxy,
    private changeDetectorRef: ChangeDetectorRef) {
    super(injector);
  }

  refBibliograficas: RefBibliograficaDto[];
  ngOnInit(): void {
    if (!this.hasPresetRefBib) {
      this.refBibService.getAll(undefined, undefined, undefined).subscribe((result) => {
        this.refBibliograficas = result.items;
        this.changeDetectorRef.detectChanges();
      });
    }
  }
}
