import { NgModule } from '@angular/core';
import { RouterModule } from '@angular/router';
import { AppComponent } from './app.component';
import { AppRouteGuard } from '@shared/auth/auth-route-guard';
import { HomeComponent } from './home/home.component';
import { AboutComponent } from './about/about.component';
import { UsersComponent } from './users/users.component';
import { TenantsComponent } from './tenants/tenants.component';
import { RolesComponent } from 'app/roles/roles.component';
import { AlunosComponent } from 'app/alunos/alunos.component';
import { AlunoDetailsComponent } from 'app/alunos/aluno-details/aluno-details.component';
import { ResponsaveisComponent } from 'app/responsaveis/responsaveis.component';
import { ResponsavelDetailsComponent } from 'app/responsaveis/responsavel-details/responsavel-details.component';
// import { EnderecosComponent } from 'app/enderecos/enderecos.component';
// import { StaffComponent } from 'app/staff/staff.component';
import { ChangePasswordComponent } from './users/change-password/change-password.component';
import { ColaboradorDetailsComponent } from './colaboradores/colaborador-details/colaborador-details.component';
import { ColaboradoresComponent } from './colaboradores/colaboradores.component';
import { ProtocoloAvaliacaoDetailsComponent } from './protocolos-avaliacao/protocolo-avaliacao-details/protocolo-avaliacao-details.component';
import { ProtocolosAvaliacaoComponent } from './protocolos-avaliacao/protocolos-avaliacao.component';
import { AplicacaoProtocoloAvaliacaoDetailsComponent } from './alunos/aluno-details/protocolos-avaliacao-caso/aplicacao-protocolo-avaliacao-details/aplicacao-protocolo-avaliacao-details.component';
import { RefBibliograficasComponent } from './ref-bibliograficas/ref-bibliograficas.component';
import { TecnicasComponent } from './tecnicas/tecnicas.component';
import { RefBibliograficaDetailsComponent } from './ref-bibliograficas/ref-bibliografica-details/ref-bibliografica-details.component';
import { TecnicaDetailsComponent } from './tecnicas/tecnica-details/tecnica-details.component';
import { FormatosEnsinoComponent } from './formatos-ensino/formatos-ensino.component';
import { FormatoEnsinoDetailsComponent } from './formatos-ensino/formato-ensino-details/formato-ensino-details.component';
import { HierarquiasAjudaComponent } from './hierarquias-ajuda/hierarquias-ajuda.component';
import { HierarquiaAjudaCreateComponent } from './hierarquias-ajuda/hierarquia-ajuda-create/hierarquia-ajuda-create.component';
import { HierarquiaAjudaDetailsComponent } from './hierarquias-ajuda/hierarquia-ajuda-details/hierarquia-ajuda-details.component';
import { ProtocolosIntervencaoComponent } from './protocolos-intervencao/protocolos-intervencao.component';
import { ProtocoloIntervencaoCreateComponent } from './protocolos-intervencao/protocolo-intervencao-create/protocolo-intervencao-create.component';
import { ProtocoloIntervencaoDetailsComponent } from './protocolos-intervencao/protocolo-intervencao-details/protocolo-intervencao-details.component';
import { VersaoDetailsPageComponent } from './protocolos-intervencao/versoes/versao-details-page/versao-details-page.component';
import { VersaoCreateComponent } from './protocolos-intervencao/versoes/versao-create/versao-create.component';
import { VersaoEditComponent } from './protocolos-intervencao/versoes/versao-edit/versao-edit.component';
import { AplicacaoProtocoloIntervencaoDetailsComponent } from './alunos/aluno-details/protocolos-intervencao-caso/aplicacao-protocolo-intervencao-details/aplicacao-protocolo-intervencao-details.component';
import { ServicosPrestadosComponent } from './servicos-prestados/servicos-prestados.component';
import { TiposServicoPrestadoComponent } from './servicos-prestados/tipos-servico-prestado/tipos-servico-prestado.component';
import { FuncoesComponent } from './funcoes/funcoes.component';
import { PerfilPessoalComponent } from './perfil-pessoal/perfil-pessoal.component';
import { RegistroAtendimentoPageComponent } from './alunos/aluno-details/atendimentos-caso/registro-atendimento-page/registro-atendimento-page.component';

@NgModule({
    imports: [
        RouterModule.forChild([
            {
                path: '',
                component: AppComponent,
                children: [
                    { path: 'home', component: HomeComponent,  canActivate: [AppRouteGuard] },
                    { path: 'personal', component: PerfilPessoalComponent,  canActivate: [AppRouteGuard] },
                    { path: 'users', component: UsersComponent, data: { permission: 'Pages.Users' }, canActivate: [AppRouteGuard] },
                    { path: 'roles', component: RolesComponent, data: { permission: 'Pages.Roles' }, canActivate: [AppRouteGuard] },
                    { path: 'alunos', data: { permission: 'Business.Admin.People' }, component: AlunosComponent, canActivate: [AppRouteGuard] },
                    { path: 'alunos/details/:alunoId', component: AlunoDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'alunos/details/:alunoId/registro-atendimento', data: { permission: 'Business.Personal.Colaborador' }, component: RegistroAtendimentoPageComponent, canActivate: [AppRouteGuard] },
                    { path: 'alunos/details/:alunoId/protocolo-avaliacao/:apliId', component: AplicacaoProtocoloAvaliacaoDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'alunos/details/:alunoId/protocolo-intervencao/:apliId', component: AplicacaoProtocoloIntervencaoDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'responsaveis', data: { permission: 'Business.Admin.People' }, component: ResponsaveisComponent, canActivate: [AppRouteGuard] },
                    { path: 'responsaveis/details/:responsavelId', component: ResponsavelDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'colaboradores', data: { permission: 'Business.Admin.People' }, component: ColaboradoresComponent, canActivate: [AppRouteGuard] },
                    { path: 'colaboradores/details/:colaboradorId', component: ColaboradorDetailsComponent, canActivate: [AppRouteGuard] },
                    // { path: 'enderecos', component: EnderecosComponent, canActivate: [AppRouteGuard] },
                    { path: 'protocolos-avaliacao', data: { permission: 'Business.Technical' }, component: ProtocolosAvaliacaoComponent, canActivate: [AppRouteGuard] },
                    { path: 'protocolos-avaliacao/details/:protocoloAvaliacaoId', component: ProtocoloAvaliacaoDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'ref-bibliograficas', data: { permission: 'Business.Technical' }, component: RefBibliograficasComponent, canActivate: [AppRouteGuard] },
                    { path: 'ref-bibliograficas/details/:refBibId', component: RefBibliograficaDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'tecnicas', data: { permission: 'Business.Technical' }, component: TecnicasComponent, canActivate: [AppRouteGuard] },
                    { path: 'tecnicas/details/:tecnicaId', component: TecnicaDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'formatos-ensino', data: { permission: 'Business.Technical' }, component: FormatosEnsinoComponent, canActivate: [AppRouteGuard] },
                    { path: 'formatos-ensino/details/:formatoEnsinoId', component: FormatoEnsinoDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'hierarquias-ajuda', data: { permission: 'Business.Technical' }, component: HierarquiasAjudaComponent, canActivate: [AppRouteGuard] },
                    { path: 'hierarquias-ajuda/create', data: { permission: 'Business.Technical' }, component: HierarquiaAjudaCreateComponent, canActivate: [AppRouteGuard] },
                    { path: 'hierarquias-ajuda/details/:hierarquiaAjudaId', component: HierarquiaAjudaDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'protocolos-intervencao', data: { permission: 'Business.Technical' }, component: ProtocolosIntervencaoComponent, canActivate: [AppRouteGuard] },
                    { path: 'protocolos-intervencao/create', data: { permission: 'Business.Technical' }, component: ProtocoloIntervencaoCreateComponent, canActivate: [AppRouteGuard] },
                    { path: 'protocolos-intervencao/details/:protocoloIntervencaoId', component: ProtocoloIntervencaoDetailsComponent, canActivate: [AppRouteGuard] },
                    { path: 'protocolos-intervencao/versoes/details/:versaoProtIntervencaoId', component: VersaoDetailsPageComponent, canActivate: [AppRouteGuard] },
                    { path: 'protocolos-intervencao/versoes/create/:protocoloIntervencaoId', data: { permission: 'Business.Technical' }, component: VersaoCreateComponent, canActivate: [AppRouteGuard] },
                    { path: 'protocolos-intervencao/versoes/edit/:versaoProtIntervencaoId', data: { permission: 'Business.Technical' }, component: VersaoEditComponent, canActivate: [AppRouteGuard] },
                    { path: 'servicos-prestados', data: { permission: 'Business.Finance' }, component: ServicosPrestadosComponent, canActivate: [AppRouteGuard] },
                    { path: 'servicos-prestados/tipos', data: { permission: 'Business.Finance' }, component: TiposServicoPrestadoComponent, canActivate: [AppRouteGuard] },
                    { path: 'funcoes', component: FuncoesComponent, data: { permission: 'Business.Admin.OrgRoles' }, canActivate: [AppRouteGuard] },
                    { path: 'tenants', component: TenantsComponent, data: { permission: 'Pages.Tenants' }, canActivate: [AppRouteGuard] },
                    { path: 'about', component: AboutComponent, canActivate: [AppRouteGuard] },
                    { path: 'update-password', component: ChangePasswordComponent, canActivate: [AppRouteGuard] }
                ]
            }
        ])
    ],
    exports: [RouterModule]
})
export class AppRoutingModule { }
