import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  ColaboradorServiceProxy,
  CreateColaboradorDto,
  CreateAlunoDto,
  CreateResponsavelDto,
  AlunoServiceProxy,
  ResponsavelServiceProxy
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import { NgForm } from '@angular/forms';

@Component({
  templateUrl: './create-pessoa-dialog-base.component.html'
})
export class CreatePessoaDialogBaseComponent extends AppComponentBase implements OnInit {
  saving = false;
  pessoa : CreateColaboradorDto | CreateAlunoDto | CreateResponsavelDto;

  @Output() onSave = new EventEmitter<any>();

  constructor(injector: Injector,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  protected initEmptyPessoaDto(): void { }
  
  ngOnInit(): void {
    this.initEmptyPessoaDto();
  }

  protected _format() {
    
  }
  public saveOrReturn: boolean = false;
  save(): void {
    this.saving = true;
    // this.pessoa.dataNascimento = moment(this.pessoa.dataNascimento);
    // this.pessoa.infoContato_Telefone1 = this.pessoa.infoContato_Telefone1.replace(new RegExp("[ ]"), "")
    this._format()
    if (this.saveOrReturn) {
      this.onSave.emit(this.pessoa);
      this.saving = false;
    } else {
      this.create().subscribe(() => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.onSave.emit();
        this.saving = false;
        this.bsModalRef.hide();
      })
    }
  }

  public printFormStatus(form: NgForm, controlNames: string[] = ['infoPessoal', 'infoContato', 'enderecoNovo']) {
    console.log(form.status);
    console.log(form.errors);
    for (let i = 0; i < controlNames.length; i++) {
      console.log(controlNames[i], form.form.controls[controlNames[i]].errors);
    }
    console.log(form);
  }

  protected create(): Observable<any> { throw "Not Implemented"; }
}
