import { ChangeDetectorRef, Component, EventEmitter, Injector, Input, OnInit, Output } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { appModuleAnimation } from "@shared/animations/routerTransition";
import { PagedListingComponentBase, PagedRequestDto } from "@shared/paged-listing-component-base";
import { CasoDto, CasoServiceProxy, ColabFuncaoOrgDto, ColaboradorDto, FuncaoOrgDto, ServicoPrestadoDto, ServicoPrestadoDtoPagedResultDto, ServicoPrestadoServiceProxy, TipoServicoPrestadoDto, TipoServicoPrestadoServiceProxy } from "@shared/service-proxies/service-proxies";
import { BsModalRef, BsModalService } from "ngx-bootstrap/modal";
import { finalize } from "rxjs";
import { ControlContainer, NgModelGroup } from "@node_modules/@angular/forms";
import { CreateServicoPrestadoDialogComponent } from "@app/servicos-prestados/create-servico-prestado-dialog/create-servico-prestado-dialog.component";

@Component({
    selector: 'servicos-prestados-colaborador',
    templateUrl: './servicos-prestados-colaborador.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class ServicosPrestadosColaboradorComponent extends PagedListingComponentBase<ServicoPrestadoDto> implements OnInit {
    servicos: ServicoPrestadoDto[] = [];
    keyword = '';
    isActive: boolean | null;
    advancedFiltersVisible = false;
    constructor(
        injector: Injector,
        private _servicoPrestadoService: ServicoPrestadoServiceProxy,
        private _tipoServicoPrestadoService: TipoServicoPrestadoServiceProxy,
        private _casoService: CasoServiceProxy,
        private _modalService: BsModalService,
        private _changeDectetor: ChangeDetectorRef,
        private _router: Router,
        private _route: ActivatedRoute) {
        super(injector)
    }
    private _colaboradorId: number;
    public get colaboradorId(): number  { return this._colaboradorId; }
    @Input()
    public set colaboradorId(v: number) { this._colaboradorId = v; this.refresh(); }
    @Input() public colaborador: ColaboradorDto;
    @Input() public funcoes: FuncaoOrgDto[];
    @Input() public historicoFuncoes: ColabFuncaoOrgDto[] = [];
    @Input() casos: CasoDto[];
    @Output() public casosChange: EventEmitter<CasoDto[]> = new EventEmitter<CasoDto[]>();
    tiposServico: TipoServicoPrestadoDto[];
    ngOnInit(): void {
        this.isTableLoading = true;
        this._tipoServicoPrestadoService
        .getAll(undefined, undefined, undefined, undefined, undefined)
        .subscribe((result) => {
            this.tiposServico = result.items;
            this.refresh();
        });
    }
    override refresh(): void {
        this.isTableLoading = true;
        this._casoService.getAllCasos(this.colaboradorId, undefined, undefined)
        .subscribe((result) => { this.casos = result; this.casosChange.emit(result) });
        super.refresh();
    }
    getNomeTipoServico(id: number): string {
        return this.tiposServico?.find(t => t.id == id)?.nome??"-";
    }
    getNomeFuncao(id: number): string {
        var cf = this.historicoFuncoes.find(cf => cf.id == id)
        return this.funcoes?.find(f => f.id == cf?.funcaoOrgId)?.nome??"-";
    }
    getNomeAlunoCaso(id: number): string {
        return this.casos?.find(c => c.id == id)?.aluno?.nome??"-";
    }
    createServicoPrestado() {
        let tiposServicoValidos: TipoServicoPrestadoDto[] = [];
        let colaboradores: ColaboradorDto[] = []; 
        let funcoesColaborador: ColabFuncaoOrgDto[] = [];
        this.historicoFuncoes.forEach((cf) => {
            if (cf.dataFim == undefined) {
                colaboradores.push(this.colaborador);
                funcoesColaborador.push(cf);
            }
        });
        this.tiposServico.forEach((t) => {
            if (t.registravelAvulso &&
                funcoesColaborador.some((cf) => t.funcoesOrg.some( f => f.id == cf.funcaoOrgId))) {
                tiposServicoValidos.push(t);
            }
        });

        let createServicoPrestadoDialog: BsModalRef;
        createServicoPrestadoDialog = this._modalService.show(
          CreateServicoPrestadoDialogComponent,
          {
            class: 'modal-lg',
            initialState: {
                tiposServico: tiposServicoValidos,
                colaboradores: colaboradores,
                funcoesColaborador: funcoesColaborador
            }
          }
        );
    
        createServicoPrestadoDialog.content.onSave.subscribe(() => {
          this.refresh();
        });
    }
    protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
        if (this._colaboradorId == undefined) {
            this.servicos = [];
            this.showPaging({items: [], totalCount: 0}, 0);
            finishedCallback();
            return;
        }
        this._servicoPrestadoService
        .getAll(this._colaboradorId,
            undefined,
            undefined,
            undefined,
            undefined,
            undefined,
            request.skipCount,
            request.maxResultCount)
        .pipe(finalize(() => { finishedCallback(); }))
        .subscribe((result: ServicoPrestadoDtoPagedResultDto) => {
            this.servicos = result.items;
            this.showPaging(result, pageNumber);
        });
    }
    clearFilters(): void {
        this.keyword = '';
        this.getDataPage(1);
    }
    protected delete(entity: ServicoPrestadoDto): void {
        abp.message.confirm(
          "O seguinte servio ser excludo do banco",
          undefined,
          (result: boolean) => {
            if (result) {
              this._servicoPrestadoService.delete(entity.id).subscribe(() => {
                abp.notify.success(this.l('SuccessfullyDeleted'));
                this.refresh();
              });
            }
          }
        );
    }
    podeRegistrarServico() {
        return (this.permission.isGranted("Business.Admin.People") || 
                this.permission.isGranted("Business.Finance") ||
                (this.permission.isGranted("Business.Personal.Colaborador") && this.appSession.user.colaborador?.id == this.colaboradorId));
    }
}