  AlunoDto,
  UpdateAlunoDto,
  AplicacaoProtocoloIntervencaoDto,
  UpdateAplicacaoProtocoloIntervencaoDto,
  UpdateAplicacaoPassoProtIntervencaoDto,
  VersaoProtIntervencaoDto,
  AtuacaoDto,
  HierarquiaAjudaServiceProxy,
