@Component({
  templateUrl: './aplicacao-protocolo-intervencao-details.component.html',
  animations: [appModuleAnimation()]
})
export class AplicacaoProtocoloIntervencaoDetailsComponent extends AppComponentBase implements OnInit {
  saving: boolean;
  alunoId: number;
  private _aluno: AlunoDto;
  get aluno(): AlunoDto  { return this._aluno }
  set aluno(v: AlunoDto) { this._aluno = v }
  apliId: number;
  aplicacao: AplicacaoProtocoloIntervencaoDto;
  modelo: VersaoProtIntervencaoDto;
  constructor(injector: Injector,
    protected _modalService: BsModalService,
    protected _alunoService: AlunoServiceProxy,
    protected _casoService: CasoServiceProxy,
    protected _colaboradorService: ColaboradorServiceProxy,
    protected _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    protected _hierarquiaAjudaService: HierarquiaAjudaServiceProxy,
    protected _changeDetector: ChangeDetectorRef,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
    this.saving = false;
  }

  goBack() : void {
    if (this.permission.isGranted('Business.Personal.Cliente.Aluno') && this.appSession.user.aluno?.id == this.alunoId)
      this._router.navigate(['app', 'personal']);
    else
    this._router.navigate(['app', 'alunos', 'details', this.alunoId]);
  }
  
  ngOnInit() : void {
    const params = this._route.snapshot.paramMap;
    this.alunoId = Number(params.get("alunoId"));
    this.apliId = Number(params.get("apliId"));
    this.refresh();
  }
  public colaboradores: ColaboradorDto[];
  public funcoesColaborador: ColabFuncaoOrgDto[];
  public staff: AtuacaoDto[];
  public modeloHierarquiaAjuda: HierarquiaAjudaDto;
  public atendimentos: RegistroAtendimentoDto[];
  // private _curPasso: number;
  // get curPasso() {return this._curPasso; }
  // private _curEtapa: number;
  // get curEtapa() {return this._curEtapa; }
  alunoLoading: boolean
  apliLoading: boolean
  procLoading: boolean
  get loading(): boolean { return this.alunoLoading || this.apliLoading || this.procLoading }
  refresh() {
    this.alunoLoading = true;
    this._alunoService.get(this.alunoId).subscribe((aluno) => {
      this.aluno = aluno;
      this._casoService.getAllAtuacoes(this.aluno.casoId, undefined, undefined, undefined, undefined)
      .subscribe((result) => { this.staff = result });
      this._colaboradorService.getAllColabFuncoes(undefined, undefined, undefined, undefined, undefined, this.aluno.casoId, undefined, undefined, undefined)
      .subscribe((result) => { this.funcoesColaborador = result.items });
      this._colaboradorService.getAll(undefined, undefined, undefined, undefined, this.aluno.casoId, undefined, undefined, undefined)
      .subscribe((result) => { this.colaboradores = result.items });
      this.alunoLoading = false;
      this._changeDetector.detectChanges();
    });
    this.apliLoading = true;
    this.procLoading = true;
    this._casoService.getAplicacaoProtocoloIntervencao(this.apliId).subscribe((apli) => {
      this.aplicacao = apli;
      this.apliLoading = false;
      this._protocoloIntervencaoService.getVersao(this.aplicacao.modeloId).subscribe((proc) => {
        proc.passosProtIntervencao.sort((a, b) => a.ordenador - b.ordenador);
        this.modelo = proc;
        this._hierarquiaAjudaService.get(this.modelo.hierarquiaAjudaId).subscribe(h => this.modeloHierarquiaAjuda = h);
        this.procLoading = false;
        this._changeDetector.detectChanges();
      })
      // this._curPasso = this._getCurPasso(this.aplicacao);
      // this._curEtapa = this._getCurEtapa(this.aplicacao);
      this._changeDetector.detectChanges();
    });
    this._casoService
      .getAllRegistrosAtendimento(undefined, this.apliId, undefined, undefined, undefined)
      .subscribe((result: RegistroAtendimentoDto[]) => { this.atendimentos = result; });
  }
  save() {
    this.saving = true;

    let updateDto = new UpdateAplicacaoProtocoloIntervencaoDto(this.aplicacao);
    this._casoService.updateAplicacaoProtocoloIntervencao(updateDto).subscribe(
      (result: AplicacaoProtocoloIntervencaoDto) => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.aplicacao = result;
        this.saving = false;
      }
    );
  }
  cancel() {
    this.refresh();
    // this.aplicacao = new AplicacaoProtocoloIntervencaoDto(thiKUs.original);
  }
  // private _getCurPasso(aplicacao) {
  //   let i;
  //   for (i = 0; i < aplicacao.passos.length; i++) {
  //     let passo = aplicacao.passos[i];
  //     if ((passo.dataLinhaDeBase == undefined && passo.dataIntroducao != undefined) ||
  //       passo.dataIntroducao == undefined ||
  //       passo.dataCriterioAlcancado == undefined) {
  //       break;
  //     }
  //   }
  //   return i;
  // }
  // private _getCurEtapa(aplicacao) {
  //   let curPassoIdx = this._getCurPasso(aplicacao);
  //   if (curPassoIdx == aplicacao.passos.length)
  //     return 3;
    
  //   let passo = aplicacao.passos[curPassoIdx];
  //   if (passo.dataLinhaDeBase == undefined && passo.dataIntroducao == undefined)
  //     return 0;
  //   else if (passo.dataIntroducao == undefined)
  //     return 1;
  //   else
  //     return 2;
  // }
  public getEtapaPassoByModeloId(id: number): number {
    let apliPasso = this.aplicacao.passos.find(p => p.modeloPassoId == id);
    return this.getEtapaPasso(apliPasso);
  }
  public getEtapaPasso(apliPasso: AplicacaoPassoProtIntervencaoDto): number {
    if (apliPasso.dataInicio == undefined)
      return 0;
    if (apliPasso.dataLinhaDeBase == undefined && apliPasso.dataIntroducao == undefined)
      return 1;
    else if (apliPasso.dataIntroducao == undefined)
      return 2;
    else if (apliPasso.dataCriterioAlcancado == undefined)
      return 3;
    else 
      return 4;
  }
  getPassoNome(id: number) {
    return this.modelo?.passosProtIntervencao?.find(p => p.id == id)?.desc;
  }
  getPassoSituacao(id: number): string {
    let passo = this.aplicacao.passos.find(p => p.modeloPassoId == id);
    if (passo.dataInicio == undefined)
      return "No iniciado";
    if (passo.dataLinhaDeBase == undefined && passo.dataIntroducao == undefined)
      return "Linha de base ou Introduo";
    else if (passo.dataIntroducao == undefined)
      return "Introduo";
    else if (passo.dataCriterioAlcancado == undefined)
      return "Manuteno";
    else 
      return "Critrio alcanado";
  }
  getPassoMarco(passo: PassoProtIntervencaoDto, j: number): string {
    let apliPasso = this.aplicacao.passos.find(p => p.modeloPassoId == passo.id);
    if (j == 0)
      return apliPasso?.dataInicio?.format('DD/MM/yyyy')??"";
    else if (j == 1)
      return apliPasso?.dataLinhaDeBase?.format('DD/MM/yyyy')??"";
    else if (j == 2)
      return apliPasso?.dataIntroducao?.format('DD/MM/yyyy')??"";
    else if (j == 3)
      return apliPasso?.dataCriterioAlcancado?.format('DD/MM/yyyy')??"";
    else
      return "";
  }
  canClickCell(passo: PassoProtIntervencaoDto, j: number): boolean {
    if (!(this.permission.isGranted("Business.Personal.Colaborador.Supervisor")||
          this.permission.isGranted("Business.Personal.Colaborador.DiretorTecnico"))) 
      return false;
      
    let apliPasso = this.aplicacao.passos.find(p => p.modeloPassoId == passo.id);
    let curEtapa = this.getEtapaPasso(apliPasso);
    return  (j == 0 && curEtapa == 0) ||
            (j == 1 && curEtapa == 1) ||
            (j == 2 && (curEtapa == 1 || curEtapa == 2)) ||
            (j == 3 && curEtapa == 3);
  }
  onClickCell(passo: PassoProtIntervencaoDto, j: number) {
    if (!(this.permission.isGranted("Business.Personal.Colaborador.Supervisor")||
          this.permission.isGranted("Business.Personal.Colaborador.DiretorTecnico"))) 
      return;
      
    let apliPasso = this.aplicacao.passos.find(p => p.modeloPassoId == passo.id);
    let curEtapa = this.getEtapaPasso(apliPasso);
    if (j == 0 && curEtapa != 0) return;
    if (j == 1 && curEtapa != 1) return;
    if (j == 2 && curEtapa != 1 && curEtapa != 2) return;
    if (j == 3 && curEtapa != 3) return;
    let marcoIntervencaoDialog: BsModalRef;
    marcoIntervencaoDialog = this._modalService.show(
      MarcoIntervencaoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          etapa: j,
          passoNome: passo.desc
        }
      }
    );

    marcoIntervencaoDialog.content.onSubmit.subscribe((result: moment.Moment) => {
      let updateDto: UpdateAplicacaoProtocoloIntervencaoDto = new UpdateAplicacaoProtocoloIntervencaoDto(this.aplicacao);
      updateDto.passos = this.aplicacao.passos.map(p => new UpdateAplicacaoPassoProtIntervencaoDto(p));
      var updatePasso = updateDto.passos.find(p => p.id == apliPasso.id);
      if (j == 0)
        updatePasso.dataInicio = result;
      else if (j == 1)
        updatePasso.dataLinhaDeBase = result;
      else if (j == 2)
        updatePasso.dataIntroducao = result;
      else
        updatePasso.dataCriterioAlcancado = result;

      this._casoService.updateAplicacaoProtocoloIntervencao(updateDto).subscribe((result: AplicacaoProtocoloIntervencaoDto) => {
        this.aplicacao = result;
        // this._curPasso = this._getCurPasso(this.aplicacao);
        // this._curEtapa = this._getCurEtapa(this.aplicacao);
        this._changeDetector.detectChanges();
      });
    });
  }
}

