import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    TecnicaServiceProxy,
    CreateTecnicaDto,
    EntityDto
} from '@shared/service-proxies/service-proxies';

@Component({
    templateUrl: './create-tecnica-dialog.component.html'
})
export class CreateTecnicaDialogComponent extends AppComponentBase implements OnInit {
    saving = false;
    tecnica = new CreateTecnicaDto();
    presetRefBib?: number;
    get hasPresetRefBib() { return this.presetRefBib != undefined }
    @Output() onSave = new EventEmitter<any>();

    constructor(injector: Injector,
        public _tecnicaService: TecnicaServiceProxy,
        public bsModalRef: BsModalRef) {
        super(injector);
    }

    ngOnInit(): void {
        if (this.hasPresetRefBib)
            this.tecnica.refBibliograficaId = this.presetRefBib;
    }

    save(): void {
        this.saving = true;

        this._tecnicaService.create(this.tecnica).subscribe(
            () => {
                this.saving = false;
                this.notify.info(this.l('SavedSuccessfully'));
                this.bsModalRef.hide();
                this.onSave.emit();
            }
        );
    }
}
  