import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  AlunoServiceProxy,
  AlunoDto,
  UpdateAlunoDto,
  ColaboradorServiceProxy,
  TipoProvedorSaudeServiceProxy,
  ProvedorSaudeDto,
  Int64EntityDto,
  ProvedorSaudeServiceProxy,
  TipoProvedorSaudeDto,
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { finalize, Observable } from 'rxjs';
import { SelectTipoProvedorDialogComponent } from './select-tipo-provedor-dialog/select-tipo-provedor-dialog.component';
import { PagedListingComponentBase, PagedRequestDto } from '@shared/paged-listing-component-base';
// import { AplicacaoDetailsDialogComponent } from './aplicacao-details-dialog/aplicacao-details-dialog.component';

@Component({
  selector: 'provedores-saude-caso',
  templateUrl: './provedores-saude-caso.component.html',
  animations: [appModuleAnimation()]
})
export class ProvedoresSaudeCasoComponent extends PagedListingComponentBase<ProvedorSaudeDto> implements OnInit {
  private _aluno: AlunoDto;
  @Input() set aluno (v: AlunoDto) { 
    this._aluno = v; 
    this.refresh();
  }
  get aluno(): AlunoDto { return this._aluno; }
  @Output() alunoChange = new EventEmitter<AlunoDto>();

  constructor(injector: Injector,
    protected _alunoService: AlunoServiceProxy,
    private _provedorSaudeService: ProvedorSaudeServiceProxy,
    private _tipoProvedorSaudeService: TipoProvedorSaudeServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }
  
  public tipos: TipoProvedorSaudeDto[];
  public provedores: ProvedorSaudeDto[];
  ngOnInit(): void {
    this._tipoProvedorSaudeService.getAll(undefined, undefined, undefined).subscribe(r => this.tipos = r.items);
    this.refresh()
  }

  protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
    this.isTableLoading = true;
    var o = {
      next: (res) => { 
        this.provedores = res.items; 
        this.showPaging(res, pageNumber);
        this.isTableLoading = false; 
      },
      error: () => { this.showPaging({items: [], totalCount:0}, pageNumber); this.isTableLoading = false; }
    }
    if (this.aluno?.id == undefined) {
      o.error()
      finishedCallback();
      return;
    }
    this._provedorSaudeService.getAll(this.aluno.id, undefined, undefined)
    .pipe(finalize(() => {
        finishedCallback();
    }))
    .subscribe(o);
  }

  getSubtipo(id) {
    return this.tipos?.flatMap(t => t.subtipos)?.find(s => s.id == id);
  }
  getTipo(id) {
    return this.tipos?.find(t => t.id == id);
  }

  protected delete(p: ProvedorSaudeDto): void {
    let subtipo = this.getSubtipo(p.subtipoId);
    let tipo = this.getTipo(subtipo.tipoId);
    abp.message.confirm(
      "Remover o provedor de sade \""+tipo.nome+" ("+subtipo.nome+")\" do aluno "+this.aluno.nome+"?",
      undefined,
      (result: boolean) => {
        if (result) {
          this._provedorSaudeService.delete(p.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }

  addProvedor() {
    let selectTipoProvedorDialog: BsModalRef;
    selectTipoProvedorDialog = this._modalService.show(
      SelectTipoProvedorDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          aluno: this.aluno
        }
      }
    );

    selectTipoProvedorDialog.content.onSave.subscribe((result: AlunoDto) => {
      this.aluno = result;
      this.alunoChange.emit(result);
    });
  }
}
