import { AppComponentBase } from '@shared/app-component-base';
import { Component, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  AlunoServiceProxy,
  AlunoDto,
  AlunoDtoPagedResultDto,
  ResponsavelDto,
  ResponsavelServiceProxy,
  CreateResponsavelDto,
  UpdateAlunoDto,
  Int64EntityDto,
  CasoServiceProxy,
  AtuacaoDto,
  PaisServiceProxy,
  ColaboradorServiceProxy,
  ColaboradorDto,
  ColabFuncaoOrgDto,
  CreateUserPessoaDto,
  UserDto
} from '@shared/service-proxies/service-proxies';
import { PessoaDetailsBaseComponent } from '@app/pessoas/pessoa-details-base/pessoa-details-base.component';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { CreateResponsavelDialogComponent } from '@app/responsaveis/create-responsavel-dialog/create-responsavel-dialog.component';

@Component({
  templateUrl: './aluno-details.component.html',
  animations: [appModuleAnimation()]
})
export class AlunoDetailsComponent extends PessoaDetailsBaseComponent implements OnInit {
  public pessoa: AlunoDto;
  protected initEmptyPessoa() : AlunoDto { return new AlunoDto();}
  
  constructor(injector: Injector,
    protected _alunoService: AlunoServiceProxy,
    protected _responsavelService: ResponsavelServiceProxy,
    protected _casoService: CasoServiceProxy,
    protected _colaboradorService: ColaboradorServiceProxy,
    protected _paisService: PaisServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector, _paisService, _modalService, _router, _route);
  }

  getPessoa(): Observable<AlunoDto> {
    const params = this._route.snapshot.paramMap;
    const id = Number(params.get("alunoId"));
    return this._alunoService.get(id);
  }
  protected getTipoPessoa(): string {
    return "Aluno";
  }  
  colaboradores: ColaboradorDto[];
  funcoesColaborador: ColabFuncaoOrgDto[];
  staff: AtuacaoDto[];
  protected override proccessDto(dto: AlunoDto): void {
    super.proccessDto(dto);    
    if (this.pessoa?.casoId != undefined) {
      this._casoService.getAllAtuacoes(this.pessoa.casoId, undefined, undefined, undefined, undefined).subscribe((result) => {
        this.staff = result;
      });
      this._colaboradorService.getAllColabFuncoes(undefined, undefined, undefined, undefined, undefined, this.pessoa.casoId, undefined, undefined, undefined)
      .subscribe((result) => {
        this.funcoesColaborador = result.items;
      });
      this._colaboradorService.getAll(undefined, undefined, undefined, undefined, this.pessoa.casoId, undefined, undefined, undefined).subscribe((result) => {
        this.colaboradores = result.items;
      });
    }
  }
  override createUser(input: CreateUserPessoaDto): Observable<UserDto> {
    return this._alunoService.createUserParaPessoa(input);
  }
}
