import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ProtocoloAvaliacaoServiceProxy,
  ProtocoloAvaliacaoDto,
  DominioDto,
  HabilidadeDto,
  TipoConfirmacaoHabilidadeDto
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { UpdateHabilidadeDialogComponent } from '../dialogs/update-habilidade-dialog/update-habilidade-dialog.component';

@Component({
  selector: 'habilidade-details',
  templateUrl: './habilidade-details.component.html',
  animations: [appModuleAnimation()]
})
export class HabilidadeDetailsComponent extends AppComponentBase implements OnInit {
  @Input() public protocoloAvaliacao:  ProtocoloAvaliacaoDto;
  @Input() public dominio:    DominioDto;
  @Input() public habilidade: HabilidadeDto | HabilidadeDto;
  @Input() public tiposConfirmacao: TipoConfirmacaoHabilidadeDto[];

  get isCodificada() { return this.habilidade instanceof HabilidadeDto }
  get codigo(): string {
    if (this.habilidade instanceof HabilidadeDto) 
      return this.habilidade.codigo; 
    else 
      return "";
  }
  set codigo(v: string) {
    if (this.habilidade instanceof HabilidadeDto) 
      this.habilidade.codigo = v; 
  }
  public deetsHidden: boolean = true;
  
  constructor(injector: Injector,
    protected _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }
  
  ngOnInit() { 
    // console.log(this.tiposConfirmacao);
    // console.log(this.habilidade);
  }
  
  @Output() public onDelete = new EventEmitter();
  @Output() public onUpdate = new EventEmitter();
  
  updateHabilidade() {
    let updateHabilidadeDialog: BsModalRef;
    updateHabilidadeDialog = this._modalService.show(
      UpdateHabilidadeDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          protocoloAvaliacao: this.protocoloAvaliacao,
          dominio: this.dominio,
          habilidade: this.habilidade,
          tiposConfirmacao: this.tiposConfirmacao
        }
      }
    );

    updateHabilidadeDialog.content.onSave.subscribe(() => {
      this.onUpdate.emit();
    });
  }
  deleteHabilidade() {
    abp.message.confirm(
      "A seguinte habilidade ser excluda do banco: " + this.habilidade.nome,
      undefined,
      (result: boolean) => {
        if (result) {
          this._protocoloAvaliacaoService.deleteHabilidade(this.habilidade.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.onDelete.emit();
          });
        }
      }
    );
  }
  getNomeConfirmacao() {
    return this.tiposConfirmacao?.find(h => h.id == this.habilidade.confirmacaoExplicitaRequeridaId)?.nome;
  }
}
