  alunoId:number;
  apliId:number;
  ngOnInit(): void { 
    const params = this._route.snapshot.paramMap;
    this.alunoId = Number(params.get("alunoId"));
    this.apliId = Number(params.get("apliId"));
    this.refresh(); 
  }

  protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
    this._casoService
      .getAllUnidadesEnsino(undefined, undefined, this.apliId, undefined, undefined, undefined)
      .pipe(finalize(() => finishedCallback()))
      .subscribe((result: UnidadeEnsinoDto[]) => {
        this.unidades = result;
        this.showPaging({ 'items': this.unidades, 'totalCount': this.unidades.length }, pageNumber);
      });
  }

