  getAtendimento(u: UnidadeEnsinoDto): RegistroAtendimentoDto {
    return this.atendimentos?.find(a => a.id == u.registroAtendimentoId);
  }
  getIdxPasso(u: UnidadeEnsinoDto): number { 
    for (let i = 0; i < (this.aplicacao?.passos?.length??0); i++)
      if (this.aplicacao?.passos[i]?.id == u?.passoId)
        return i;
    return -1;
  }
  getAtuacao(id) { return this.staff?.find(a => a.id == id) }
  getColabFuncao(id) { return this.funcoesColaborador?.find(cf => cf.id == id) }
  getColaborador(id) { return this.colaboradores?.find(c => c.id == id) }
  getNomeAT(u: UnidadeEnsinoDto): string { 
    var id = this.getAtendimento(u)?.atuacaoTerapeutaId;
    return this.getColaborador(this.getColabFuncao(this.getAtuacao(id)?.colabFuncaoId)?.colaboradorId)?.nome??"";
  }
  getDataHora(u: UnidadeEnsinoDto): string {
    var a = this.getAtendimento(u);
    return (a?.inicio?.format('DD/MM/yyyy HH:mm')??'') + "~" + (a?.fim?.format('HH:mm')??'')
  }
  getNoAjudas(u: UnidadeEnsinoDto, t: TipoAjudaDto) {
    return u?.ajudas?.find(a => a.key == t.id)?.value;
  }
  podeRegistrarAtendimento(): boolean{
    return (this.permission.isGranted('Business.Personal.Colaborador.Supervisor')||this.permission.isGranted('Business.Personal.Colaborador.AT')) &&
      (this.permission.isGranted('Business.Admin.People') ||
      this.staff.some((a) => this.getColabFuncao(a.colabFuncaoId).colaboradorId == this.appSession.user.colaborador.id && a.dataFim == undefined));
  }
