import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './marco-intervencao-dialog.component.html'
})
export class MarcoIntervencaoDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean = false;
  constructor(injector: Injector,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit() { }

  etapa: number;
  get etapaNome(): string { 
    if (this.etapa == 0)
      return "Incio";
    else if (this.etapa == 1)
      return "Linha de Base";
    else if (this.etapa == 2)
      return "Introduo";
    else
      return "Critrio Alcanado";
  }
  passoNome: string;
  private _data: moment.Moment = moment();
  get data() : string     { return this._data.format('yyyy-MM-DD') }
  set data(value: string) { this._data = moment(value) }
  @Output() onSubmit: EventEmitter<moment.Moment> = new EventEmitter<moment.Moment>();
  save() {
    this.saving = true;
    this.onSubmit.emit(this._data);
    this.bsModalRef.hide();
    this.saving = false;
  }
}
