import {
  Component,
  Injector,
  OnInit,
  Input,
  EventEmitter,
  Output
} from '@angular/core';
import { ControlContainer, NgModelGroup} from '@angular/forms'
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  EntityDto,
  FuncaoOrgDto,
  TipoServicoPrestadoDto,
  TipoServicoPrestadoServiceProxy
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import * as moment from 'moment/moment';
import { CustomFormBaseComponent } from '@app/bases/custom-form-base.component';

@Component({
    selector: 'funcao-form',
    templateUrl: './funcao-form.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class FuncaoFormComponent extends CustomFormBaseComponent implements OnInit {
  @Input() funcao : FuncaoOrgDto;

  optionsAcessoACaso: string[] = [
    "Nenhum",
    "Caso a Caso",
    "Todos"
  ]

  constructor(
    injector: Injector,
    private _tipoServicoPrestadoService: TipoServicoPrestadoServiceProxy
  ) {
    super(injector);
  }

  isTableLoading: boolean = false;
  tiposServicoPrestado: TipoServicoPrestadoDto[];
  ngOnInit(): void {
    this.funcao.acessoAdministrativo = false;
    this.funcao.acessoTecnico = false;
    this.funcao.acessoFinanceiro = false;
    this.isTableLoading = true;
    this._tipoServicoPrestadoService.getAll(undefined, undefined, undefined, undefined, undefined).subscribe((result) => {
      this.tiposServicoPrestado = result.items;
      this.refreshTable();
      this.isTableLoading = false;
    });
  }
  private _tipoServicoPrestadoAdd: number = undefined;
  get tipoServicoPrestadoAdd(): string  { return this._tipoServicoPrestadoAdd?.toString()??"" }
  set tipoServicoPrestadoAdd(v: string) { this._tipoServicoPrestadoAdd = (v == "" || v == undefined)?undefined:Number(v) }
  addTipoServicoPrestado() {
    if (this._tipoServicoPrestadoAdd != undefined) {
      let tipo = this.tiposServicoPrestado[this._tipoServicoPrestadoAdd];
      this.tiposServicoNaoPrestadoPorFunc.splice(this._tipoServicoPrestadoAdd, 1)
      
      this.funcao.tiposServicoPrestado.push(new EntityDto({id: tipo.id}));
      this.tiposServicoPrestadoPorFunc.push(tipo);
      
      this._tipoServicoPrestadoAdd = undefined;
    }
  }
  removeTipoServicoPrestado(i: number) {
    this.funcao.tiposServicoPrestado.splice(i, 1);
    this.tiposServicoNaoPrestadoPorFunc.push(this.tiposServicoPrestadoPorFunc.splice(i, 1)[0]);
  }
  
  tiposServicoPrestadoPorFunc: TipoServicoPrestadoDto[]; 
  tiposServicoNaoPrestadoPorFunc: TipoServicoPrestadoDto[]; 
  refreshTable() {
    this.tiposServicoPrestadoPorFunc = [];
    this.tiposServicoNaoPrestadoPorFunc = [];

    this.funcao.tiposServicoPrestado.forEach(() => { this.tiposServicoPrestadoPorFunc.push(undefined) });
    
    this.tiposServicoPrestado.forEach((tipo) => {
      let i = this.funcao.tiposServicoPrestado.findIndex(t => t.id == tipo.id);
      if (i != -1) 
        this.tiposServicoPrestadoPorFunc[i] = tipo; 
      else
        this.tiposServicoNaoPrestadoPorFunc.push(tipo); 
    });
  }
}
