import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  AlunoServiceProxy,
  TipoProvedorSaudeServiceProxy,
  CreateProvedorSaudeDto,
  TipoProvedorSaudeDto,
  SubtipoProvedorSaudeDto,
  TipoProvedorSaudeDtoPagedResultDto,
  UpdateAlunoDto,
  AlunoDto,
  EntityDto,
  ProvedorSaudeServiceProxy,
  ProvedorSaudeDto
} from '@shared/service-proxies/service-proxies';

@Component({
  templateUrl: './select-tipo-provedor-dialog.component.html'
})
export class SelectTipoProvedorDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean = false;
  @Input() aluno: AlunoDto;
  constructor(injector: Injector,
    private _provedorSaudeService: ProvedorSaudeServiceProxy,
    private _tipoProvedorSaudeService: TipoProvedorSaudeServiceProxy,
    private _alunoService: AlunoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit() {
    this.saving = true;
    this.tipos = [];
    this._tipoProvedorSaudeService.getAll(undefined, undefined, undefined).subscribe((p: TipoProvedorSaudeDtoPagedResultDto) => {
      this.tipos = p.items;
      console.log(this.tipos);
      this.saving = false;
    });
  }

  provedor: CreateProvedorSaudeDto = new CreateProvedorSaudeDto();
  tipos: TipoProvedorSaudeDto[] = [];
  subtipos: SubtipoProvedorSaudeDto[] = [];
  extraInfo: boolean = false;
  _tipoProvedorId: number;
  get tipoProvedorId(): string  { return this._tipoProvedorId?.toString()??"" }
  set tipoProvedorId(v: string) { 
    this._tipoProvedorId = v!=""?Number(v):undefined;
    this.subtipoProvedorId = "";
    let t = this.tipos.find(t => t.id == this._tipoProvedorId);
    this.subtipos = t?.subtipos??[];
  }
  get subtipoProvedorId(): string  { return this.provedor?.subtipoId?.toString()??"" }
  set subtipoProvedorId(v: string) { 
    this.provedor.subtipoId = v!=""?Number(v):undefined;
    let t = this.subtipos.find(t => t.id == this.provedor.subtipoId);
    if (t != undefined)
      this.extraInfo = t.usaCampoDadosAdicionais;
    else 
      this.extraInfo = false;
    if (this.extraInfo)
      this.provedor.campoInfo = "";
    else 
      this.provedor.campoInfo = undefined;
  }

  @Output() onSave: EventEmitter<ProvedorSaudeDto> = new EventEmitter<ProvedorSaudeDto>();
  save() {
    this.saving = true;
    this.provedor.alunoId = this.aluno.id;
    // console.log(this.provedor);
    this._provedorSaudeService.create(this.provedor).subscribe(
      (result: ProvedorSaudeDto) => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.bsModalRef.hide();
        this.onSave.emit(result);
        this.saving = false;
      }
    );
  }
}
