import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    TecnicaServiceProxy,
    UpdateTecnicaDto,
    EntityDto
} from '@shared/service-proxies/service-proxies';

@Component({
    templateUrl: './edit-tecnica-dialog.component.html'
})
export class EditTecnicaDialogComponent extends AppComponentBase implements OnInit {
    saving = false;
    tecnica = new UpdateTecnicaDto();
    @Output() onSave = new EventEmitter<any>();

    constructor(injector: Injector,
        public _tecnicaService: TecnicaServiceProxy,
        public bsModalRef: BsModalRef) {
        super(injector);
    }

    ngOnInit(): void {
    }

    save(): void {
        this.saving = true;
        console.log(this.tecnica)
        this._tecnicaService.update(this.tecnica).subscribe(
            () => {
                this.saving = false;
                this.notify.info(this.l('SavedSuccessfully'));
                this.bsModalRef.hide();
                this.onSave.emit();
            }
        );
    }
}
  