import { AppComponentBase } from '@shared/app-component-base';
import { Component, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ResponsavelServiceProxy,
  ResponsavelDto,
  ResponsavelDtoPagedResultDto,
  AlunoServiceProxy,
  AlunoDto,
  Int64EntityDto,
  PaisServiceProxy,
  CreateUserPessoaDto,
  UserDto
} from '@shared/service-proxies/service-proxies';
import { PessoaDetailsBaseComponent } from '@app/pessoas/pessoa-details-base/pessoa-details-base.component';
import { Observable } from 'rxjs';
import * as moment from 'moment';
import { BsModalService } from 'ngx-bootstrap/modal';
import { CreateAlunoDialogComponent } from '@app/alunos/create-aluno-dialog/create-aluno-dialog.component';
import { SelectAlunoDialogComponent } from '@app/alunos/select-aluno-dialog/select-aluno-dialog.component';

@Component({
  templateUrl: './responsavel-details.component.html',
  animations: [appModuleAnimation()]
})
export class ResponsavelDetailsComponent extends PessoaDetailsBaseComponent implements OnInit {
  public pessoa: ResponsavelDto;
  public dependentes: AlunoDto[];

  protected initEmptyPessoa() : ResponsavelDto { return new ResponsavelDto()}
  
  constructor(injector: Injector,
    protected _responsavelService: ResponsavelServiceProxy,
    protected _alunoService: AlunoServiceProxy,
    protected _paisService: PaisServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector, _paisService, _modalService, _router, _route);
  }

  getPessoa(): Observable<ResponsavelDto> {
    const params = this._route.snapshot.paramMap;
    const id = Number(params.get("responsavelId"));
    return this._responsavelService.get(id);
  }
  protected getTipoPessoa(): string {
    return "Responsavel";
  }
  protected override proccessDto(dto: ResponsavelDto): void {
    super.proccessDto(dto);
    let dep = [];
    this.pessoa.dependentes.forEach((aId) => {
      this._alunoService.get(aId.id).subscribe((result)=>{dep.push(result)});
    });
    this.dependentes = dep;
  }

  getDependenteNome(dependente: Int64EntityDto): string {
    return this.dependentes?.find((d) => d.id == dependente.id)?.nome??"";
  }
  detailsDependente(dependente: Int64EntityDto): void {
    console.log(this.appSession.user)
    if (this.appSession.user.aluno?.id == dependente.id)
      this._router.navigate(['app', 'personal']);
    else
      this._router.navigate(['app', 'alunos', 'details', dependente.id]);
  }

  cadastrarDependente(): void {
    let createDependenteDialog = this._modalService.show(
      CreateAlunoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          temResponsavelPredefinido:true,
          responsavel:this.pessoa
        },
      }
    );
    createDependenteDialog.content.onSave.subscribe(this.ngOnInit.bind(this));
  }
  addDependente(): void {
    let selectDependenteDialog = this._modalService.show(
      SelectAlunoDialogComponent,
      {
        class: 'modal-lg'
      }
    );
    selectDependenteDialog.content.onSubmit.subscribe((result) => {
      this.pessoa.dependentes.push(new Int64EntityDto({ 'id': result.id }));
      this._responsavelService.update(this.pessoa).subscribe((responsavel) => {
        this.pessoa = responsavel;
        this.dependentes.push(result);
      });
    });
  }
  override createUser(input: CreateUserPessoaDto): Observable<UserDto> {
    return this._responsavelService.createUserParaPessoa(input);
  }
}
