import { ChangeDetectorRef, Component, Injector } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  TecnicaServiceProxy,
  TecnicaDto,
  TecnicaDtoPagedResultDto,
  RefBibliograficaDto,
  RefBibliograficaServiceProxy
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
import { CreateTecnicaDialogComponent } from './create-tecnica-dialog/create-tecnica-dialog.component';
import { TecnicaDetailsDialogComponent } from '../ref-bibliograficas/ref-bibliografica-details/tecnicas-ref-bib/tecnica-details-dialog/tecnica-details-dialog.component';
// import { EditTecnicaDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';


class PagedTecnicasRequestDto extends PagedRequestDto {
  keyword: string;
  refBibId: number;
}

@Component({
  templateUrl: './tecnicas.component.html',
  animations: [appModuleAnimation()]
})
export class TecnicasComponent extends PagedListingComponentBase<TecnicaDto> {
  tecnicas: TecnicaDto[] = [];
  referencias: RefBibliograficaDto[] = [];
  keyword = '';
  isActive: boolean | null;
  advancedFiltersVisible = false;

  constructor(
    injector: Injector,
    private _tecnicaService: TecnicaServiceProxy,
    private _refBibService: RefBibliograficaServiceProxy,
    private _modalService: BsModalService,
    private _changeDetector: ChangeDetectorRef,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }
  ngOnInit(): void {
    this._refBibService.getAll(undefined, undefined, undefined).subscribe((result) => {
      this.referencias = result.items;
      this._changeDetector.detectChanges();
    });
    this.refresh();
  }
  getRefBib(id: number): RefBibliograficaDto {
    return this.referencias?.find(r => r.id == id);
  }
  createTecnica(): void {
    let createTecnicaDialog: BsModalRef;
    createTecnicaDialog = this._modalService.show(
      CreateTecnicaDialogComponent,
      {
        class: 'modal-lg',
      }
    );

    createTecnicaDialog.content.onSave.subscribe(() => {
      this.refresh();
    });
  }

  detailsTecnica(tecnica: TecnicaDto): void {
    this._router.navigate(['details', tecnica.id], { relativeTo : this._route });
  }

  clearFilters(): void {
    this.keyword = '';
    this.getDataPage(1);
  }

  protected list(
    request: PagedTecnicasRequestDto,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    request.keyword = this.keyword;

    this._tecnicaService
      .getAll(
        request.keyword,
        request.refBibId,
        request.skipCount,
        request.maxResultCount
      )
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: TecnicaDtoPagedResultDto) => {
        this.tecnicas = result.items;
        this.showPaging(result, pageNumber);
      });
  }

  protected delete(pesssoa: TecnicaDto): void {
    abp.message.confirm(
      "O seguinte tecnica ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._tecnicaService.delete(pesssoa.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
