export class Marco {
  public constructor(private _dict: {[key: string]: number}) { }
  private _etapa: string;
  public get etapa(): string  { return this._etapa }
  public set etapa(v: string) { 
    if (Object.keys(this._dict).includes(this._etapa)) 
      delete this._dict[this._etapa];
    this._etapa = v;
    if (v != undefined)
      this._dict[v] = this._habilidade;
  }
  private _habilidade: number;
  public get habilidade(): number  { return this._habilidade }
  public set habilidade(v: number) { 
    this._habilidade = v; 
    if (this._etapa != undefined)
      this._dict[this._etapa] = v;
  }
  public destroy() { if (this._etapa != undefined) delete this._dict[this._etapa] }
}

