import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ProtocoloAvaliacaoServiceProxy,
  DominioDto,
  NivelDto,
  ProtocoloAvaliacaoDto,
  UpdateProtocoloAvaliacaoDto,
  Int64EntityDto,
  TipoConfirmacaoHabilidadeDto,
  HabilidadeDto
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { UpdateNivelDialogComponent } from '../dialogs/update-nivel-dialog/update-nivel-dialog.component';
import { AddHabilidadeDialogComponent } from '../dialogs/add-habilidade-dialog/add-habilidade-dialog.component';
import { AddDominioDialogComponent } from '../dialogs/add-dominio-dialog/add-dominio-dialog.component';
@Component({
  selector: 'nivel-details',
  templateUrl: './nivel-details.component.html',
  animations: [appModuleAnimation()]
})
export class NivelDetailsComponent extends AppComponentBase implements OnInit {
  @Input() public protocoloAvaliacao: ProtocoloAvaliacaoDto;
  @Input() public nivel: NivelDto;
  private _dominios: DominioDto[];
  public get dominios(): DominioDto[]  { return this._dominios }
  @Input() 
  public set dominios(v: DominioDto[]) {
    this._dominios = v?.filter(d => d.nivelId == this.nivel?.id)??[];
  }
  private _habilidades: HabilidadeDto[];
  public get habilidades(): HabilidadeDto[]  { return this._habilidades }
  @Input() 
  public set habilidades(v: HabilidadeDto[]) {
    this._habilidades = v?.filter(h => this.dominios?.some(d => d.id == h.dominioId)??false)??[];
  }
  @Input() public tiposConfirmacao: TipoConfirmacaoHabilidadeDto[];
  public deetsHidden: boolean = true;
  constructor(injector: Injector,
    protected _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }
  ngOnInit() {
    // console.log(this.tiposConfirmacao);
    // console.log(this.nivel);
    if (this.dominios == undefined)
      this._protocoloAvaliacaoService
      .getAllDominios(undefined, undefined, this.nivel.id, undefined, undefined, undefined, undefined)
      .subscribe((result) => { this._dominios = result.items; });
    if (this.habilidades == undefined)
      this._protocoloAvaliacaoService
      .getAllHabilidades(undefined, undefined, undefined, undefined, this.nivel.id, undefined, undefined, undefined, undefined, undefined)
      .subscribe((result) => { this._habilidades = result.items; });
  }

  @Output() public onDelete = new EventEmitter();
  @Output() public onUpdate = new EventEmitter();
  
  updateNivel() {
    let updateNivelDialog: BsModalRef;
    updateNivelDialog = this._modalService.show(
      UpdateNivelDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          protocoloAvaliacao: this.protocoloAvaliacao,
          nivel: this.nivel,
          tiposConfirmacao: this.tiposConfirmacao
        }
      }
    );

    updateNivelDialog.content.onSave.subscribe(() => {
      this.onUpdate.emit();
    });
  }
  deleteNivel() {
    abp.message.confirm(
      "O seguinte nivel ser excludo do banco: " + this.nivel.nome,
      undefined,
      (result: boolean) => {
        if (result) {
          this._protocoloAvaliacaoService.deleteNivel(this.nivel.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.onDelete.emit();
          });
        }
      }
    );
  }

  addDominio() {
    let addDominioDialog: BsModalRef;
    addDominioDialog = this._modalService.show(
      AddDominioDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          nivel: this.nivel,
          tiposConfirmacao: this.tiposConfirmacao
        }
      }
    );

    addDominioDialog.content.onSave.subscribe(() => {
      this.onUpdate.emit();
    });

  }
}
