import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { 
  AlunoDto,
  AlunoServiceProxy,
  Int64Int32KeyValuePair,
  TipoAjudaDto,
  PassoProtIntervencaoDto,
  TipoServicoPrestadoServiceProxy,
  AtuacaoDto,
  CasoServiceProxy,
  VersaoProtIntervencaoDto,
  ParticipacaoSupervisaoDto,
  ColaboradorDto,
  ColabFuncaoOrgDto,
  CreateRegistroSupervisaoDto,
  RegistroSupervisaoDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable, Observer } from 'rxjs';

@Component({
  templateUrl: './registro-supervisao-dialog.component.html'
})
export class RegistroSupervisaoDialogComponent extends AppComponentBase implements OnInit {
  public readonly NOME_SERVICO_SUPERVISAO = "Superviso";
  public readonly NOME_SERVICO_PARTICIPACAO = "Participao em Superviso";
  loadingTiposServico: boolean = false;
  loadingStaff: boolean = false;
  loadingFuncoesColaborador: boolean = false;
  loadingColaboradores: boolean = false;
  get loading(): boolean { return this.loadingTiposServico || this.loadingStaff || this.loadingFuncoesColaborador || this.loadingColaboradores }
  saving: boolean = false;
  get disabled(): boolean { return this.loading || this.saving }

  aluno: AlunoDto;
  supervisao: CreateRegistroSupervisaoDto;
  constructor(injector: Injector,
    private _alunoService: AlunoServiceProxy,
    private _casoService: CasoServiceProxy,
    private _tipoServicoService: TipoServicoPrestadoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  private _setSupervisoresEParticipantes() {
    if (this.loading ||
      this.staff == undefined || this._funcoesApropriadasParticipantes == undefined || this._funcoesApropriadasSupervisor == undefined)
      return;
    
    this.supervisores = [];
    this.podemParticipar = [];
    this.staff.forEach((a) => {
      if (this._funcoesApropriadasSupervisor.includes(this.funcoesColaborador.find(cf => cf.id == a.colabFuncaoId).funcaoOrgId) && a.dataFim == undefined)
        this.supervisores.push(a);
      if (this._funcoesApropriadasParticipantes.includes(this.funcoesColaborador.find(cf => cf.id == a.colabFuncaoId).funcaoOrgId) && a.dataFim == undefined)
        this.podemParticipar.push(a);
    });
    if (this.permission.isGranted('Business.Personal.Colaborador.DiretorTecnico')) {
      this.options = this.supervisores;
    } else if (this.permission.isGranted('Business.Personal.Colaborador.Supervisor.Gestor')) {
      var id = this.appSession.user.colaborador?.id;
      if (id != undefined) {
        var cfs = this.funcoesColaborador.filter(cf => cf.colaboradorId == id && cf.dataFim == undefined);
        var geridos = this.funcoesColaborador.filter(cf => cf.dataFim == undefined && cf.gestorId != undefined && cfs.some(g => g.id == cf.gestorId));
        var cfOptions = cfs.concat(geridos);
        this.options = this.supervisores.filter(a => cfOptions.some(cf => cf.id == a.colabFuncaoId))
      } 
    } else if (this.permission.isGranted('Business.Personal.Colaborador.Supervisor.Assistente')) {
      var id = this.appSession.user.colaborador?.id;
      if (id != undefined) {
        var cfs = this.funcoesColaborador.filter(cf => cf.colaboradorId == id);
        this.options = this.supervisores.filter(a => cfs.some(cf => cf.id == a.colabFuncaoId))
      } 
    } else {
      this.options = [];
    }
  }

  private _funcoesApropriadasSupervisor: number[];
  private _funcoesApropriadasParticipantes: number[];
  colaboradores: ColaboradorDto[];
  funcoesColaborador: ColabFuncaoOrgDto[];
  staff: AtuacaoDto[];
  options: AtuacaoDto[];
  supervisores: AtuacaoDto[];
  podemParticipar: AtuacaoDto[];
  ngOnInit() {
    this.loadingTiposServico = true;
    this._tipoServicoService
      .getAll(undefined, undefined, undefined, undefined, undefined)
      .subscribe((result) => {
        this._funcoesApropriadasSupervisor = [];
        this._funcoesApropriadasParticipantes = [];
        result.items.forEach((t) => {
          if (t.nome.startsWith(this.NOME_SERVICO_SUPERVISAO))
            t.funcoesOrg.forEach((f) => {
              if (!this._funcoesApropriadasSupervisor.includes(f.id))
                this._funcoesApropriadasSupervisor.push(f.id);
            });
          else if (t.nome.startsWith(this.NOME_SERVICO_PARTICIPACAO)) 
            t.funcoesOrg.forEach((f) => {
              if (!this._funcoesApropriadasParticipantes.includes(f.id))
                this._funcoesApropriadasParticipantes.push(f.id);
            });
        });
        this.loadingTiposServico = false;
        this._setSupervisoresEParticipantes();
      });
    if (this.staff == undefined) {
      this.loadingStaff = true;
      this._casoService.getAllAtuacoes(this.aluno.casoId, undefined, undefined, undefined, undefined)
        .subscribe((result) => { 
          this.staff = result; 
          this.loadingStaff = false; 
        this._setSupervisoresEParticipantes();
      });
    }
    this.supervisao = new CreateRegistroSupervisaoDto();
    this.supervisao.casoId = this.aluno.casoId;
    this.supervisao.participacoes = [];
    this.supervisao.inicio = moment();
    this.supervisao.fim    = moment();
  }
  getFuncaoColaborador(id: number): ColabFuncaoOrgDto {
    return this.funcoesColaborador?.find(c => c.id == id);
  }
  getColaborador(id: number): ColaboradorDto {
    return this.colaboradores?.find(c => c.id == id);
  }

  get data(): string  { return this.supervisao?.inicio?.format("YYYY-MM-DD") }
  set data(v: string) { 
    let ini = moment(v), fim = moment(v); 
    ini = ini.hour  (this.supervisao?.inicio?.hour()  ??0); 
    fim = fim.hour  (this.supervisao?.fim?.   hour()  ??0); 
    ini = ini.minute(this.supervisao?.inicio?.minute()??0); 
    fim = fim.minute(this.supervisao?.fim?.   minute()??0);
    this.supervisao.inicio = ini; 
    this.supervisao.fim    = fim; 
  }
  get horarioInicio(): string  { return this.supervisao?.inicio?.format("HH:mm") }
  set horarioInicio(v: string) {
    let hora   = Number(v.substring(0, 2)); 
    let minuto = Number(v.substring(3, 5)); 
    this.supervisao.inicio = this.supervisao?.inicio?.hour  (hora); 
    this.supervisao.inicio = this.supervisao?.inicio?.minute(minuto); 
  }
  get horarioFim(): string  { return this.supervisao?.fim?.format("HH:mm") }
  set horarioFim(v: string) {
    let hora   = Number(v.substring(0, 2)); 
    let minuto = Number(v.substring(3, 5)); 
    this.supervisao.fim = this.supervisao?.fim?.hour  (hora); 
    this.supervisao.fim = this.supervisao?.fim?.minute(minuto); 
  }

  get supervisorId(): string  { return this.supervisao?.atuacaoSupervisorId?.toString()??"" }
  set supervisorId(v: string) { this.supervisao.atuacaoSupervisorId = (v != "" && v != undefined ? Number(v) : undefined) }


  onParticipacaoChange(i: number, checked: boolean) {
    let part = this.podemParticipar[i];
    if (!this.supervisao.participacoes.some(p => p.atuacaoParticipanteId == part.id) && checked)
      this.supervisao.participacoes.push(new ParticipacaoSupervisaoDto({ atuacaoParticipanteId: part.id, servicoPrestadoId: undefined }));
    else if (this.supervisao.participacoes.some(p => p.atuacaoParticipanteId == part.id) && !checked)
      this.supervisao.participacoes.splice(this.supervisao.participacoes.findIndex(p => p.atuacaoParticipanteId == part.id), 1);
  }

  @Output() onSave: EventEmitter<RegistroSupervisaoDto> = new EventEmitter<RegistroSupervisaoDto>();
  save() {
    this.saving = true;
    let o: Observer<RegistroSupervisaoDto> = {
      next: (a) => {
        this.onSave.emit(a);
        this.bsModalRef.hide();
        this.saving = false;
      },
      error: (e) => {
        console.log(e)
        this.saving = false;
      },
      complete: () => {}
    };
    this.supervisao.inicio = this.supervisao.inicio.utcOffset(0, true)
    this.supervisao.fim    = this.supervisao.fim   .utcOffset(0, true)
    this._casoService.registrarSupervisao(this.supervisao).subscribe(o);
  }
}
