import {
    Component,
    Injector,
    OnInit,
    Input,
    EventEmitter,
    Output,
    ChangeDetectorRef
} from '@angular/core';
import { ControlContainer, NgModelGroup} from '@angular/forms'
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    ProtocoloIntervencaoServiceProxy,
    ProtocoloIntervencaoDto,
    CreateProtocoloIntervencaoDto,
    TipoAjudaDto,
    VersaoProtIntervencaoDto,
    CreateVersaoProtIntervencaoDto,
    FormatoEnsinoServiceProxy,
    HierarquiaAjudaServiceProxy,
    FormatoEnsinoDto,
    HierarquiaAjudaDto,
    ProtocoloAvaliacaoServiceProxy,
    ProtocoloAvaliacaoDto,
    HabilidadeDto,
    TecnicaServiceProxy,
    TecnicaDto,
    CreatePassoProtIntervencaoDto,
    UpdatePassoProtIntervencaoDto,
    UpdateVersaoProtIntervencaoDto,
    EntityDto,
    ReforcadorDto,
    HabilidadeDtoPagedResultDto,
    DominioDto,
    ProtocoloAvaliacaoDtoPagedResultDto,
    Int64EntityDto,
    DominioDtoPagedResultDto,
    NivelDto,
    NivelDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { finalize } from 'rxjs/operators';

import { PagedListingComponentBase } from '@shared/paged-listing-component-base';
import { CustomFormBaseComponent } from '@app/bases/custom-form-base.component';
import { SelectTecnicaDialogComponent } from './select-tecnica-dialog/select-tecnica-dialog.component';
import { SelectHabilidadeDialogComponent } from './select-habilidade-dialog/select-habilidade-dialog.component';
import { CreateReforcadorDialogComponent } from './create-reforcador-dialog/create-reforcador-dialog.component';
import { CreatePassoProtIntervencaoDialogComponent } from './create-passo-prot-intervencao-dialog/create-passo-prot-intervencao-dialog.component';
import { ActivatedRoute, Router } from '@angular/router';
import { SelectFormatoEnsinoDialogComponent } from './select-formato-ensino-dialog/select-formato-ensino-dialog.component';
import { CreateHierarquiaAjudaDialogComponent } from '@app/hierarquias-ajuda/create-hierarquia-ajuda-dialog/create-hierarquia-ajuda-dialog.component';

export function getData(v, skips:string[]=[]): any {
  let data: any = {};
  let init_props = Object.keys(v);
  init_props.forEach((propName) => {
    let vProp = v[propName];
    if (!skips.includes(vProp)) {
      if (typeof(vProp) == "object" && Object.keys(vProp).includes("id")) {
        data[propName + "Id"] = vProp["id"];
      } else if (Array.isArray(vProp) && Object.keys(vProp[0]).includes('id')) {
        data[propName + "Id"] = [];
        vProp.forEach((i) => data[propName + "Id"].push(i['id']));
      } else if (Array.isArray(vProp)) {
        data[propName] = [];
        vProp.forEach((i) => data[propName].push(i));
      } else {
        data[propName] = vProp;
      }
    }
  });
  return data;
}

@Component({
  selector: 'versao-form',
  templateUrl: './versao-form.component.html',
  viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class VersaoFormComponent extends CustomFormBaseComponent implements OnInit {
  private _versao: CreateVersaoProtIntervencaoDto | UpdateVersaoProtIntervencaoDto;
  @Input() 
  get versao(): CreateVersaoProtIntervencaoDto | UpdateVersaoProtIntervencaoDto  { return this._versao }
  set versao(v: CreateVersaoProtIntervencaoDto | UpdateVersaoProtIntervencaoDto) { 
    this._versao = v;
    if (this._versao instanceof UpdateVersaoProtIntervencaoDto)
      this._versao?.passosProtIntervencao?.sort((a, b) => a.ordenador - b.ordenador);
  }
  @Output() versaoChange: EventEmitter<CreateVersaoProtIntervencaoDto | UpdateVersaoProtIntervencaoDto> = new EventEmitter<CreateVersaoProtIntervencaoDto | UpdateVersaoProtIntervencaoDto>();
  @Output() public loadingChange: EventEmitter<boolean> = new EventEmitter<boolean>();
  updateLoading() {
    this.loadingChange.emit(
      this.habilidadesLoading||
      this.dominiosLoading||
      this.niveisLoading||
      this.protocolosAvaliacaoLoading||
      this.formatosEnsinoLoading||
      this.hierarquiasAjudaLoading||
      this.tecnicasLoading
    );
  }
  @Input() public disabled: boolean = false;
  public saving: boolean = false;

  constructor(
    injector: Injector,
    protected _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    protected _formatoEnsinoService: FormatoEnsinoServiceProxy,
    protected _hierarquiaAjudaService: HierarquiaAjudaServiceProxy,
    protected _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    protected _tecnicaService: TecnicaServiceProxy,
    protected _changeDetector: ChangeDetectorRef,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute
  ) {
    super(injector);
  }
  
  protocolosAvaliacaoLoading: boolean;
  protocolosAvaliacao: ProtocoloAvaliacaoDto[];
  protocolosAvaliacaoMap: Map<number, ProtocoloAvaliacaoDto>;
  
  niveisLoading: boolean;
  niveis: NivelDto[];
  niveisMap: Map<number, NivelDto>;
  
  dominiosLoading: boolean;
  dominios: DominioDto[];
  dominiosMap: Map<number, DominioDto>;
  
  habilidadesLoading: boolean;
  habilidades: HabilidadeDto[];
  habilidadesMap: Map<number, HabilidadeDto>;
  
  formatosEnsinoLoading: boolean;
  formatosEnsino: FormatoEnsinoDto[];
  formatosEnsinoMap: Map<number, FormatoEnsinoDto>;

  hierarquiasAjudaLoading: boolean;
  hierarquiasAjuda: HierarquiaAjudaDto[];
  
  tecnicasLoading: boolean;
  tecnicas: TecnicaDto[];
  tecnicasMap: Map<number, TecnicaDto>;
  
  ngOnInit(): void {
    this.protocolosAvaliacaoLoading = true;
    this.updateLoading()
    this._protocoloAvaliacaoService.getAll(undefined, undefined, undefined, undefined, undefined)
    .subscribe((result: ProtocoloAvaliacaoDtoPagedResultDto) => {
      this.protocolosAvaliacao = result.items;
      this.protocolosAvaliacaoMap = new Map<number, ProtocoloAvaliacaoDto>();
      this.protocolosAvaliacao.forEach(p => this.protocolosAvaliacaoMap.set(p.id, p));

      this.protocolosAvaliacaoLoading = false;
      this.updateLoading()
    });

    this.niveisLoading = true;
    this.updateLoading()
    this._protocoloAvaliacaoService.getAllNiveis(undefined, undefined, undefined, undefined, undefined, undefined)
    .subscribe((result: NivelDtoPagedResultDto) => {
      this.niveis = result.items;
      this.niveisMap = new Map<number, NivelDto>();
      this.niveis.forEach(d => this.niveisMap.set(d.id, d));

      this.niveisLoading = false;
      this.updateLoading()
    });

    this.dominiosLoading = true;
    this.updateLoading()
    this._protocoloAvaliacaoService.getAllDominios(undefined, undefined, undefined, undefined, undefined, undefined, undefined)
    .subscribe((result: DominioDtoPagedResultDto) => {
      this.dominios = result.items;
      this.dominiosMap = new Map<number, DominioDto>();
      this.dominios.forEach(d => this.dominiosMap.set(d.id, d));

      this.dominiosLoading = false;
      this.updateLoading()
    });

    this.habilidadesLoading = true;
    this.updateLoading()
    this._protocoloAvaliacaoService
    .getAllHabilidades(undefined, undefined, undefined, undefined, undefined, undefined, undefined, undefined, undefined, undefined)
    .subscribe((result: HabilidadeDtoPagedResultDto) => {
      this.habilidades = result.items;
      this.habilidadesMap = new Map<number, HabilidadeDto>();
      this.habilidades.forEach(h => this.habilidadesMap.set(h.id, h));

      this.habilidadesLoading = false;
      this.updateLoading()
    });
    
    this.formatosEnsinoLoading = true; 
    this.updateLoading()
    this._formatoEnsinoService.getAll(undefined, undefined).subscribe((result) => {
      this.formatosEnsino = result.items;
      this.formatosEnsinoMap = new Map<number, FormatoEnsinoDto>();
      this.formatosEnsino.forEach(f => this.formatosEnsinoMap.set(f.id, f));
      this.formatosEnsinoLoading = false; 
      this.updateLoading()
    });
    
    this.hierarquiasAjudaLoading = true; 
    this.updateLoading()
    this._hierarquiaAjudaService.getAll(undefined, undefined).subscribe((result) => {
      this.hierarquiasAjuda = result.items;
      this.hierarquiasAjudaLoading = false; 
      this.updateLoading()
    });
    
    this.tecnicasLoading = true; 
    this.updateLoading()
    this._tecnicaService.getAll(undefined, undefined, undefined, undefined).subscribe((result) => {
      this.tecnicas = result.items;
      this.tecnicasMap = new Map<number, TecnicaDto>();
      this.tecnicas.forEach((v) => this.tecnicasMap.set(v.id, v));
      this.tecnicasLoading = false; 
      this.updateLoading()
    });
  }

  getHabilidadeNome(habilidadeId: number): string {
    if (habilidadeId == undefined) return undefined;
    let hab = this.habilidadesMap?.get(Number(habilidadeId));
    return hab?.nome??"";
  }
  
  getHabilidadeString(habilidadeId: number): string {
    if (habilidadeId == undefined) return undefined;
    let hab = this.habilidadesMap?.get(Number(habilidadeId));
    if (hab == undefined) return undefined;
    let s: string = "";
    if (hab.codigo != undefined)
      s += "[" + hab.codigo + "] ";
    s += hab.nome;
    if (hab?.dominioId != undefined) {
      let dominio = this.dominiosMap.get(hab.dominioId);
      let nivel = this.niveisMap.get(dominio.nivelId);
      let protocoloAvaliacao = this.protocolosAvaliacaoMap.get(nivel?.protocoloAvaliacaoId);
      s += " (";
      s += protocoloAvaliacao?.nome + ": ";
      s += nivel?.nome + ": ";
      s += dominio?.nome;
      s += ")";
    }
    return s;
  }
  addAlvo() {
    let habs: HabilidadeDto[] = [];
    this.habilidades.forEach((tec) => {
      if (!this.versao.objetivosAlvo.some(t => t.id == tec.id))
        habs.push(tec);
    });

    let selectHabilidadeDialog: BsModalRef;
    selectHabilidadeDialog = this._modalService.show(
      SelectHabilidadeDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          habilidades: habs
        }
      }
    );

    selectHabilidadeDialog.content.onSubmit.subscribe(((hab: HabilidadeDto) => {
      this.versao.objetivosAlvo.push(new Int64EntityDto(hab));
      // this.objetivosAlvo.push(hab);
      this._changeDetector.detectChanges();
    }).bind(this));
  }
  rmvAlvo(i: number) {
    this.versao.objetivosAlvo.splice(i, 1);
    this._changeDetector.detectChanges();
  }
  getFormatoEnsinoNome(formatoId: number): string {
    return this.formatosEnsinoMap?.get(formatoId)?.nome??"";
  }
  addFormatoEnsino() {
    let foes: FormatoEnsinoDto[] = [];
    this.formatosEnsino.forEach((foe) => {
      if (!this.versao.formatosEnsino.some(t => t.id == foe.id))
        foes.push(foe);
    });

    let selectFormatoEnsinoDialog: BsModalRef;
    selectFormatoEnsinoDialog = this._modalService.show(
      SelectFormatoEnsinoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          formatosEnsino: foes
        }
      }
    );

    selectFormatoEnsinoDialog.content.onSubmit.subscribe((formatoEnsino: FormatoEnsinoDto) => {
      if (this.formatosEnsino.every(f => f.id != formatoEnsino.id)) {
        this.formatosEnsino.push(formatoEnsino);
        this.formatosEnsinoMap.set(formatoEnsino.id, formatoEnsino);
      }
      this.versao.formatosEnsino.push(new EntityDto({id: formatoEnsino.id}));
      this._changeDetector.detectChanges();
    });
  }
  rmvFormatoEnsino(i: number) {
    this.versao.formatosEnsino.splice(i, 1);
    this._changeDetector.detectChanges();
  }
  getTecnicaNome(tecnicaId: number): string {
    return this.tecnicasMap?.get(tecnicaId)?.nome??"";
  }
  addTecnica() {
    let tecs: TecnicaDto[] = [];
    this.tecnicas.forEach((tec) => {
      if (!this.versao.tecnicasUtilizadas.some(t => t.id == tec.id))
        tecs.push(tec);
    });

    let selectTecnicaDialog: BsModalRef;
    selectTecnicaDialog = this._modalService.show(
      SelectTecnicaDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          tecnicas: tecs
        }
      }
    );

    selectTecnicaDialog.content.onSubmit.subscribe((tecnica) => {
      this.versao.tecnicasUtilizadas.push(new EntityDto({id: tecnica.id}));
      this._changeDetector.detectChanges();
    });
  }
  rmvTecnica(i: number) {
    this.versao.tecnicasUtilizadas.splice(i, 1);
    this._changeDetector.detectChanges();
  }
  addReforcador() {
    let createReforcadorDialog: BsModalRef;
    createReforcadorDialog = this._modalService.show(
      CreateReforcadorDialogComponent,
      {
        class: 'modal-lg'
      }
    );

    createReforcadorDialog.content.onSubmit.subscribe((nomeReforcador: string) => {
      if (this.versao.reforcadores == undefined) this.versao.reforcadores = [];
      this.versao.reforcadores.push(new ReforcadorDto({ nome: nomeReforcador, index: this.versao.reforcadores.length}));
      this._changeDetector.detectChanges();
    });
  }
  rmvReforcador(i: number) {
    this.versao.reforcadores.splice(i, 1);
    this.versao.reforcadores = this.versao.reforcadores.sort(r => r.index);
    this.versao.reforcadores.forEach((r, j) => r.index = j);
    this._changeDetector.detectChanges();
  }

  get reforcadores(): string[]  {
    return this.versao?.reforcadores?.sort(r => r.index)?.map(r => r.nome)??[];  
  }
  get passos(): (CreatePassoProtIntervencaoDto | UpdatePassoProtIntervencaoDto)[]  {
    if (this.versao instanceof CreateVersaoProtIntervencaoDto) {
      return this.versao.passosProtIntervencao;
    } else if (this.versao instanceof UpdateVersaoProtIntervencaoDto) {
      return this.versao.passosProtIntervencao; 
    } else {
      return [];
    }
  }
  addPasso() {
    let createPassoDialog: BsModalRef;
    createPassoDialog = this._modalService.show(
      CreatePassoProtIntervencaoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          habilidades: this.habilidades,
          objetivosAlvoId: this.versao.objetivosAlvo.map((d) => d.id)
        }
      }
    );

    createPassoDialog.content.onSubmit.subscribe((passo) => {
      if (this.versao instanceof CreateVersaoProtIntervencaoDto)
        this.versao.passosProtIntervencao.push(passo);
      else if (this.versao instanceof UpdateVersaoProtIntervencaoDto)
        this.versao.passosProtIntervencao.push(new UpdatePassoProtIntervencaoDto(passo));
      this._changeDetector.detectChanges();
    });
  }
  rmvPasso(i: number) {
    if (this.versao instanceof CreateVersaoProtIntervencaoDto) {
      this.versao.passosProtIntervencao.splice(i, 1);
    } else if (this.versao instanceof UpdateVersaoProtIntervencaoDto) {
      this.versao.passosProtIntervencao.splice(i, 1);
    }
    this._changeDetector.detectChanges();
  }

  novaHierarquiaAjuda() {
    let createHierarquiaAjudaDialog: BsModalRef;
    createHierarquiaAjudaDialog = this._modalService.show(
      CreateHierarquiaAjudaDialogComponent,
      {
        class: 'modal-lg',
      }
    );

    createHierarquiaAjudaDialog.content.onSave.subscribe((result: HierarquiaAjudaDto) => {
      this.hierarquiasAjuda.push(result);
      this.versao.hierarquiaAjudaId = result.id;
    });
  }

  public get hierarquiaAjudaId(): string  { return this.versao.hierarquiaAjudaId?.toString()??"" }
  public set hierarquiaAjudaId(v: string) { this.versao.hierarquiaAjudaId = (v != "" ? Number(v) : undefined) }
  public get marcoHabilidadeManutencaoId(): string  { return this.versao.marcoHabilidadeManutencaoId?.toString()??"" }
  public set marcoHabilidadeManutencaoId(v: string) { this.versao.marcoHabilidadeManutencaoId = (v != "" ? Number(v) : undefined) }
  public get marcoHabilidadeAmbienteNaturalId(): string  { return this.versao.marcoHabilidadeAmbienteNaturalId?.toString()??"" }
  public set marcoHabilidadeAmbienteNaturalId(v: string) { this.versao.marcoHabilidadeAmbienteNaturalId = (v != "" ? Number(v) : undefined) }
  public get marcoHabilidadeArquivamentoId(): string  { return this.versao.marcoHabilidadeArquivamentoId?.toString()??"" }
  public set marcoHabilidadeArquivamentoId(v: string) { this.versao.marcoHabilidadeArquivamentoId = (v != "" ? Number(v) : undefined) }
}
  