import { AppComponentBase } from '@shared/app-component-base';
import { ChangeDetectorRef, Component, EventEmitter, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ProtocoloIntervencaoServiceProxy,
  ProtocoloIntervencaoDto,
  UpdateVersaoProtIntervencaoDto,
  VersaoProtIntervencaoDto,
  UpdatePassoProtIntervencaoDto
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { getData } from '../versao-form/versao-form.component';

@Component({
  templateUrl: './versao-edit.component.html',
  animations: [appModuleAnimation()]
})
export class VersaoEditComponent extends AppComponentBase implements OnInit {
  public loading: boolean = false;
  public saving: boolean = false;
  public versao: UpdateVersaoProtIntervencaoDto = new UpdateVersaoProtIntervencaoDto();
  public protocoloIntervencao: ProtocoloIntervencaoDto;
  
  constructor(injector: Injector,
    protected _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    protected _changeDetector: ChangeDetectorRef,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  public id: number;
  ngOnInit() { 
    const params = this._route.snapshot.paramMap;
    this.id = Number(params.get("versaoProtIntervencaoId"));
    this.refresh();
  }
  initialPassos: UpdatePassoProtIntervencaoDto[];
  refresh() {
    this._protocoloIntervencaoService.getVersao(this.id)
    .subscribe((r: VersaoProtIntervencaoDto) => { 
      this._protocoloIntervencaoService.get(r.protocoloIntervencaoId)
      .subscribe((p: ProtocoloIntervencaoDto) => { this.protocoloIntervencao = p });
      
      this.versao = new UpdateVersaoProtIntervencaoDto(r);

      this.versao.passosProtIntervencao = [];
      this.initialPassos = [];
      r.passosProtIntervencao.forEach((p) => { 
        let updP = new UpdatePassoProtIntervencaoDto(p);
        this.initialPassos.push(updP);
        this.versao.passosProtIntervencao.push(new UpdatePassoProtIntervencaoDto(updP));
      });
    });
  }

  public onSave: EventEmitter<VersaoProtIntervencaoDto> = new EventEmitter<VersaoProtIntervencaoDto>();
  save() {
    this.saving = true;
    this._protocoloIntervencaoService.updateVersao(this.versao).subscribe(
        (result) => {
          this.saving = false;
          this.notify.info(this.l('SavedSuccessfully'));
          this.onSave.emit(result);
          this._navToDetailsVersao();
        }
    );
  }
  goBack() {
    this._navToDetailsVersao()
  }
  private _navToDetailsVersao() {
    this._router.navigate(["app", "protocolos-intervencao", "versoes", "details", this.id])
  }
  log(x) { console.log(x) }

}
