import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  ProtocoloAvaliacaoServiceProxy,
  CreateResponsavelDto,
  Int64EntityDto,
  ResponsavelDto,
  NivelDto,
  CreateNivelDto,
  UpdateNivelDto,
  UpdateProtocoloAvaliacaoDto,
  ProtocoloAvaliacaoDto,
  TipoConfirmacaoHabilidadeDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './update-nivel-dialog.component.html'
})
export class UpdateNivelDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean;
  tiposConfirmacao: TipoConfirmacaoHabilidadeDto[] = [];
  constructor(injector: Injector,
    public _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit(): void {

  }

  
  protocoloAvaliacao: ProtocoloAvaliacaoDto;
  nivel : NivelDto;
  @Output() onSave = new EventEmitter();
  protected add() {
    this.saving = true;
    let updateInput = new UpdateNivelDto(this.nivel as any);
    this._protocoloAvaliacaoService.updateNivel(updateInput).subscribe(
      () => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.bsModalRef.hide();
        this.saving = false;
        this.onSave.emit();
      }
    );
  }
}
