import { Component, Injector } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  AlunoServiceProxy,
  AlunoDto,
  AlunoDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
import { CreateAlunoDialogComponent } from './create-aluno-dialog/create-aluno-dialog.component';
// import { EditAlunoDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';


class PagedAlunosRequestDto extends PagedRequestDto {
  keyword: string;
}

@Component({
  templateUrl: './alunos.component.html',
  animations: [appModuleAnimation()]
})
export class AlunosComponent extends PagedListingComponentBase<AlunoDto> {
  alunos: AlunoDto[] = [];
  keyword = '';
  isActive: boolean | null;
  advancedFiltersVisible = false;

  constructor(
    injector: Injector,
    private _alunoService: AlunoServiceProxy,
    private _modalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }
  
  createAluno(): void {
    let createAlunoDialog: BsModalRef;
    createAlunoDialog = this._modalService.show(
      CreateAlunoDialogComponent,
      {
        class: 'modal-lg',
      }
    );

    createAlunoDialog.content.onSave.subscribe(() => {
      this.refresh();
    });
  }

  detailsAluno(aluno: AlunoDto): void {
    this._router.navigate(['details', aluno.id], { relativeTo : this._route });
  }

  clearFilters(): void {
    this.keyword = '';
    this.isActive = undefined;
    this.getDataPage(1);
  }

  protected list(
    request: PagedAlunosRequestDto,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    request.keyword = this.keyword;

    this._alunoService
      .getAll(
        request.keyword,
        request.skipCount,
        request.maxResultCount
      )
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: AlunoDtoPagedResultDto) => {
        this.alunos = result.items;
        this.showPaging(result, pageNumber);
      });
  }

  protected delete(pesssoa: AlunoDto): void {
    abp.message.confirm(
      "O seguinte endereo ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._alunoService.delete(pesssoa.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
