import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  ColaboradorServiceProxy,
  CreateColaboradorDto,
  CreateAlunoDto,
  CreateResponsavelDto,
  FuncaoOrgServiceProxy,
  FuncaoOrgDto,
  FuncaoOrgDtoPagedResultDto,
  ColabFuncaoOrgDto,
  CreateGestaoDto,
  ColaboradorDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { CreatePessoaDialogBaseComponent } from '@app/pessoas/create-pessoa-dialog-base/create-pessoa-dialog-base.component';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './add-gerenciado-dialog.component.html'
})
export class AddGerenciadoDialogComponent extends AppComponentBase implements OnInit {
  saving = false;

  gestao: CreateGestaoDto = new CreateGestaoDto();

  public get cfGestorId(): string  { return this.gestao.gestorId?.toString()??"" }
  public set cfGestorId(v: string) { 
    this.gestao.gestorId = v!=""?Number(v):undefined;
    this.gestao.novoGerenciadoId = undefined; 
    this.setTrueGeridoOptions();
  }
  private setTrueGeridoOptions() {
    if (this.gestao.gestorId == undefined) {
      this._trueGeridoOptions = undefined;
      return;
    }
    var gestorCf = this.getGestor(this.gestao.gestorId);
    var gestorFuncao = this.getFuncao(gestorCf.funcaoOrgId);
    var funcoesGeridasId = gestorFuncao.funcoesGeridasDiretamente.map(f => f.id);
    this._trueGeridoOptions = this.geridoOptions.filter(cf => cf.gestorId != gestorCf.id && funcoesGeridasId.includes(cf.funcaoOrgId));
    // console.log(gestorCf, this._trueGeridoOptions);
    this._trueGeridoOptions = this._trueGeridoOptions.filter(cf => cf.gestorId != this.gestao.gestorId);
  }
  
  public get cfGeridoId(): string  { return this.gestao.novoGerenciadoId?.toString()??"" }
  public set cfGeridoId(v: string) { this.gestao.novoGerenciadoId = v!=""?Number(v):undefined }

  constructor(
    protected _colaboradorService: ColaboradorServiceProxy,
    injector: Injector,
    public bsModalRef: BsModalRef
  ) {
    super(injector);
  }

  funcoes: FuncaoOrgDto[];
  colaboradores: ColaboradorDto[];
  gestorOptions: ColabFuncaoOrgDto[];
  geridoOptions: ColabFuncaoOrgDto[];
  private _trueGeridoOptions: ColabFuncaoOrgDto[];
  get trueGeridoOptions(): ColabFuncaoOrgDto[] { return this._trueGeridoOptions };
  ngOnInit(): void {
    if (this.geridoOptions == undefined) {
      let funcoesGerenciaveisId: number[] = [];
      this.gestorOptions.forEach(cf => this.getFuncao(cf.funcaoOrgId).funcoesGeridasDiretamente.forEach(f => funcoesGerenciaveisId.push(f.id)));
      funcoesGerenciaveisId = funcoesGerenciaveisId.filter((fId, i) => !funcoesGerenciaveisId.slice(0, i).includes(fId));
      this._colaboradorService.getAll(undefined, undefined, funcoesGerenciaveisId, undefined, undefined, undefined, undefined, undefined)
      .subscribe((result) => this.colaboradores = this.colaboradores.concat(result.items));
      this._colaboradorService.getAllColabFuncoes(undefined, undefined, undefined, funcoesGerenciaveisId, true, undefined, undefined, undefined, undefined)
      .subscribe((result) => this.geridoOptions = result.items); 
    }
  }
  getFuncao(id: number): FuncaoOrgDto { return this.funcoes.find(f => f.id == id) }
  getGestor(id: number): ColabFuncaoOrgDto { return this.gestorOptions?.find(f => f.id == id) }
  getGerido(id: number): ColabFuncaoOrgDto { return this.geridoOptions?.find(f => f.id == id) }
  getColaborador(id: number): ColaboradorDto { return this.colaboradores?.find(f => f.id == id) }

  @Output() onSave = new EventEmitter<ColabFuncaoOrgDto>();
  save(): void {
    this.saving = true;
    this._colaboradorService.createGestaoColabFuncao(this.gestao)
    .subscribe({
      next: (result: ColabFuncaoOrgDto) => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.onSave.emit(result);
        this.saving = false;
        this.bsModalRef.hide();
      },
      error(err) {
        console.log(err);
        this.saving = false;   
      }
    });
  }
}
