import { AppComponentBase } from '@shared/app-component-base';
import { ChangeDetectorRef, Component, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  AlunoServiceProxy,
  ProtocoloAvaliacaoServiceProxy,
  AlunoDto,
  UpdateAlunoDto,
  AplicacaoProtocoloAvaliacaoDto,
  DominioDto,
  HabilidadeDto,
  AquisicaoHabilidadeDto,
  UpdateAplicacaoProtocoloAvaliacaoDto,
  CasoServiceProxy,
  ProtocoloAvaliacaoDto,
  NivelDto
} from '@shared/service-proxies/service-proxies';
import { PessoaDetailsBaseComponent } from '@app/pessoas/pessoa-details-base/pessoa-details-base.component';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { CreateResponsavelDialogComponent } from '@app/responsaveis/create-responsavel-dialog/create-responsavel-dialog.component';
import * as moment from 'moment';

@Component({
  templateUrl: './aplicacao-protocolo-avaliacao-details.component.html',
  animations: [appModuleAnimation()]
})
export class AplicacaoProtocoloAvaliacaoDetailsComponent extends AppComponentBase implements OnInit {
  saving: boolean;
  
  alunoId: number;
  aluno: AlunoDto;
  alunoLoading: boolean;

  apliId: number;
  aplicacao: AplicacaoProtocoloAvaliacaoDto;
  original: AplicacaoProtocoloAvaliacaoDto;
  aplicacaoLoading: boolean;

  protocoloAvaliacao: ProtocoloAvaliacaoDto;
  niveis: NivelDto[];
  dominios: DominioDto[];
  habilidades: HabilidadeDto[];
  protocoloAvaliacaoLoading: boolean;
  niveisLoading: boolean;
  dominiosLoading: boolean;
  habilidadesLoading: boolean;

  constructor(injector: Injector,
    protected _modalService: BsModalService,
    protected _alunoService: AlunoServiceProxy,
    protected _casoService: CasoServiceProxy,
    protected _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    protected _changeDetector: ChangeDetectorRef,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
    this.saving = false;
  }

  goBack() : void {
    if (this.permission.isGranted('Business.Personal.Cliente.Aluno') && this.appSession.user.aluno?.id == this.alunoId)
      this._router.navigate(['app', 'personal']);
    else
      this._router.navigate(["../.."], {relativeTo:this._route})
  }

  setHabilidadeAdquirida(aquisicao: AquisicaoHabilidadeDto, adq: boolean) {
    if (adq) {
      aquisicao.dataAdquirida = moment(moment.now());
      aquisicao.confirmacaoDireta = true;
      aquisicao.notaConfirmacao = "";
    }
    else {
      aquisicao.dataAdquirida = undefined;
      aquisicao.notaConfirmacao = undefined;
      aquisicao.confirmacaoDireta = undefined;
    }
    this._changeDetector.detectChanges();
  }
  
  nivelPares: {nivel: NivelDto, dominioPares: { dominio: DominioDto, pares: { habilidade: HabilidadeDto, aquisicao: AquisicaoHabilidadeDto }[] }[] }[];
  ngOnInit() : void {
    const params = this._route.snapshot.paramMap;
    this.alunoId = Number(params.get("alunoId"));
    this.apliId = Number(params.get("apliId"));
    this.load();
  }

  refresh() { this._setApli(this.original); }
  _setApli(apli: AplicacaoProtocoloAvaliacaoDto) {
    this.original = apli;
    this.aplicacao = new AplicacaoProtocoloAvaliacaoDto(this.original);
    this.aplicacao.habilidades = [];
    this.original.habilidades.forEach(h => this.aplicacao.habilidades.push(new AquisicaoHabilidadeDto(h)));
  }
  load() {
    this.alunoLoading = true;
    this._alunoService.get(this.alunoId).subscribe((aluno) => {
      this.aluno = aluno;
      this.alunoLoading = false;
      this._changeDetector.detectChanges();
    });
    this.aplicacaoLoading = true;
    this._casoService.getAplicacaoProtocoloAvaliacao(this.apliId).subscribe((apli) => {
      this._setApli(apli);
      this.aplicacaoLoading = false;
      this.setNivelPares();
    });
    this.protocoloAvaliacaoLoading = true;
    this.niveisLoading = true;
    this.dominiosLoading = true;
    this.habilidadesLoading = true;
    this._protocoloAvaliacaoService.getByAplicacao(this.apliId).subscribe((prot) => {
      this.protocoloAvaliacao = prot;
      this._protocoloAvaliacaoService.getAllNiveis(undefined, prot.id, undefined, undefined, undefined, undefined)
      .subscribe((result) => {
        this.niveis = result.items;
        this.niveisLoading = false;
        this.setNivelPares();
      })
      this._protocoloAvaliacaoService.getAllDominios(undefined, prot.id, undefined, undefined, undefined, undefined, undefined)
      .subscribe((result) => {
        this.dominios = result.items;
        this.dominiosLoading = false;
        this.setNivelPares();
      })
      this._protocoloAvaliacaoService.getAllHabilidades(undefined, undefined, undefined, undefined, undefined, prot.id, undefined, undefined, undefined, undefined)
      .subscribe((result) => {
        this.habilidades = result.items;
        this.habilidadesLoading = false;
        this.setNivelPares();
      })
      this.protocoloAvaliacaoLoading = false;
      this.setNivelPares();
    });
  }
  setNivelPares() {
    if (!this.aplicacaoLoading && !this.protocoloAvaliacaoLoading && !this.dominiosLoading && !this.habilidadesLoading) {
      this.nivelPares = [];
      this.niveis.forEach((nivel) => {
        let nivelPar = {'nivel': nivel, 'dominioPares':[]};
        this.nivelPares.push(nivelPar);
      });

      let dominioPares: { dominio: DominioDto, pares: { habilidade: HabilidadeDto, aquisicao: AquisicaoHabilidadeDto }[] }[] = [];
      this.dominios.forEach((dominio) => {
        let dominioPar = { dominio: dominio, pares: []};
        dominioPares.push(dominioPar)
        this.nivelPares.find((n) => n.nivel.id == dominio.nivelId).dominioPares.push(dominioPar);
      });
      this.habilidades.forEach((habilidade) => {
        let dominioPar = dominioPares.find(dp => dp.dominio.id == habilidade.dominioId);
        dominioPar.pares.push({
          "habilidade": habilidade,
          "aquisicao":  this.aplicacao.habilidades.find(h => h.habilidadeId == habilidade.id)
        });
      })
    }
    this._changeDetector.detectChanges();
  }

  hasChanged() {
    // console.log(this.original);
    // console.log(this.aplicacao);
    return !(JSON.stringify(this.original) === JSON.stringify(this.aplicacao));
  }
  save() {
    this.saving = true;
    this.aplicacaoLoading = true;
    let updateDto = new UpdateAplicacaoProtocoloAvaliacaoDto(this.aplicacao);
    this._casoService.updateAplicacaoProtocoloAvaliacao(updateDto).subscribe(
      (result: AplicacaoProtocoloAvaliacaoDto) => {
        this.notify.info(this.l('SavedSuccessfully'));
        this._setApli(result);
        this.aplicacaoLoading = false;
        this.saving = false;
      }
    );
  }
  cancel() {
    console.log(this.hasChanged());
    this.refresh();
    // this.aplicacao = new AplicacaoProtocoloAvaliacaoDto(thiKUs.original);
  }
}

