import { AppComponentBase } from '@shared/app-component-base';
import { Component, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ResponsavelServiceProxy,
  ResponsavelDto,
  ResponsavelDtoPagedResultDto,
  AlunoServiceProxy,
  AlunoDto,
  ColaboradorServiceProxy,
  ColaboradorDto,
  CidadeDto,
  UFDto,
  PaisDto,
  PaisServiceProxy,
  CreateUserPessoaDto,
  UserDto
} from '@shared/service-proxies/service-proxies';
import { Observable } from 'rxjs';
import { BsModalService } from 'ngx-bootstrap/modal';
import { EditInfoPessoalDialogComponent } from '../edit-info-pessoal-dialog/edit-info-pessoal-dialog.component';

@Component({
  templateUrl: './pessoa-details-base.component.html',
  animations: [appModuleAnimation()]
})
export class PessoaDetailsBaseComponent extends AppComponentBase implements OnInit {
  public pessoa: AlunoDto | ColaboradorDto | ResponsavelDto;

  protected initEmptyPessoa() : AlunoDto | ColaboradorDto | ResponsavelDto { return undefined }
  
  createUser(input: CreateUserPessoaDto): Observable<UserDto> { return undefined  }
  get createUserCallable(): (input: CreateUserPessoaDto) => Observable<UserDto> { return this.createUser.bind(this) }

  constructor(injector: Injector,
    protected _paisService: PaisServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
    this.pessoa = this.initEmptyPessoa();
  }

  goBack() : void {
    this._router.navigate(["../.."], {relativeTo:this._route})
  }

  public id: number;
  public cidade: CidadeDto;
  public uf: UFDto;
  public pais: PaisDto;
  protected getPessoa() : Observable<AlunoDto | ColaboradorDto | ResponsavelDto> { throw "NotImplemented" }
  protected getTipoPessoa() : string { throw "NotImplemented" }

  protected proccessDto(dto : AlunoDto | ColaboradorDto | ResponsavelDto) {
    this.pessoa = dto;
    if (this.pessoa.endereco_CidadeId != undefined) {
      this._paisService.getCidade(this.pessoa.endereco_CidadeId).subscribe(r => this.cidade = r);
      this._paisService.getUnidadeFederal(undefined, this.pessoa.endereco_CidadeId).subscribe(r => this.uf = r);
      this._paisService.getPais(undefined, undefined, this.pessoa.endereco_CidadeId).subscribe(r => this.pais = r);
    }
  }
  
  ngOnInit() : void {
    this.getPessoa().subscribe(this.proccessDto.bind(this));
  }

  editInfoPessoal() : void {
    let editInfoPessoalDialog = this._modalService.show(
      EditInfoPessoalDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          pessoaId: this.pessoa.id,
          inputPessoa: this.pessoa,
          tipoPessoa: this.getTipoPessoa()
        },
      }
    );

    editInfoPessoalDialog.content.onSave.subscribe(((dto) => { this.pessoa = dto }).bind(this));
  }
}
