import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  AlunoServiceProxy,
  AlunoDto,
  AlunoDtoPagedResultDto,
  ResponsavelDto,
  ResponsavelServiceProxy,
  CreateResponsavelDto,
  UpdateAlunoDto,
  AtuacaoDto,
  ColaboradorServiceProxy,
  ColaboradorDto,
  Int64EntityDto,
  FuncaoOrgServiceProxy,
  FuncaoOrgDtoPagedResultDto,
  FuncaoOrgDto,
  CasoServiceProxy,
  ColabFuncaoOrgDto
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { NovaAtuacaoDialogComponent } from './nova-atuacao-dialog/nova-atuacao-dialog.component';
import { runPostSignalSetFn } from '@node_modules/@angular/core/primitives/signals';

@Component({
  selector: 'staff-caso',
  templateUrl: './staff-caso.component.html',
  animations: [appModuleAnimation()]
})
export class StaffCasoComponent extends AppComponentBase implements OnInit {
  public pageSize: number = 10;
  public tables: number[] = [];
  public readonly tableNames:     Map<number, string>  = new Map<number, string>();
  public readonly isTableLoading: Map<number, boolean> = new Map<number, boolean>();
  public readonly pageNumber:     Map<number, number>  = new Map<number, number>();
  public readonly totalPages:     Map<number, number>  = new Map<number, number>();
  public readonly totalItems:     Map<number, number>  = new Map<number, number>();
  public readonly showActiveOnly: Map<number, boolean> = new Map<number, boolean>();
  public readonly items:          Map<number, AtuacaoDto[]> = new Map<number, AtuacaoDto[]>();

  private _aluno: AlunoDto;
  get aluno(): AlunoDto { return this._aluno; }
  @Input() set aluno (v: AlunoDto) {
    this._aluno = v;
  }
  @Output() alunoChange = new EventEmitter<AlunoDto>();
  constructor(injector: Injector,
    protected _alunoService: AlunoServiceProxy,
    protected _casoService: CasoServiceProxy,
    protected _colaboradorService: ColaboradorServiceProxy,
    protected _funcaoOrgService: FuncaoOrgServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }
  
  loadingFuncoes: boolean = false;
  loadingStaff: boolean = false;
  funcoesOrg: FuncaoOrgDto[];
  ngOnInit(): void {
    this.loadingFuncoes = true;
    this._funcaoOrgService.getAll(undefined, undefined, undefined).subscribe((res: FuncaoOrgDtoPagedResultDto) => {
      this.tables = [];
      this.funcoesOrg = res.items.map(f => f.nome).sort().map(fn => res.items.find(f => f.nome == fn)).reverse();
      this.funcoesOrg.forEach((f: FuncaoOrgDto) => {
        if (f.acessoCasoNome == "Caso a Caso") {
          this.tables.push(f.id)
          this.tableNames[f.id] = f.nome;
          this.isTableLoading[f.id] = false;
          this.showActiveOnly[f.id] = true;
          this.items[f.id] = [];
          this.pageNumber[f.id] = 1;
          this.totalItems[f.id] = 0;
          this.totalPages[f.id] = 1;
        }
      });
      this.loadingFuncoes = false;
    });
  }
  
  private _colaboradores: ColaboradorDto[];
  @Input()
  public set colaboradores(v: ColaboradorDto[]) { this._colaboradores = v; this.reset() }
  public get colaboradores(): ColaboradorDto[]  { return this._colaboradores }

  private _funcoesColaborador: ColabFuncaoOrgDto[];
  get funcoesColaborador(): ColabFuncaoOrgDto[] { return this._funcoesColaborador }
  @Input() set funcoesColaborador(v: ColabFuncaoOrgDto[]) {
    this._funcoesColaborador = v;
    this.reset()
  }

  private _staff: AtuacaoDto[];
  get staff(): AtuacaoDto[] { return this._staff }
  @Input() set staff(v: AtuacaoDto[]) {
    this._staff = v;
    this.reset()
  }
  getFuncaoColaborador(id: number): ColabFuncaoOrgDto {
    return this._funcoesColaborador?.find(c => c.id == id);
  }
  getColaborador(id: number): ColaboradorDto {
    return this.colaboradores?.find(c => c.id == id);
  }
  reset(): void {
    // console.log(this.aluno);
    this.tables.forEach((id) => {
      this.isTableLoading[id] = true;
      this.items[id] = [];
    });
    this.staff?.forEach((atuacao) => {
      let funcId = this.funcoesColaborador?.find(cf => cf.id == atuacao.colabFuncaoId)?.funcaoOrgId;
      if (this.tables.includes(funcId)) {
        if (!this.showActiveOnly[funcId] || atuacao.dataFim == undefined)
          this.items[funcId].push(atuacao);
      }
    });
    this.tables.forEach((id) => {
      this.pageNumber[id] = 1;
      this.totalItems[id] = this.items[id].length;
      this.totalPages[id] = ((this.items[id].length - (this.items[id].length % this.pageSize)) / this.pageSize) + 1;
      this.isTableLoading[id] = false;
    });
    // console.log("Tables:", this.tables)
  }

  detailsColaborador(a: AtuacaoDto): void {
    var cf = this.funcoesColaborador.find(cf => cf.id == a.colabFuncaoId);
    this._router.navigate(['app', 'colaboradores', 'details', cf.colaboradorId]);
  }

  getDataPage(table: number, page: number) {
    this.isTableLoading[table] = true;
    
    this.totalItems[table] = this.items[table].length;
    this.totalPages[table] = ((this.totalItems[table] - (this.totalItems[table] % this.pageSize)) / this.pageSize) + 1;

    this.pageNumber[table] = (page < this.totalPages[table])?(page):(1);
    this.isTableLoading[table] = false;
  }

  getFuncao(fId: number): FuncaoOrgDto { return this.funcoesOrg.find(f => f.id == fId) }
  hasAddPermission(fId: number): boolean {
    let f = this.getFuncao(fId);
    if (this.permission.isGranted('Business.Personal.Colaborador.DiretorTecnico') || 
        this.permission.isGranted('Business.Admin.People.Assignment'))
      return true;
    return (f.funcaoGestoraDiretaId != undefined &&
      this.permission.isGranted('Business.Personal.Colaborador.Supervisor.Gestor'));
  }
  hasRmvPermission(fId: number, a: AtuacaoDto): boolean {
    if (this.permission.isGranted('Business.Personal.Colaborador.DiretorTecnico') || this.permission.isGranted('Business.Admin.People.Assignment'))
      return true; 
    let cf = this.getFuncaoColaborador(a.colabFuncaoId);
    let f = this.getFuncao(cf.funcaoOrgId);
    if (f.funcaoGestoraDiretaId != undefined &&
      this.permission.isGranted('Business.Personal.Colaborador.Supervisor.Gestor')) {
      let gestorCf = this.getFuncaoColaborador(cf.gestorId);
      return this.appSession?.user?.colaborador?.id == gestorCf.colaboradorId;
    }
  }
  add(fId: number) {
    let novaAtuacaoDialog: BsModalRef;
    novaAtuacaoDialog = this._modalService.show(
      NovaAtuacaoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          aluno: this.aluno,
          funcoesOrg: this.funcoesOrg,
          currentFuncoesColaborador: this.funcoesColaborador,
          staff: this.staff,
          funcId: fId
        }
      }
    );

    novaAtuacaoDialog.content.onSave.subscribe((result: {atuacao: AtuacaoDto, cf: ColabFuncaoOrgDto, colaborador: ColaboradorDto}) => {
      this.staff.push(result.atuacao);
      if (this.funcoesColaborador.every(cf => cf.id != result.cf.id))
        this.funcoesColaborador.push(result.cf);
      if (this.colaboradores.every(c => c.id != result.colaborador.id))
        this.colaboradores.push(result.colaborador);
      this.reset();
    });
  }

  deleteColaborador(a: AtuacaoDto): void {
    if (a.dataFim != undefined)
      return;
    let updateDto = new Int64EntityDto({
      "id": a.id
    });

    this._casoService.removeAtuacao(updateDto).subscribe(
      (result: AtuacaoDto) => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.staff[this.staff.findIndex(x => x.id == a.id)] = result;
        this.reset();
      }
    );
  }

  switchActiveOnly(table: number) {
    this.showActiveOnly[table] = !this.showActiveOnly[table]
    this.reset();
  }
}
