import { AppComponentBase } from '@shared/app-component-base';
import { ChangeDetectorRef, Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ResponsavelServiceProxy,
  ResponsavelDto,
  ResponsavelDtoPagedResultDto,
  AlunoServiceProxy,
  AlunoDto,
  Int64EntityDto,
  CreateResponsavelDto
} from '@shared/service-proxies/service-proxies';
import { PessoaDetailsBaseComponent } from '@app/pessoas/pessoa-details-base/pessoa-details-base.component';
import { Observable } from 'rxjs';
import * as moment from 'moment';
import { BsModalService } from 'ngx-bootstrap/modal';
import { CreateResponsavelDialogComponent } from '@app/responsaveis/create-responsavel-dialog/create-responsavel-dialog.component';
import { ControlContainer, NgModelGroup } from '@node_modules/@angular/forms';

@Component({
  selector: 'responsaveis-aluno',
  templateUrl: './responsaveis-aluno.component.html',
  viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup }]
})
export class ResponsaveisAlunoComponent extends AppComponentBase implements OnInit {
  private _aluno: AlunoDto;
  @Input()
  public set aluno(v: AlunoDto) { this._aluno = v; this.refresh() }
  public get aluno(): AlunoDto  { return this._aluno } 
  
  @Output() 
  public alunoChange: EventEmitter<AlunoDto> = new EventEmitter<AlunoDto>();
  
  public responsaveis: ResponsavelDto[];
  
  constructor(injector: Injector,
    protected _responsavelService: ResponsavelServiceProxy,
    protected _alunoService: AlunoServiceProxy,
    protected _modalService: BsModalService,
    protected _changeDetector: ChangeDetectorRef,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  ngOnInit() {
    this.refresh()
  }
  refresh() {
    this.responsaveis = []
    // console.log(this.aluno)
    this.aluno?.responsaveis?.forEach((aId) => {
      this._responsavelService.get(aId.id).subscribe((result) => {
        this.responsaveis.push(result);
      });
    });
  }

  getResponsavelNome(responsavel: Int64EntityDto): string {
    return this.responsaveis?.find((d) => d.id == responsavel.id)?.nome??"";
  }
  detailsResponsavel(responsavel: Int64EntityDto): void {
    if (this.appSession.user.responsavel?.id == responsavel.id)
      this._router.navigate(['app', 'personal', responsavel.id]);
    else
      this._router.navigate(['app', 'responsaveis', 'details', responsavel.id]);
  }

  cadastrarResponsavel(): void {
    let createResponsavelDialog = this._modalService.show(
      CreateResponsavelDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          saveOrReturn:true
        },
      }
    );

    createResponsavelDialog.content.onSave.subscribe(((dto: CreateResponsavelDto) => {
      dto.dependentes = [new Int64EntityDto({'id': this.aluno.id})]
      this._responsavelService.create(dto).subscribe((dto: ResponsavelDto) => { 
        this.aluno.responsaveis.push(new Int64EntityDto(dto));
        createResponsavelDialog.content.notify.info(this.l('SavedSuccessfully'));
        createResponsavelDialog.content.bsModalRef.hide();
      });
    }).bind(this));

  }
  // addDependente(): void {
  //   let selectDependenteDialog = this._modalService.show(
  //     SelectAlunoDialogComponent,
  //     {
  //       class: 'modal-lg'
  //     }
  //   );
  //   selectDependenteDialog.content.onSubmit.subscribe((result) => {
  //     this.aluno.dependentes.push(new Int64EntityDto({ 'id': result.id }));
  //     this._responsavelService.update(this.aluno).subscribe((responsavel) => {
  //       this.aluno = responsavel;
  //       this.alunoChange.emit(responsavel);
  //       this.responsaveis.push(result);
  //     });
  //   });
  // }
  podeAddResponsaveis() {
    return this.permission.isGranted("Business.Admin.People")
  }
}
