import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ProtocoloAvaliacaoServiceProxy,
  ProtocoloAvaliacaoDto,
  AlunoServiceProxy,
  AlunoDto,
  AplicacaoProtocoloAvaliacaoDto,
  CasoServiceProxy
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { EntityDto } from '@shared/paged-listing-component-base';

@Component({
  selector: 'casos-protocolo-avaliacao',
  templateUrl: './casos-protocolo-avaliacao.component.html',
  animations: [appModuleAnimation()]
})
export class CasosProtocoloAvaliacaoComponent extends AppComponentBase implements OnInit {
  @Input() public protocoloAvaliacao: ProtocoloAvaliacaoDto;
  constructor(injector: Injector,
    protected _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    protected _casoService: CasoServiceProxy,
    protected _alunoService: AlunoServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }
  ngOnInit() {
    this.refresh();
  }
  aplicacoes: AplicacaoProtocoloAvaliacaoDto[];
  refresh() {
    this.aplicacoes = [];
    if (this.protocoloAvaliacao != undefined)
        this._casoService.getAllAplicacoesProtocoloAvaliacao(undefined, this.protocoloAvaliacao.id).subscribe((result) => this.aplicacoes = result);
  }
  detailsAluno(a: AlunoDto) {
    this._router.navigate(['../../../alunos/details', a.id], { relativeTo : this._route });
  }
}