import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { PagedListingComponentBase, PagedRequestDto } from '@shared/paged-listing-component-base';
import { AlunoDto, AlunoServiceProxy, AtuacaoDto, CasoServiceProxy, ColabFuncaoOrgDto, ColaboradorDto, ColaboradorDtoPagedResultDto, ColaboradorServiceProxy, FuncaoOrgDto, FuncaoOrgDtoPagedResultDto, FuncaoOrgServiceProxy, ColabFuncaoOrgDtoPagedResultDto, Int64EntityDto, UpdateAlunoDto, CreateAtuacaoDto } from '@shared/service-proxies/service-proxies';

@Component({
  templateUrl: './nova-atuacao-dialog.component.html'
})
export class NovaAtuacaoDialogComponent extends PagedListingComponentBase<ColabFuncaoOrgDto> implements OnInit {
  public aluno : AlunoDto;
  public funcId: number = 0;
  public funcoesOrg: FuncaoOrgDto[];
  public get funcaoOrg(): FuncaoOrgDto { return this.funcoesOrg.find(f => f.id == this.funcId) }
  
  public saving: boolean = false;

  public colaboradorSelected: ColabFuncaoOrgDto;
  public colaboradores: ColaboradorDto[] = [];
  public funcoesColaborador: ColabFuncaoOrgDto[] = [];
  public options: ColabFuncaoOrgDto[] = [];
  
  public staff: AtuacaoDto[] = [];
  public currentStaff: AtuacaoDto[] = [];
  public currentFuncoesColaborador: ColabFuncaoOrgDto[] = [];
  
  constructor(injector: Injector,
    public bsModalRef: BsModalRef,
    private _alunoService: AlunoServiceProxy,
    private _colaboradorService: ColaboradorServiceProxy,
    private _casoService: CasoServiceProxy,
    private _funcaoOrgService: FuncaoOrgServiceProxy
  ) {
    super(injector);
  }
  ngOnInit(): void {
    this.currentStaff = this.staff?.filter(a => a.dataFim == undefined)??[];
    if (this.funcoesOrg == undefined) {
      this._funcaoOrgService.getAll(undefined, undefined, undefined).subscribe((res: FuncaoOrgDtoPagedResultDto) => {
        this.funcoesOrg = res.items;
        this.refresh();
      });
    } else {
      this.refresh();
    }
  }
  delete(entity: ColabFuncaoOrgDto): void {
      
  }
  list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
    this.colaboradorSelected = undefined;

    this.isTableLoading = true;
    
    var gestorId: number = undefined;
    var f = this.funcaoOrg;
    if (!this.permission.isGranted('Business.Personal.Colaborador.DiretorTecnico') && !this.permission.isGranted('Business.Admin.People.Assignment')) {
      gestorId = this.appSession?.user?.colaborador?.id;
      if (gestorId == undefined) {
        this.options = [];
        this.showPaging({items: this.options, totalCount: this.options.length}, pageNumber);
      }
    }
    this._colaboradorService.getAll(undefined, gestorId, [this.funcId], true, undefined, undefined, undefined, undefined)
    .subscribe((result: ColaboradorDtoPagedResultDto) => {
      this.colaboradores = result.items;
    });
    this._colaboradorService.getAllColabFuncoes(undefined, undefined, gestorId, [this.funcId], true, undefined, undefined, undefined, undefined)
    .pipe(finalize(() => {
      finishedCallback();
    }))
    .subscribe((result: ColabFuncaoOrgDtoPagedResultDto) => {
      this.funcoesColaborador = result.items;
      this.options = this.funcoesColaborador
      .filter(cf => this.currentStaff.every(a => a.colabFuncaoId != cf.id));
      if (f.funcaoGestoraDiretaId != undefined)
        this.options = this.options.filter(cf => this.currentStaff.some(a => a.colabFuncaoId == cf.gestorId));
      this.showPaging({items: this.options, totalCount: this.options.length}, pageNumber);
    });
  }
  
  getColaborador(id: number): ColaboradorDto {
    return this.colaboradores?.find(c => c.id == id);
  }
  getNomeColaborador(id: number): string {
    return this.getColaborador(id)?.nome;
  }

  selectionToString(): string {
    if (this.colaboradorSelected != undefined) {
      let nomes = this.getNomeColaborador(this.colaboradorSelected.colaboradorId)?.split(' ')??[];
      if (nomes.length == 0) return "";
      return nomes[0] + " " + nomes.reverse()[0];
    }
    return "Nenhum"
  }
  select(c: ColabFuncaoOrgDto) {
    this.colaboradorSelected = c;
  }
  @Output() public onSave = new EventEmitter<{atuacao: AtuacaoDto, cf: ColabFuncaoOrgDto, colaborador: ColaboradorDto}>();
  save() {
    this.saving = true;

    let updateDto = new CreateAtuacaoDto({ "casoId": this.aluno.casoId, "colabFuncaoId": this.colaboradorSelected.id });
    this._casoService.createAtuacao(updateDto).subscribe(
      (result: AtuacaoDto) => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.bsModalRef.hide();
        this.onSave.emit({atuacao: result, cf: this.colaboradorSelected, colaborador: this.colaboradores.find(c => c.id == this.colaboradorSelected.colaboradorId)});
        this.saving = false;
      }
    );
  }
  buttonString(cf: ColabFuncaoOrgDto): string {
    if (this.colaboradorSelected == undefined || cf.id != this.colaboradorSelected.id) {
      return "Select";
    } else {
      return "Deselect";
    }
  }
}
