import { AppComponentBase } from '@shared/app-component-base';
import { Component, Injector, Input, Output, OnInit, EventEmitter } from "@angular/core";
import { PessoaDto, AlunoDto, ColaboradorDto,  ResponsavelDto, CidadeDto, UFDto, PaisDto, UserServiceProxy, UserDto, CreateUserPessoaDto } from '@shared/service-proxies/service-proxies';
import { Observable } from 'rxjs';

@Component({
    selector:'pessoa-account-details',
    templateUrl: './pessoa-account-details.component.html'
})
export class PessoaAccountDetailsComponent extends AppComponentBase implements OnInit {
    private _pessoa: PessoaDto;
    private _user: UserDto;
    @Input() 
    public set pessoa(v: PessoaDto) { 
        this._pessoa = v;
        if (this.pessoa.userId != undefined) {
            this._userService.get(this._pessoa.userId)
            .subscribe((result) => {
                this._user = result;
            });
        }
        this.cancelUserCreate();
    }
    public get pessoa(): PessoaDto  { return this._pessoa }
    public get user(): UserDto { return this._user }
    constructor(injector: Injector,
        protected _userService: UserServiceProxy) {
        super(injector);
    }

    ngOnInit() {
    }

    podeConceder(): boolean {return true}
    
    creating: boolean = false;
    userCreate: CreateUserPessoaDto;
    startUserCreate() {
        this.creating = true;
        let names = this._pessoa.nome.toLowerCase().split(' ');
        this.userCreate = new CreateUserPessoaDto({
            pessoaId: this._pessoa.id,
            username: names[0]+"."+names[names.length-1]
        });
    }
    cancelUserCreate() {
        this.creating = false;
        this.userCreate = undefined;
    }
    usernameValid(): boolean {
        return this.userCreate.username != undefined && this.userCreate.username != "";
    }

    saving: boolean = false;
    @Input()
    createUserCallable: (input: CreateUserPessoaDto) => Observable<UserDto>;
    finishUserCreate() {
        this.saving = true;
        this.createUserCallable(this.userCreate)
        .subscribe({
            next: (result) => {
                this._user = result;
                this._pessoa.userId = result.id;
                this.saving = false;
                this.cancelUserCreate();
            },
            error: (err) => {
                this.saving = false;
                this.cancelUserCreate();
            },
        });
    }
}