import { AppComponentBase } from '@shared/app-component-base';
import { ChangeDetectorRef, Component, EventEmitter, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ProtocoloIntervencaoServiceProxy,
  ProtocoloIntervencaoDto,
  CreateVersaoProtIntervencaoDto,
  VersaoProtIntervencaoDto
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';

@Component({
  templateUrl: './versao-create.component.html',
  animations: [appModuleAnimation()]
})
export class VersaoCreateComponent extends AppComponentBase implements OnInit {
  public loading: boolean = false;
  public saving: boolean = false;
  public protocoloIntervencao: ProtocoloIntervencaoDto;
  public versao: CreateVersaoProtIntervencaoDto = new CreateVersaoProtIntervencaoDto();
  
  constructor(injector: Injector,
    protected _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    protected _changeDetector: ChangeDetectorRef,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  ngOnInit() { 
    const params = this._route.snapshot.paramMap;
    this.loading = true;
    this.versao = new CreateVersaoProtIntervencaoDto();
    this.versao.protocoloIntervencaoId = Number(params.get("protocoloIntervencaoId"));
    this._protocoloIntervencaoService.get(this.versao.protocoloIntervencaoId)
    .subscribe((p: ProtocoloIntervencaoDto) => { 
      this.protocoloIntervencao = p;
      this._protocoloIntervencaoService.getVersao(p.versaoPadraoId)
      .subscribe((v: VersaoProtIntervencaoDto) => {
        this.versao = new CreateVersaoProtIntervencaoDto(v);
        this.loading = false;
      });
    });
  }

  public onSave: EventEmitter<VersaoProtIntervencaoDto> = new EventEmitter<VersaoProtIntervencaoDto>();
  save() {
    this.saving = true;

    this._protocoloIntervencaoService.createVersao(this.versao)
    .subscribe((result) => {
      this.saving = false;
      this.notify.info(this.l('SavedSuccessfully'));
      this.onSave.emit(result);
      this._router.navigate(["app", "protocolos-intervencao", "versao", "details", result.id])
    });
  }
  goBack() {
    this._router.navigate(["app", "protocolos-intervencao", "versao", "details", this.versao.protocoloIntervencaoId])
  }
  log(x) { console.log(x) }

}
