import { Component, Injector } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  RefBibliograficaServiceProxy,
  RefBibliograficaDto,
  RefBibliograficaDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
import { CreateRefBibliograficaDialogComponent } from './create-ref-bibliografica-dialog/create-ref-bibliografica-dialog.component';
// import { EditRefBibliograficaDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';


class PagedRefBibliograficasRequestDto extends PagedRequestDto {
  keyword: string;
}

@Component({
  templateUrl: './ref-bibliograficas.component.html',
  animations: [appModuleAnimation()]
})
export class RefBibliograficasComponent extends PagedListingComponentBase<RefBibliograficaDto> {
  refBibliograficas: RefBibliograficaDto[] = [];
  keyword = '';
  isActive: boolean | null;
  advancedFiltersVisible = false;

  constructor(
    injector: Injector,
    private _refbibliograficaService: RefBibliograficaServiceProxy,
    private _modalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }
  
  createRefBibliografica(): void {
    let createRefBibliograficaDialog: BsModalRef;
    createRefBibliograficaDialog = this._modalService.show(
      CreateRefBibliograficaDialogComponent,
      {
        class: 'modal-lg',
      }
    );

    createRefBibliograficaDialog.content.onSave.subscribe((result) => {
      this.refresh();
      this._router.navigate(['details', result.id], { relativeTo : this._route });
    });
  }

  detailsRefBibliografica(refBibliografica: RefBibliograficaDto): void {
    this._router.navigate(['details', refBibliografica.id], { relativeTo : this._route });
  }

  clearFilters(): void {
    this.keyword = '';
    this.getDataPage(1);
  }

  protected list(
    request: PagedRefBibliograficasRequestDto,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    request.keyword = this.keyword;

    this._refbibliograficaService
      .getAll(
        request.keyword,
        request.skipCount,
        request.maxResultCount
      )
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: RefBibliograficaDtoPagedResultDto) => {
        this.refBibliograficas = result.items;
        this.showPaging(result, pageNumber);
      });
  }

  protected delete(pesssoa: RefBibliograficaDto): void {
    abp.message.confirm(
      "O seguinte refbibliografica ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._refbibliograficaService.delete(pesssoa.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
