import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  AlunoServiceProxy,
  AlunoDto,
  UpdateAlunoDto,
  ColaboradorServiceProxy,
  VersaoProtIntervencaoDto,
  ProtocoloIntervencaoServiceProxy,
  ProtocoloAvaliacaoDtoPagedResultDto,
  AplicacaoProtocoloIntervencaoDto,
  Int64EntityDto,
  CasoServiceProxy,
  VersaoProtIntervencaoDtoPagedResultDto,
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { finalize, Observable } from 'rxjs';
import { SelectProtocoloIntervencaoDialogComponent } from './select-protocolo-intervencao-dialog/select-protocolo-intervencao-dialog.component';
import { PagedListingComponentBase, PagedRequestDto } from '@shared/paged-listing-component-base';
// import { AplicacaoDetailsDialogComponent } from './aplicacao-details-dialog/aplicacao-details-dialog.component';

@Component({
  selector: 'protocolos-intervencao-caso',
  templateUrl: './protocolos-intervencao-caso.component.html',
  animations: [appModuleAnimation()]
})
export class ProtocolosIntervencaoCasoComponent extends PagedListingComponentBase<AplicacaoProtocoloIntervencaoDto> implements OnInit {
  private _aluno: AlunoDto;
  @Input() set aluno (v: AlunoDto) { 
    this._aluno = v; 
    this.refresh();
  }
  get aluno(): AlunoDto { return this._aluno; }
  @Output() alunoChange = new EventEmitter<AlunoDto>();

  constructor(injector: Injector,
    protected _alunoService: AlunoServiceProxy,
    protected _casoService: CasoServiceProxy,
    protected _colaboradorService: ColaboradorServiceProxy,
    private   _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }
  
  ngOnInit(): void {
  }

  detailsProtocoloIntervencao(pId: number) {
    this._router.navigate(['app', 'protocolos-intervencao', 'versoes', 'details', pId]);
  }
  aplicacoes: AplicacaoProtocoloIntervencaoDto[]
  protocolosIntervencao: VersaoProtIntervencaoDto[]
  protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
    this.protocolosIntervencao = [];
    this.aplicacoes = [];
    if (this._aluno == undefined || this._aluno?.casoId == undefined) {
      finishedCallback();
      this.showPaging({ 'items': [], 'totalCount': 0 }, pageNumber);
      return;
    }
    this._protocoloIntervencaoService
      .getAllVersoes(undefined, this._aluno.casoId, undefined, undefined)
      .subscribe((result: VersaoProtIntervencaoDtoPagedResultDto) => {
        this.protocolosIntervencao = result.items;
      });
    this._casoService
      .getAllAplicacoesProtocoloIntervencao(this.aluno.casoId, undefined)
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: AplicacaoProtocoloIntervencaoDto[]) => {
        this.aplicacoes = result;
        this.showPaging({ 'items': result, 'totalCount': result.length }, pageNumber);
      });
  }

  getNomeProtocoloIntervencao(id: number): string {
    return this.protocolosIntervencao?.find(p => p.id == id)?.protocoloIntervencaoNome??id.toString();
  }
  getNomeVersaoProtocoloIntervencao(id: number): string {
    return this.protocolosIntervencao?.find(p => p.id == id)?.nome??id.toString();
  }
  protected delete(proc: AplicacaoProtocoloIntervencaoDto): void {
    abp.message.confirm(
      "Arquivar o protocolo \""+this.getNomeProtocoloIntervencao(proc.modeloId) +"("+ this.getNomeVersaoProtocoloIntervencao(proc.modeloId) +")"+"\" no caso do aluno "+this.aluno.nome+"?",
      undefined,
      (result: boolean) => {
        if (result) {
          let updateDto = new Int64EntityDto({ 'id': proc.id });
          this._casoService.removeAplicacaoProtocoloIntervencao(updateDto).subscribe((result: AplicacaoProtocoloIntervencaoDto) => {
            this.aplicacoes[this.aplicacoes.findIndex(p => p.id == result.id)] = result;
          });
        }
      }
    );
  }

  addProtocoloIntervencao() {
    let selectProtocoloIntervencaoDialog: BsModalRef;
    selectProtocoloIntervencaoDialog = this._modalService.show(
      SelectProtocoloIntervencaoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          aluno: this.aluno
        }
      }
    );

    selectProtocoloIntervencaoDialog.content.onSave.subscribe((result: AplicacaoProtocoloIntervencaoDto) => {
      this.refresh();
    });
  }

  detailsAplicacaoProtocoloIntervencao(pId: number) {
    this._router.navigate(['app', 'alunos', 'details',this.aluno.id,'protocolo-intervencao', pId]);
  }
}
