import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  ColaboradorServiceProxy,
  CreateColaboradorDto,
  CreateAlunoDto,
  CreateResponsavelDto,
  FuncaoOrgServiceProxy,
  FuncaoOrgDto,
  FuncaoOrgDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { CreatePessoaDialogBaseComponent } from '@app/pessoas/create-pessoa-dialog-base/create-pessoa-dialog-base.component';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './create-colaborador-dialog.component.html'
})
export class CreateColaboradorDialogComponent extends CreatePessoaDialogBaseComponent implements OnInit {
  constructor(
    injector: Injector,
    public _colaboradorService: ColaboradorServiceProxy,
    public _funcaoOrgService: FuncaoOrgServiceProxy,
    public bsModalRef: BsModalRef
  ) {
    super(injector, bsModalRef);
  }

  funcoes: FuncaoOrgDto[];
  public override ngOnInit(): void {
    super.ngOnInit();
    this.funcoes = [];
    this._funcaoOrgService.getAll(undefined, undefined, undefined).subscribe((funcs: FuncaoOrgDtoPagedResultDto) => {
      this.funcoes = funcs.items;
    });
  }

  pessoa: CreateColaboradorDto;
  protected override initEmptyPessoaDto(): void {
    this.pessoa = new CreateColaboradorDto();
    super.initEmptyPessoaDto();
    this.pessoa.funcoes = [];
  }

  protected create(): Observable<any> {
    return this._colaboradorService.create(this.pessoa)
  }

  public funcaoChange(id: number, checked: boolean) {
    if (checked && !this.pessoa.funcoes.includes(id)) {
      this.pessoa.funcoes.push(id);
    } else if (!checked && this.pessoa.funcoes.includes(id)) {
      this.pessoa.funcoes.splice(this.pessoa.funcoes.indexOf(id), 1);
    }
  }
}
