import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output,
    Input
  } from '@angular/core';
  import { BsModalRef } from 'ngx-bootstrap/modal';
  import { forEach as _forEach, map as _map } from 'lodash-es';
  import { AppComponentBase } from '@shared/app-component-base';
  import { 
    AlunoDto,
    AplicacaoProtocoloIntervencaoDto,
    AlunoServiceProxy,
    RegistroAtendimentoDto,
    Int64Int32KeyValuePair,
    TipoAjudaDto,
    PassoProtIntervencaoDto,
    ProtocoloIntervencaoServiceProxy,
    CasoServiceProxy,
    VersaoProtIntervencaoDto,
    HierarquiaAjudaDto,
    HierarquiaAjudaServiceProxy,
    UnidadeEnsinoDto,
    ProtocoloIntervencaoDto,
    VersaoProtIntervencaoDtoPagedResultDto
  } from '@shared/service-proxies/service-proxies';
  import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
  import { types } from 'util';
  import { isMoment, isDate } from 'moment';
  import * as moment from 'moment';
  import { Observable, Observer } from 'rxjs';
  
  @Component({
    templateUrl: './unidade-intervencao-dialog.component.html'
  })
  export class UnidadeIntervencaoDialogComponent extends AppComponentBase implements OnInit {
    public readonly NOME_SERVICO_ATENDIMENTO = "Atendimento";
    loading: boolean = false;
    saving: boolean = false;
    get disabled(): boolean { return this.loading || this.saving }
  
    aluno: AlunoDto;
    unidade: UnidadeEnsinoDto = new UnidadeEnsinoDto();
    constructor(injector: Injector,
      private _casoService: CasoServiceProxy,
      private _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
      private _hierarquiaAjudaService: HierarquiaAjudaServiceProxy,
      public bsModalRef: BsModalRef) {
      super(injector);
    }

    protocolosIntervencao: VersaoProtIntervencaoDto[];
    aplicacoes: AplicacaoProtocoloIntervencaoDto[];
    hierarquiasAjuda: HierarquiaAjudaDto[];
    ngOnInit() {
        if (this.protocolosIntervencao == undefined) {
            this.protocolosIntervencao = [];
            this._protocoloIntervencaoService
            .getAllVersoes(undefined, this.aluno.casoId, undefined, undefined)
            .subscribe((result: VersaoProtIntervencaoDtoPagedResultDto) => {
                this.protocolosIntervencao = result.items;
                if (this.hierarquiasAjuda == undefined) {
                    this.hierarquiasAjuda = [];
                    this.protocolosIntervencao.map(p => p.hierarquiaAjudaId).forEach((hId) => {
                    if (this.hierarquiasAjuda.every(h => h.id != hId))
                        this._hierarquiaAjudaService.get(hId).subscribe(this.hierarquiasAjuda.push);
                    });
                }
            });
        } else if (this.hierarquiasAjuda == undefined) {
            this.hierarquiasAjuda = [];
            this.protocolosIntervencao.map(p => p.hierarquiaAjudaId).forEach((hId) => {
            if (this.hierarquiasAjuda.every(h => h.id != hId))
                this._hierarquiaAjudaService.get(hId).subscribe(this.hierarquiasAjuda.push);
            });
        }
        if (this.aplicacoes == undefined) {
            this.aplicacoes = [];
            this._casoService
            .getAllAplicacoesProtocoloIntervencao(this.aluno.casoId, undefined)
            .subscribe((result: AplicacaoProtocoloIntervencaoDto[]) => {
                this.aplicacoes = result;
            });
        }
    }

    private _protocoloIdx: number;
    public get protocoloIdx(): string  { return this._protocoloIdx?.toString()??"" }
    public set protocoloIdx(v: string) { 
        if (v != this.protocoloIdx) {
            this.passoIdx = "";
            this.unidade.ajudas = undefined;
        }
        this._protocoloIdx = (v??"")!=""?Number(v):undefined; 
        this.protocolosIntervencao[this._protocoloIdx].passosProtIntervencao.sort((a, b) => a.ordenador - b.ordenador); 
        if (this.unidade.ajudas == undefined) {
            this.unidade.ajudas = [];
            this.tiposAjuda.forEach(t => this.unidade.ajudas.push(new Int64Int32KeyValuePair({key: t.id, value:0})));
        }
    }
    
    public get passoOptions(): PassoProtIntervencaoDto[] { return this.protocolosIntervencao[this._protocoloIdx]?.passosProtIntervencao }

    private _passoIdx: number;
    public get passoIdx(): string  { return this._passoIdx?.toString()??"" }
    public set passoIdx(v: string) { 
        this._passoIdx = (v??"")!=""?Number(v):undefined; 
        this.unidade.passoId = (this.passoOptions??[])[this._passoIdx]?.id;
    }

    public get tiposAjuda(): TipoAjudaDto[] {
        let hId = this.protocolosIntervencao[this._protocoloIdx]?.hierarquiaAjudaId;
        if (hId == undefined) return undefined;
        return this.hierarquiasAjuda.find(h => h.id == hId).tiposAjuda;
    }

    @Output() 
    onSave = new EventEmitter<UnidadeEnsinoDto>();
    save() {
        this.onSave.emit(this.unidade);
        this.bsModalRef.hide();
    }
  }
  