import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input,
  ChangeDetectorRef
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { CreatePassoProtIntervencaoDto, HabilidadeDto } from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import { Marco } from '../passo-prot-intervencao-form/passo-prot-intervencao-form.component';

@Component({
  templateUrl: './create-passo-prot-intervencao-dialog.component.html'
})
export class CreatePassoProtIntervencaoDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean = false;
  private _passo: CreatePassoProtIntervencaoDto = new CreatePassoProtIntervencaoDto();
  
  public set passo(v: CreatePassoProtIntervencaoDto) { this._passo = v }
  public get passo(): CreatePassoProtIntervencaoDto  { return this._passo }
  
  constructor(injector: Injector,
    private _changeDetector: ChangeDetectorRef,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit() { this.passo = new CreatePassoProtIntervencaoDto() }
  
  @Output()
  public onSubmit: EventEmitter<CreatePassoProtIntervencaoDto> = new EventEmitter<CreatePassoProtIntervencaoDto>();
  save() {
    this.saving = true;
    this.onSubmit.emit(this.passo);
    this.bsModalRef.hide();
    this.saving = false;
  }
  @Input()
  habilidades: HabilidadeDto[];

  private _objetivosAlvoId: number[];
  public   objetivosAlvo:   HabilidadeDto[];
  @Input()
  set objetivosAlvoId(v: number[]) { 
    this._objetivosAlvoId = v;
    this.objetivosAlvo = [];
    this._objetivosAlvoId.forEach((id) => {
      this.objetivosAlvo.push(this.habilidades.find(h => h.id == id));
    })
    this._changeDetector.detectChanges();
  }
  get objetivosAlvoId(): number[]  { return this._objetivosAlvoId }
  
  public get marcoHabilidadeLinhaDeBaseId(): string  { return this.passo.marcoHabilidadeLinhaDeBaseId?.toString()??"" }
  public set marcoHabilidadeLinhaDeBaseId(v: string) { this.passo.marcoHabilidadeLinhaDeBaseId = (v != "" ? Number(v) : undefined) }
  public get marcoHabilidadeIntroducaoId(): string  { return this.passo.marcoHabilidadeIntroducaoId?.toString()??"" }
  public set marcoHabilidadeIntroducaoId(v: string) { this.passo.marcoHabilidadeIntroducaoId = (v != "" ? Number(v) : undefined) }
  public get marcoHabilidadeCriterioAlcancadoId(): string  { return this.passo.marcoHabilidadeCriterioAlcancadoId?.toString()??"" }
  public set marcoHabilidadeCriterioAlcancadoId(v: string) { this.passo.marcoHabilidadeCriterioAlcancadoId = (v != "" ? Number(v) : undefined) }
}
