import { Component, Injector } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  FormatoEnsinoServiceProxy,
  FormatoEnsinoDto,
  FormatoEnsinoDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
import { CreateFormatoEnsinoDialogComponent } from './create-formato-ensino-dialog/create-formato-ensino-dialog.component';
// import { EditFormatoEnsinoDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';


class PagedFormatoEnsinosRequestDto extends PagedRequestDto {
  keyword: string;
}

@Component({
  templateUrl: './formatos-ensino.component.html',
  animations: [appModuleAnimation()]
})
export class FormatosEnsinoComponent extends PagedListingComponentBase<FormatoEnsinoDto> {
  formatosEnsino: FormatoEnsinoDto[] = [];
  maxDescLength: number = 250;
  keyword = '';
  isActive: boolean | null;
  advancedFiltersVisible = false;

  constructor(
    injector: Injector,
    private _formatoEnsinoService: FormatoEnsinoServiceProxy,
    private _modalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }
  
  createFormatoEnsino(): void {
    let createFormatoEnsinoDialog: BsModalRef;
    createFormatoEnsinoDialog = this._modalService.show(
      CreateFormatoEnsinoDialogComponent,
      {
        class: 'modal-lg',
      }
    );

    createFormatoEnsinoDialog.content.onSave.subscribe((result) => {
      this.refresh();
      this._router.navigate(['details', result.id], { relativeTo : this._route });
    });
  }

  detailsFormatoEnsino(formatoEnsino: FormatoEnsinoDto): void {
    this._router.navigate(['details', formatoEnsino.id], { relativeTo : this._route });
  }

  clearFilters(): void {
    this.keyword = '';
    this.getDataPage(1);
  }

  protected list(
    request: PagedFormatoEnsinosRequestDto,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    request.keyword = this.keyword;

    this._formatoEnsinoService
      .getAll(
        request.skipCount,
        request.maxResultCount
      )
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: FormatoEnsinoDtoPagedResultDto) => {
        this.formatosEnsino = result.items;
        this.showPaging(result, pageNumber);
      });
  }

  protected delete(pesssoa: FormatoEnsinoDto): void {
    abp.message.confirm(
      "O seguinte formatoensino ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._formatoEnsinoService.delete(pesssoa.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
