import {
    Component,
    Injector,
    OnInit,
    Input,
    EventEmitter,
    Output
} from '@angular/core';
import { ControlContainer, NgModelGroup} from '@angular/forms'
import { AppComponentBase } from '@shared/app-component-base';
import { CreateHabilidadeDto, DominioDto, HabilidadeDto, ProtocoloAvaliacaoDto, ProtocoloAvaliacaoServiceProxy, TipoConfirmacaoHabilidadeDto } from '@shared/service-proxies/service-proxies';

@Component({
    selector: 'habilidade-form',
    templateUrl: './habilidade-form.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class HabilidadeFormComponent extends AppComponentBase implements OnInit {
    @Input() disabled: boolean = false;
    
    private _habilidade: CreateHabilidadeDto | HabilidadeDto;
    @Input() 
    set habilidade(v: CreateHabilidadeDto | HabilidadeDto) {
        this._habilidade = v;
        if (this._habilidade != undefined) {
            if (this._habilidade.confirmacaoExplicitaRequeridaId == undefined)
                this._habilidade.confirmacaoExplicitaRequeridaId = this.tiposConfirmacao[0].id;
        }
    }
    get habilidade(): CreateHabilidadeDto | HabilidadeDto  {
        return this._habilidade;
    }
    
    @Input() tiposConfirmacao: TipoConfirmacaoHabilidadeDto[] = [];

    public constructor(
        injector: Injector
    ) {
        super(injector);
    }
    public ngOnInit(): void {
        if (this._habilidade instanceof CreateHabilidadeDto)
            this._habilidade.confirmacaoExplicitaRequeridaId = this.tiposConfirmacao[0].id;
    }
    get isNova() { return (this.habilidade instanceof CreateHabilidadeDto || this.habilidade instanceof CreateHabilidadeDto) }

    get isCodificada() { return !this.habilidade.isPersonalizada }
    get codigo(): string {
        return this.habilidade?.codigo; 
    }
    set codigo(v: string) {
        this.habilidade.codigo = v; 
    }

    get tipoConfirmacao(): string  { return (this.habilidade?.confirmacaoExplicitaRequeridaId?.toString())??this.tiposConfirmacao[0].id.toString(); }
    set tipoConfirmacao(v: string) { this.habilidade.confirmacaoExplicitaRequeridaId = Number(v); }
}