import { AppComponentBase } from '@shared/app-component-base';
import { ChangeDetectorRef, Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ResponsavelServiceProxy,
  ResponsavelDto,
  ResponsavelDtoPagedResultDto,
  AlunoServiceProxy,
  AlunoDto,
  Int64EntityDto
} from '@shared/service-proxies/service-proxies';
import { PessoaDetailsBaseComponent } from '@app/pessoas/pessoa-details-base/pessoa-details-base.component';
import { Observable } from 'rxjs';
import * as moment from 'moment';
import { BsModalService } from 'ngx-bootstrap/modal';
import { CreateAlunoDialogComponent } from '@app/alunos/create-aluno-dialog/create-aluno-dialog.component';
import { SelectAlunoDialogComponent } from '@app/alunos/select-aluno-dialog/select-aluno-dialog.component';
import { ControlContainer, NgModelGroup } from '@node_modules/@angular/forms';

@Component({
  selector: 'dependentes-details',
  templateUrl: './dependentes-details.component.html',
  viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup }]
})
export class DependentesDetailsComponent extends AppComponentBase implements OnInit {
  private _responsavel: ResponsavelDto;
  @Input()
  public set responsavel(v: ResponsavelDto) { this._responsavel = v; this.refresh() }
  public get responsavel(): ResponsavelDto  { return this._responsavel } 
  
  @Output() 
  public responsavelChange: EventEmitter<ResponsavelDto> = new EventEmitter<ResponsavelDto>();
  
  public dependentes: AlunoDto[];
  
  constructor(injector: Injector,
    protected _responsavelService: ResponsavelServiceProxy,
    protected _alunoService: AlunoServiceProxy,
    protected _modalService: BsModalService,
    protected _changeDetector: ChangeDetectorRef,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  ngOnInit() {
    this.refresh()
  }
  refresh() {
    this.dependentes = []
    // console.log(this.responsavel)
    this.responsavel?.dependentes?.forEach((aId) => {
      this._alunoService.get(aId.id).subscribe((result) => {
        this.dependentes.push(result);
        // this._changeDetector.detectChanges();
      });
    });
  }

  getDependenteNome(dependente: Int64EntityDto): string {
    return this.dependentes?.find((d) => d.id == dependente.id)?.nome??"";
  }
  detailsDependente(dependente: Int64EntityDto): void {
    if (this.appSession.user.aluno?.id == dependente.id)
      this._router.navigate(['app', 'personal']);
    else
      this._router.navigate(['app', 'alunos', 'details', dependente.id]);
  }

  cadastrarDependente(): void {
    let createDependenteDialog = this._modalService.show(
      CreateAlunoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          temResponsavelPredefinido:true,
          responsavel:this.responsavel
        },
      }
    );
    createDependenteDialog.content.onSave.subscribe((novo: AlunoDto) => {
      if (novo != undefined) {
        this.responsavel.dependentes.push(new Int64EntityDto({id: novo.id}));
        this.dependentes.push(novo);
      }
    });
  }
  addDependente(): void {
    let selectDependenteDialog = this._modalService.show(
      SelectAlunoDialogComponent,
      {
        class: 'modal-lg'
      }
    );
    selectDependenteDialog.content.onSubmit.subscribe((result) => {
      this.responsavel.dependentes.push(new Int64EntityDto({ 'id': result.id }));
      this._responsavelService.update(this.responsavel).subscribe((responsavel) => {
        this.responsavel = responsavel;
        this.responsavelChange.emit(responsavel);
        this.dependentes.push(result);
      });
    });
  }
  podeAddDependentes() {
    return this.permission.isGranted("Business.Admin.People")
  }
}
