import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  HierarquiaAjudaServiceProxy,
  HierarquiaAjudaDto,
  TipoAjudaDto,
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './hierarquia-ajuda-details.component.html',
  animations: [appModuleAnimation()]
})
export class HierarquiaAjudaDetailsComponent extends AppComponentBase implements OnInit {
  public saving: boolean = false;
  public hierarquiaAjuda: HierarquiaAjudaDto;
  
  constructor(injector: Injector,
    protected _hierarquiaAjudaService: HierarquiaAjudaServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }


  private id: number;
  ngOnInit() { 
    const params = this._route.snapshot.paramMap;
    this.id = Number(params.get("hierarquiaAjudaId"));
    this.refresh();
  }

  refresh() {
    this._hierarquiaAjudaService.get(this.id).subscribe((r: HierarquiaAjudaDto) => { 
      this.hierarquiaAjuda = r;
    });
  }

  public onSave: EventEmitter<HierarquiaAjudaDto> = new EventEmitter<HierarquiaAjudaDto>();
  save() {
    // this.saving = true;

    // this._hierarquiaAjudaService.details(this.hierarquiaAjuda).subscribe(
    //     (result) => {
    //       this.saving = false;
    //       this.notify.info(this.l('SavedSuccessfully'));
    //       this.onSave.emit(result);
    //       this._router.navigate(["..", "details", result.id], {relativeTo:this._route})
    //     }
    // );
  }
  
  goBack() {
    this._router.navigate(["../.."], {relativeTo:this._route})
  }
  addTipoAjuda() {
    this.hierarquiaAjuda.tiposAjuda.push(new TipoAjudaDto());
  }
  rmvTipoAjuda(i: number) {
    this.hierarquiaAjuda.tiposAjuda.splice(i, 1);
  }
}
