import {
    Component,
    Injector,
    OnInit,
    Input,
    EventEmitter,
    Output,
    ChangeDetectorRef
} from '@angular/core';
import { ControlContainer, NgModelGroup} from '@angular/forms'
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    FormatoEnsinoServiceProxy,
    HierarquiaAjudaDto,
    TipoAjudaDto
} from '@shared/service-proxies/service-proxies';
import { CustomFormBaseComponent } from '@app/bases/custom-form-base.component';

@Component({
    selector: 'hierarquia-ajuda-form',
    templateUrl: './hierarquia-ajuda-form.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class HierarquiaAjudaFormComponent extends CustomFormBaseComponent implements OnInit {
    @Input() hierarquiaAjuda : HierarquiaAjudaDto;

    constructor(injector: Injector) {
        super(injector);
    }

    ngOnInit(): void {

    }
    addTipoAjuda() {
        this.hierarquiaAjuda.tiposAjuda.push(new TipoAjudaDto());
    }
    rmvTipoAjuda(i: number) {
        this.hierarquiaAjuda.tiposAjuda.splice(i, 1);
    }
}
  