import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  AlunoDto,
  AlunoServiceProxy,
  CreateAlunoDto,
  CreateResponsavelDto,
  Int64EntityDto,
  ResponsavelDto,
  ResponsavelServiceProxy
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { CreatePessoaDialogBaseComponent } from '@app/pessoas/create-pessoa-dialog-base/create-pessoa-dialog-base.component';
import { Observable, Subject } from 'rxjs';

@Component({
  templateUrl: './create-aluno-dialog.component.html'
})
export class CreateAlunoDialogComponent extends CreatePessoaDialogBaseComponent implements OnInit {
  private _temResponsavelPredefinido: boolean = false;
  public get temResponsavelPredefinido(): boolean {
    return this._temResponsavelPredefinido;
  }
  public set temResponsavelPredefinido(value: boolean) {
    this._temResponsavelPredefinido = value;
    if (value)
      this.semResponsavel = false;
  }
  semResponsavel = true;
  responsavel: CreateResponsavelDto | ResponsavelDto;
  private _semContato: boolean = false;
  get semContato(): boolean  { return this._semContato }
  set semContato(v: boolean) { 
    this._semContato = v;
    if (v) {
      this.pessoa.infoContato_Email = undefined;
      this.pessoa.infoContato_Telefone1 = undefined;
      this.pessoa.infoContato_Telefone2 = undefined;
    }
  }
  private _semEndereco: boolean = false;
  get semEndereco(): boolean  { return this._semEndereco }
  set semEndereco(v: boolean) { 
    this._semEndereco = v;
    if (v) {
      this.semResponsavel = false;
      this.pessoa.endereco_Linha1 = this.responsavel.endereco_Linha1
      this.pessoa.endereco_Linha2 = this.responsavel.endereco_Linha2
      this.pessoa.endereco_Bairro = this.responsavel.endereco_Bairro
      this.pessoa.endereco_CidadeId = this.responsavel.endereco_CidadeId
      this.pessoa.endereco_CodigoPostal = this.responsavel.endereco_CodigoPostal
    }
  }
  
  constructor(injector: Injector,
    public _alunoService: AlunoServiceProxy,
    public _responsavelService: ResponsavelServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector, bsModalRef);
  }

  ngOnInit(): void {
    super.ngOnInit();
    if (!this.temResponsavelPredefinido) {
      this.responsavel = new CreateResponsavelDto();
    }
  }
  pessoa : CreateAlunoDto = new CreateAlunoDto();
  protected override initEmptyPessoaDto(): void {
    this.pessoa = new CreateAlunoDto();
    super.initEmptyPessoaDto();
    this.pessoa.responsaveis = [];
  }
  protected _format(): void {
      
  }
  protected create(): Observable<any> {
    if (this.temResponsavelPredefinido && this.responsavel instanceof ResponsavelDto) {
      this.pessoa.responsaveis = [ new Int64EntityDto({ "id" : this.responsavel.id }) ];
    } else if (!this.semResponsavel) {
      if (this.responsavel instanceof CreateResponsavelDto) {        
        let s: Subject<AlunoDto> = new Subject<AlunoDto>();
        this._responsavelService.create(this.responsavel).subscribe((r) => {
          this.pessoa.responsaveis = [new Int64EntityDto({"id": r.id})];
          this._alunoService.create(this.pessoa).subscribe(r => s.next(r));
        })
        return s;
      } else if (this.responsavel instanceof ResponsavelDto) {
        this.pessoa.responsaveis = [new Int64EntityDto({"id": this.responsavel.id})];
        return this._alunoService.create(this.pessoa);
      }
    } else {
      return this._alunoService.create(this.pessoa);
    }
  }
}
