import { Component, Injector } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  HierarquiaAjudaServiceProxy,
  HierarquiaAjudaDto,
  HierarquiaAjudaDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
// import { CreateHierarquiaAjudaDialogComponent } from './create-hierarquia-ajuda-dialog/create-hierarquia-ajuda-dialog.component';
// import { EditHierarquiaAjudaDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';


class PagedHierarquiaAjudasRequestDto extends PagedRequestDto {
  keyword: string;
}

@Component({
  templateUrl: './hierarquias-ajuda.component.html',
  animations: [appModuleAnimation()]
})
export class HierarquiasAjudaComponent extends PagedListingComponentBase<HierarquiaAjudaDto> {
  hierarquiasAjuda: HierarquiaAjudaDto[] = [];
  maxDescLength: number = 250;
  keyword = '';
  isActive: boolean | null;
  advancedFiltersVisible = false;

  constructor(
    injector: Injector,
    private _hierarquiaAjudaService: HierarquiaAjudaServiceProxy,
    private _modalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }
  
  createHierarquiaAjuda(): void {
    this._router.navigate(['create'], { relativeTo : this._route });
  }

  detailsHierarquiaAjuda(hierarquiaAjuda: HierarquiaAjudaDto): void {
    this._router.navigate(['details', hierarquiaAjuda.id], { relativeTo : this._route });
  }

  clearFilters(): void {
    this.keyword = '';
    this.getDataPage(1);
  }

  protected list(
    request: PagedHierarquiaAjudasRequestDto,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    request.keyword = this.keyword;

    this._hierarquiaAjudaService
      .getAll(
        request.skipCount,
        request.maxResultCount
      )
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: HierarquiaAjudaDtoPagedResultDto) => {
        this.hierarquiasAjuda = result.items;
        this.showPaging(result, pageNumber);
      });
  }

  protected delete(pesssoa: HierarquiaAjudaDto): void {
    abp.message.confirm(
      "O seguinte hierarquiaajuda ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._hierarquiaAjudaService.delete(pesssoa.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
