import {
    Component,
    Injector,
    OnInit,
    Input,
    EventEmitter,
    Output,
    ViewChild
  } from '@angular/core';
  import { ControlContainer, NG_VALUE_ACCESSOR, NgModel, NgModelGroup} from '@angular/forms'
  import { BsModalRef } from 'ngx-bootstrap/modal';
  import { forEach as _forEach, map as _map } from 'lodash-es';
  import { AppComponentBase } from '@shared/app-component-base';
  import {
    ColaboradorServiceProxy,
    ColabFuncaoOrgDto,
    FuncaoOrgDto,
    ColaboradorDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { finalize } from 'rxjs/operators';
import { EntityDto, PagedListingComponentBase } from '@shared/paged-listing-component-base';
import { ActivatedRoute } from '@angular/router';

@Component({
    selector: 'historico-funcoes-table',
    templateUrl: './historico-funcoes-table.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class HistoricoFuncaoTableComponent extends PagedListingComponentBase<ColabFuncaoOrgDto> implements OnInit {
    private _historicoFuncoes: ColabFuncaoOrgDto[];
    @Input() 
    set historicoFuncoes(v: ColabFuncaoOrgDto[]) { this._historicoFuncoes = v; this.refresh(); }
    get historicoFuncoes(): ColabFuncaoOrgDto[]  { return this._historicoFuncoes }
    @Input()  
    public funcoes: FuncaoOrgDto[];

    funcoesHistoricas : ColabFuncaoOrgDto[] = [];

    @Input()  
    gerentesCF: ColabFuncaoOrgDto[];
    @Input()  
    gerentes: ColaboradorDto[];
    getFuncao(id: number): FuncaoOrgDto { return this.funcoes?.find(f => f.id == id) }

    getGestorString(cf: ColabFuncaoOrgDto): string {
        if (cf == undefined) return "...";
        var f = this.getFuncao(cf.funcaoOrgId);
        if (f == undefined) return "...";
        if (f.funcaoGestoraDiretaId == undefined) return "N/A";
        if (cf.gestorId == undefined) return "No atribudo"
        var gcf = this.gerentesCF?.find(gcf => gcf.id == cf?.gestorId);
        var gc = this.gerentes?.find(gc => gc.id == gcf?.colaboradorId);
        if (gc == undefined) return "..."
        return gc.nome;
    }

    override ngOnInit(): void {
        super.ngOnInit();
        this.refresh();
    }

    protected list(
        request,
        pageNumber: number,
        finishedCallback: Function) : void 
    {
        this.funcoesHistoricas = [];
        this.historicoFuncoes.forEach((cf) => { if (cf.dataFim != undefined) this.funcoesHistoricas.push(cf) });
        this.showPaging({"items" : this.funcoesHistoricas, "totalCount" : this.funcoesHistoricas.length}, pageNumber);
        finishedCallback();
    }

    delete() { }
}
