import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    RefBibliograficaServiceProxy,
    RefBibliograficaDto
} from '@shared/service-proxies/service-proxies';

@Component({
    templateUrl: './edit-ref-bibliografica-dialog.component.html'
})
export class EditRefBibliograficaDialogComponent extends AppComponentBase implements OnInit {
    saving = false;
    refBibliografica: RefBibliograficaDto;

    @Output() onSave = new EventEmitter<any>();

    constructor(injector: Injector,
        public _refbibliograficaService: RefBibliograficaServiceProxy,
        public bsModalRef: BsModalRef) {
        super(injector);
    }

    ngOnInit(): void {
    }

    save(): void {
        this.saving = true;

        this._refbibliograficaService.update(this.refBibliografica).subscribe(
            () => {
                this.saving = false;
                this.notify.info(this.l('SavedSuccessfully'));
                this.bsModalRef.hide();
                this.onSave.emit();
            }
        );
    }
}
  