@Component({
  selector: 'atendimentos-caso',
  templateUrl: './atendimentos-caso.component.html',
  animations: [appModuleAnimation()]
})
export class AtendimentosCasoComponent extends PagedListingComponentBase<RegistroAtendimentoDto> implements OnInit {
  public atendimentos: RegistroAtendimentoDto[]; 

  private _aluno: AlunoDto;
  @Input() set aluno (v: AlunoDto) { 
    this._aluno = v; 
    this._localService.aluno = v;
    this.refresh();
  }
  get aluno(): AlunoDto { return this._aluno; }
  
  @Output() refreshCallback = new EventEmitter<undefined>();

  constructor(injector: Injector,
    protected _alunoService: AlunoServiceProxy,
    protected _casoService: CasoServiceProxy,
    protected _colaboradorService: ColaboradorServiceProxy,
    protected _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    protected _hierarquiaAjudaService: HierarquiaAjudaServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute,
    protected _changeDetector: ChangeDetectorRef,
    protected _localService: LocalService) {
    super(injector);
  }
  
  @Input()
  public colaboradores: ColaboradorDto[];
  @Input()
  public funcoesColaborador: ColabFuncaoOrgDto[];
  @Input()
  staff: AtuacaoDto[];
  ngOnInit(): void { 
    this.refresh();
  }

  getAtuacao(id) { return this.staff?.find(a => a.id == id) }
  getColabFuncao(id) { return this.funcoesColaborador?.find(cf => cf.id == id) }
  getColaborador(id) { return this.colaboradores?.find(c => c.id == id) }
  protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
    if (this.aluno?.casoId != undefined) {
      this._casoService
        .getAllRegistrosAtendimento(this.aluno.casoId, undefined, undefined, undefined, undefined)
        .pipe(finalize(() => finishedCallback()))
        .subscribe((result: RegistroAtendimentoDto[]) => {
          this.atendimentos = result;
          this.showPaging({ 'items': result, 'totalCount': result.length }, pageNumber);
        });
    } else {
      this.atendimentos = [];
      this.showPaging({ 'items': [], 'totalCount': 0 }, pageNumber);
    }
  }

  protected delete(proc: RegistroAtendimentoDto): void {
    // abp.message.confirm(
    //   "Arquivar o protocolo \""+proc.modelo.protocoloIntervencao.nome+"\" no caso do aluno "+this.aluno.nome+"?",
    //   undefined,
    //   (result: boolean) => {
    //     if (result) {
    //       let updateDto = new UpdateAlunoDto(this.aluno as any);
    //       updateDto.caso = new UpdateCasoDto();
    //       updateDto.caso.arqProtocolosIntervencao = [new Int64EntityDto({ 'id': proc.id })]
    //       updateDto.responsaveis = this.aluno.responsaveis;
    //       this._alunoService.update(updateDto).subscribe((result: AlunoDto) => {
    //         this.aluno = result;
    //         this.aplicacaoChange.emit(result);
    //       });
    //     }
    //   }
    // );
  }

  registrarAtendimento() {
    this._router.navigate(['registro-atendimento'], { relativeTo: this._route })
  }
  getNomeAT(id: number): string { 
    return this.getColaborador(this.getColabFuncao(this.getAtuacao(id)?.colabFuncaoId)?.colaboradorId)?.nome??"";
  }
  podeRegistrarAtendimento(): boolean{
    return (this.permission.isGranted('Business.Personal.Colaborador.Supervisor')||this.permission.isGranted('Business.Personal.Colaborador.AT')) &&
      (this.permission.isGranted('Business.Admin.People') ||
      this.staff?.some((a) => this.getColabFuncao(this.getAtuacao(a.id)?.colabFuncaoId)?.colaboradorId == this.appSession.user.colaborador?.id && this.appSession.user.colaborador?.id != undefined && a.dataFim == undefined));
  }
}
