import { Component, EventEmitter, Injector, OnInit } from "@angular/core";
import { appModuleAnimation } from "@shared/animations/routerTransition";
import { AppComponentBase } from "@shared/app-component-base";
import { 
    ColaboradorServiceProxy, 
    ServicoPrestadoServiceProxy, 
    TipoServicoPrestadoServiceProxy,
    CreateServicoPrestadoDto,
    TipoServicoPrestadoDto,
    AtuacaoDto,
    ColaboradorDto,
    ColabFuncaoOrgDto,
    ServicoPrestadoDto,
    FuncaoOrgDto,
    FuncaoOrgServiceProxy
} from "@shared/service-proxies/service-proxies";
import * as moment from "moment";
import { BsModalRef } from "ngx-bootstrap/modal";

@Component({
    templateUrl: './create-servico-prestado-dialog.component.html',
    animations: [appModuleAnimation()]
})
export class CreateServicoPrestadoDialogComponent extends AppComponentBase implements OnInit {
    constructor(injector: Injector,
        private _servicoPrestadoService: ServicoPrestadoServiceProxy,
        private _tipoServicoPrestadoService: TipoServicoPrestadoServiceProxy,
        private _colaboradorService: ColaboradorServiceProxy,
        private _funcaoService: FuncaoOrgServiceProxy,
        public bsModalRef: BsModalRef
    ) {
        super(injector);
    }

    servico: CreateServicoPrestadoDto;
    get colabFuncaoOrgId(): string  { return this.servico?.colabFuncaoOrgId?.toString()??"" }
    set colabFuncaoOrgId(v: string) { 
        let nv = Number(v);
        this.servico.colabFuncaoOrgId = nv;
    }
    get tipoServicoId(): string  { return this.servico?.tipoServicoId?.toString()??"" }
    set tipoServicoId(v: string) { 
        let nv = Number(v);
        this.servico.tipoServicoId = nv;
    }
    get dataInicio(): string  { return this.servico?.inicio?.format("YYYY-MM-DD") }
    set dataInicio(v: string) { 
      let ini = moment(v); 
      ini = ini.hour  (this.servico?.inicio?.hour()  ??0); 
      ini = ini.minute(this.servico?.inicio?.minute()??0); 
      this.servico.inicio = ini; 
    }
    get horarioInicio(): string  { return this.servico?.inicio?.format("HH:mm") }
    set horarioInicio(v: string) {
      let hora   = Number(v.substring(0, 2)); 
      let minuto = Number(v.substring(3, 5)); 
      this.servico.inicio = this.servico?.inicio?.hour  (hora); 
      this.servico.inicio = this.servico?.inicio?.minute(minuto); 
    }
    horasTrabalho: number = 0;
    
    loadingFuncoes: boolean;
    funcoes: FuncaoOrgDto[];
    
    loadingColaboradores: boolean;
    colaboradores: ColaboradorDto[]; 
    
    loadingFuncoesColaborador: boolean;
    funcoesColaborador: ColabFuncaoOrgDto[];
    
    loadingTiposServico: boolean;
    tiposServico: TipoServicoPrestadoDto[]
    
    get loading(): boolean { return this.loadingFuncoes || this.loadingColaboradores || this.loadingFuncoesColaborador || this.loadingTiposServico }
    
    ngOnInit(): void {
        this.servico = new CreateServicoPrestadoDto();
        this.servico.inicio = moment();
        this.servico.fim = moment();
        
        if (this.funcoes == undefined) {
            this.loadingFuncoes = true;
            this._funcaoService.getAll(undefined, undefined, undefined)
            .subscribe((result) => { 
                this.funcoes = result.items;
                this.loadingFuncoes = false;
            })
        }
        if (this.colaboradores == undefined) {
            this.loadingColaboradores = true;
            this._colaboradorService.getAll(undefined, undefined, undefined, true, undefined, undefined, undefined, undefined)
            .subscribe((result) => { 
                this.colaboradores = result.items;
                this.loadingColaboradores = false;
            })
        }
        if (this.funcoesColaborador == undefined) {
            this.loadingFuncoesColaborador = true;
            this._colaboradorService.getAllColabFuncoes(undefined, undefined, undefined, undefined, true, undefined, undefined, undefined, undefined)
            .subscribe((result) => { 
                this.funcoesColaborador = result.items;
                this.loadingFuncoesColaborador = false;
            })
        }
        if (this.tiposServico == undefined) {
            this.loadingTiposServico = true;
            this._tipoServicoPrestadoService.getAll(undefined, undefined, undefined, undefined, undefined)
            .subscribe((result) => { 
                this.tiposServico = [];
                result.items.forEach((t) => { if (t.registravelAvulso) this.tiposServico.push(t) });
                this.loadingTiposServico = false;
            })
        } else {
            let buf: TipoServicoPrestadoDto[] = []
            this.tiposServico.forEach((t) => { if (t.registravelAvulso) buf.push(t) });
            this.tiposServico = buf;
        }
    }
    
    getNomeColaborador(id: number): string {
        return this.colaboradores.find(c => c.id == id).nome;
    }
    getNomeFuncao(id: number): string {
        return this.funcoes.find(c => c.id == id).nome;
    }

    onSave: EventEmitter<ServicoPrestadoDto> = new EventEmitter<ServicoPrestadoDto>();
    saving: boolean = false;
    save() {
        let tipoServico = this.tiposServico.find(t => t.id == this.servico.tipoServicoId);
        let colabFuncao = this.funcoesColaborador.find(cf => cf.id == this.servico.colabFuncaoOrgId);
        if (!tipoServico.funcoesOrg.some(f => f.id == colabFuncao.funcaoOrgId)) {
            this.notify.info(this.l('O tipo de servio "'+tipoServico.nome+'" no pode ser realizado pela funo "'+ this.getNomeFuncao(colabFuncao.funcaoOrgId) +'".'));
            return;
        }
        this.servico.fim = this.servico.inicio.add(this.horasTrabalho, 'hours');
        this.saving = true;
        this._servicoPrestadoService.create(this.servico).subscribe((result) => {
            this.saving = false;
            this.notify.info(this.l('SavedSuccessfully'));
            this.bsModalRef.hide();
            this.onSave.emit(result);
        });
    }
}