import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, OnInit, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  AlunoServiceProxy,
  CasoServiceProxy,
  AlunoDto,
  AtuacaoDto,
  ColaboradorServiceProxy,
  ColaboradorDto,
  FuncaoOrgDto,
  ColabFuncaoOrgDto,
  CasoDto,
  ResponsavelServiceProxy,
  ResponsavelDto,
  PaisServiceProxy,
  FuncaoOrgServiceProxy
} from '@shared/service-proxies/service-proxies';
import { PessoaDetailsBaseComponent } from '@app/pessoas/pessoa-details-base/pessoa-details-base.component';
import { BsModalService } from 'ngx-bootstrap/modal';
import { Observable, Subject } from 'rxjs';

@Component({
  templateUrl: './perfil-pessoal.component.html',
  animations: [appModuleAnimation()]
})
export class PerfilPessoalComponent extends PessoaDetailsBaseComponent implements OnInit {

  protected initEmptyPessoa() : AlunoDto | ColaboradorDto | ResponsavelDto { return undefined }
  
  constructor(injector: Injector,
    protected _alunoService: AlunoServiceProxy,
    protected _casoService: CasoServiceProxy,
    protected _colaboradorService: ColaboradorServiceProxy,
    protected _responsavelService: ResponsavelServiceProxy,
    protected _funcaoService: FuncaoOrgServiceProxy,
    protected _paisService: PaisServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector, _paisService, _modalService, _router, _route);
  }

  override ngOnInit(): void {
    if (this.appSession.user.colaborador != undefined) 
      this._funcaoService.getAll(undefined, undefined, undefined).subscribe(r => this.funcoes = r.items);
    super.ngOnInit();
  }

  colaboradores: ColaboradorDto[];
  funcoesColaborador: ColabFuncaoOrgDto[];
  staff: AtuacaoDto[];
  protected override proccessDto(dto: AlunoDto | ColaboradorDto | ResponsavelDto): void {
      super.proccessDto(dto);
      if (dto instanceof AlunoDto && dto.casoId != undefined) {
        this._casoService.getAllAtuacoes(dto.casoId, undefined, undefined, undefined, undefined)
        .subscribe((result) => { this.staff = result; });
        this._colaboradorService.getAllColabFuncoes(undefined, undefined, undefined, undefined, undefined, dto.casoId, undefined, undefined, undefined)
        .subscribe((result) => { this.funcoesColaborador = result.items; });
        this._colaboradorService.getAll(undefined, undefined, undefined, undefined, dto.casoId, undefined, undefined, undefined)
        .subscribe((result) => { this.colaboradores = result.items; });
      }
  }
  getPessoa(): Observable<AlunoDto | ColaboradorDto | ResponsavelDto> {
    if (this.appSession.user.aluno != undefined) {
      this.id = this.appSession.user.aluno.id;
      this.tipoPessoa = "Aluno";
      return this._alunoService.get(this.appSession.user.aluno.id);
    } 

    if (this.appSession.user.colaborador != undefined) {
      this.id = this.appSession.user.colaborador.id;
      this.tipoPessoa = "Colaborador";
      return this._colaboradorService.get(this.appSession.user.colaborador.id);
    } 

    if (this.appSession.user.responsavel != undefined) {
      this.id = this.appSession.user.responsavel.id;
      this.tipoPessoa = "Responsavel";
      return this._responsavelService.get(this.appSession.user.responsavel.id);
    }

    return undefined;
  }

  tipoPessoa: string = "Usuario";
  protected getTipoPessoa(): string {
    return this.tipoPessoa;
  }
  getFuncaoOrg(id) {return this.funcoes?.find(f => f.id == id)}
  public funcoes: FuncaoOrgDto[];
  public historicoFuncoes: ColabFuncaoOrgDto[];
  public casos: CasoDto[];
  showAbaCasos(){
    return this.historicoFuncoes?.some(cf => this.getFuncaoOrg(cf.funcaoOrgId)?.acessoCasoNome == "Caso a Caso");
  }

  busy: boolean = false;

  public gerenciados: ColaboradorDto[];
  public gerenciadosFuncoes: ColabFuncaoOrgDto[];
  cfGerencia(cf: ColabFuncaoOrgDto): boolean { return (this.getFuncaoOrg(cf.funcaoOrgId)?.funcoesGeridasDiretamente?.length??0)>0 }
  showAbaGerenciados(): boolean {
    if (this.tipoPessoa != 'Colaborador') return false;    
    return this.historicoFuncoes?.some(this.cfGerencia.bind(this));
  }
  gerentesCF: ColabFuncaoOrgDto[];
  gerentes: ColaboradorDto[];
}
