import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  AlunoServiceProxy,
  CreateAlunoDto,
  CreateResponsavelDto,
  Int64EntityDto,
  ResponsavelDto,
  ProtocoloAvaliacaoServiceProxy,
  ProtocoloAvaliacaoDto,
  ProtocoloAvaliacaoDtoPagedResultDto,
  UpdateAlunoDto,
  AlunoDto,
  EntityDto,
  AplicacaoProtocoloAvaliacaoDto,
  CreateAplicacaoProtocoloAvaliacaoDto,
  CasoServiceProxy
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './select-protocolo-avaliacao-dialog.component.html'
})
export class SelectProtocoloAvaliacaoDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean = false;
  @Input() aluno: AlunoDto;
  protocolosAvaliacao: ProtocoloAvaliacaoDto[] = [];
  options: ProtocoloAvaliacaoDto[] = [];
  constructor(injector: Injector,
    private _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    private _alunoService: AlunoServiceProxy,
    private _casoService: CasoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  curAplicacoes: AplicacaoProtocoloAvaliacaoDto[] = [];
  ngOnInit() {
    this.saving = true;
    this.protocolosAvaliacao = [];
    this._protocoloAvaliacaoService.getAll(undefined, undefined, undefined, undefined, undefined)
    .subscribe((p: ProtocoloAvaliacaoDtoPagedResultDto) => {
      this.protocolosAvaliacao = p.items;
      this.options = this.protocolosAvaliacao.filter(p => this.curAplicacoes.every(ap => ap.protocoloAvaliacaoId != p.id || ap.dataArquivamento != undefined));
      this.saving = false;
    });
  }

  protocoloAvaliacaoIdx: number;
  get protocoloAvaliacaoSelected(): ProtocoloAvaliacaoDto {
    if (this.protocoloAvaliacaoIdx == undefined) return undefined; 
    return this.protocolosAvaliacao[this.protocoloAvaliacaoIdx] 
  }
  @Output() onSave: EventEmitter<{apli: AplicacaoProtocoloAvaliacaoDto, prot: ProtocoloAvaliacaoDto}> = new EventEmitter<{apli: AplicacaoProtocoloAvaliacaoDto, prot: ProtocoloAvaliacaoDto}>();
  save() {
    this.saving = true;

    let updateDto = new CreateAplicacaoProtocoloAvaliacaoDto({ 'casoId': this.aluno.casoId, 'protocoloAvaliacaoId': this.protocoloAvaliacaoSelected.id });
    this._casoService.createAplicacaoProtocoloAvaliacao(updateDto).subscribe(
      (result: AplicacaoProtocoloAvaliacaoDto) => {
        this.notify.info(this.l('SavedSuccessfully'));
        this.bsModalRef.hide();
        this.onSave.emit({apli: result, prot: this.protocolosAvaliacao.find(p => p.id == result.protocoloAvaliacaoId)});
        this.saving = false;
      }
    );
  }
}
