import { AppComponentBase } from '@shared/app-component-base';
import { ChangeDetectorRef, Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  AlunoServiceProxy,
  AlunoDto,
  UpdateAlunoDto,
  ColaboradorServiceProxy,
  ProtocoloAvaliacaoDto,
  ProtocoloAvaliacaoServiceProxy,
  ProtocoloAvaliacaoDtoPagedResultDto,
  AplicacaoProtocoloAvaliacaoDto,
  Int64EntityDto,
  CasoServiceProxy,
  DominioDto,
  HabilidadeDto,
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { finalize, Observable } from 'rxjs';
import { SelectProtocoloAvaliacaoDialogComponent } from './select-protocolo-avaliacao-dialog/select-protocolo-avaliacao-dialog.component';
import { PagedListingComponentBase, PagedRequestDto } from '@shared/paged-listing-component-base';

@Component({
  selector: 'protocolos-avaliacao-caso',
  templateUrl: './protocolos-avaliacao-caso.component.html',
  animations: [appModuleAnimation()]
})
export class ProtocolosAvaliacaoCasoComponent extends PagedListingComponentBase<AplicacaoProtocoloAvaliacaoDto> implements OnInit {
  private _aluno: AlunoDto;
  @Input() set aluno (v: AlunoDto) { 
    this._aluno = v; 
    this.refresh();
  }
  get aluno(): AlunoDto { return this._aluno; }
  @Output() alunoChange = new EventEmitter<AlunoDto>();

  constructor(injector: Injector,
    protected _alunoService: AlunoServiceProxy,
    protected _casoService: CasoServiceProxy,
    protected _colaboradorService: ColaboradorServiceProxy,
    protected _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    protected _modalService: BsModalService,
    protected _changeDetector: ChangeDetectorRef,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }
  
  ngOnInit(): void {
  }

  detailsProtocoloAvaliacao(pId: number) {
    this._router.navigate(['app', 'protocolos-avaliacao','details', pId]);
  }

  aplicacoes: AplicacaoProtocoloAvaliacaoDto[]
  protocolosAvaliacao: ProtocoloAvaliacaoDto[]
  // dominios: DominioDto[]
  // habilidades: HabilidadeDto[]
  protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
    this.protocolosAvaliacao = [];
    this.aplicacoes = [];
    if (this._aluno == undefined || this._aluno?.casoId == undefined) {
      finishedCallback();
      this.showPaging({ 'items': [], 'totalCount': 0 }, pageNumber);
      return;
    }
    this._protocoloAvaliacaoService
      .getAll(undefined, this._aluno.casoId, undefined, undefined, undefined)
      .subscribe((result: ProtocoloAvaliacaoDtoPagedResultDto) => {
        this.protocolosAvaliacao = result.items;
        this._changeDetector.detectChanges();
      });
    this._casoService
      .getAllAplicacoesProtocoloAvaliacao(this.aluno.casoId, undefined)
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: AplicacaoProtocoloAvaliacaoDto[]) => {
        this.aplicacoes = result;
        this.showPaging({ 'items': result, 'totalCount': result.length }, pageNumber);
      });
  }

  getNomeProtocoloAvaliacao(id: number): string {
    return this.protocolosAvaliacao?.find(p => p.id == id)?.nome??"";
  }
  protected delete(prot: AplicacaoProtocoloAvaliacaoDto): void {
    abp.message.confirm(
      "Arquivar o protocolo avaliacao \""+this.getNomeProtocoloAvaliacao(prot.protocoloAvaliacaoId)+"\" no caso do aluno "+this.aluno.nome+"?",
      undefined,
      (result: boolean) => {
        console.log(result);
        if (result) {
          let updateDto = new Int64EntityDto({ 'id': prot.id });
          console.log(updateDto);
          this._casoService.removeAplicacaoProtocoloAvaliacao(updateDto)
          .subscribe((result: AplicacaoProtocoloAvaliacaoDto) => {
            console.log(result, this.aplicacoes);
            this.aplicacoes[this.aplicacoes.findIndex(p => p.id == result.id)] = result;
            console.log(this.aplicacoes);
          });
        }
      }
    );
  }

  addProtocoloAvaliacao() {
    if (!this.permission.isGranted('Business.Personal.Colaborador.Supervisor')) return;
    let selectProtocoloAvaliacaoDialog: BsModalRef;
    selectProtocoloAvaliacaoDialog = this._modalService.show(
      SelectProtocoloAvaliacaoDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          aluno: this.aluno,
          curAplicacoes: this.aplicacoes
        }
      }
    );

    selectProtocoloAvaliacaoDialog.content.onSave.subscribe((result: {apli: AplicacaoProtocoloAvaliacaoDto, prot: ProtocoloAvaliacaoDto}) => {
      this.aplicacoes.push(result.apli);
      if (this.protocolosAvaliacao.every(p => p.id != result.prot.id))
        this.protocolosAvaliacao.push(result.prot);
      this.showPaging({ 'items': this.aplicacoes, 'totalCount': this.aplicacoes.length }, this.pageNumber);
    });
  }

  detailsAplicacaoProtocoloAvaliacao(p: AplicacaoProtocoloAvaliacaoDto) {
    this._router.navigate(['app', 'alunos', 'details',this.aluno.id,'protocolo-avaliacao', p.id]);
  }
}
