import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, includes as _includes, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  AlunoDto,
  ColaboradorDto,
  ResponsavelDto,
  AlunoServiceProxy,
  ColaboradorServiceProxy,
  ResponsavelServiceProxy,
  UpdateAlunoDto,
  UpdateColaboradorDto,
  UpdateResponsavelDto
} from '@shared/service-proxies/service-proxies';
import * as moment from 'moment';

@Component({
  templateUrl: './edit-info-pessoal-dialog.component.html'
})
export class EditInfoPessoalDialogComponent extends AppComponentBase implements OnInit {
  saving = false;

  tipoPessoa: string = undefined;

  pessoaId: number;
  inputPessoa: AlunoDto | ColaboradorDto | ResponsavelDto;
  pessoa: UpdateAlunoDto | UpdateColaboradorDto | UpdateResponsavelDto;

  private _pessoaService;

  @Output() onSave = new EventEmitter<any>();

  constructor(
    injector: Injector,
    private _alunoService: AlunoServiceProxy,
    private _colaboradorService: ColaboradorServiceProxy,
    private _responsavelService: ResponsavelServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit(): void {
    if (this.tipoPessoa == "Aluno") {
      this._pessoaService = this._alunoService;
    } else if (this.tipoPessoa == "Colaborador") {
      this._pessoaService = this._colaboradorService;
    } else if (this.tipoPessoa == "Responsavel") {
      this._pessoaService = this._responsavelService;
    } else {
      throw "Tipo de pessoa invlido";
    }
    if (this.inputPessoa == undefined)
      this._pessoaService.get(this.pessoaId).subscribe((result) => {
        this.inputPessoa = result;
        this._setUpdateDto();
      });
    else 
      this._setUpdateDto();
  }
  private _setUpdateDto() {
    if (this.inputPessoa instanceof AlunoDto)
      this.pessoa = new UpdateAlunoDto(this.inputPessoa as any);
    else if (this.inputPessoa instanceof ColaboradorDto)
      this.pessoa = new UpdateColaboradorDto(this.inputPessoa as any);
    else if (this.inputPessoa instanceof ResponsavelDto)
      this.pessoa = new UpdateResponsavelDto(this.inputPessoa as any);
  }

  save(): void {
    this.saving = true;
    this._pessoaService.update(this.pessoa).subscribe((dto) => {
      this.notify.info(this.l('SavedSuccessfully'));
      this.bsModalRef.hide();
      this.onSave.emit(dto);
      this.saving = false;
    });
  }
}
