import { ChangeDetectorRef, Component, EventEmitter, Injector, Input, OnInit, Output } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { appModuleAnimation } from "@shared/animations/routerTransition";
import { PagedListingComponentBase, PagedRequestDto } from "@shared/paged-listing-component-base";
import { CasoDto, CasoServiceProxy, ColabFuncaoOrgDto, FuncaoOrgDto, AtuacaoDto, ColaboradorDto } from "@shared/service-proxies/service-proxies";
import { BsModalRef, BsModalService } from "ngx-bootstrap/modal";
import { finalize } from "rxjs";
import { ControlContainer, NgModelGroup } from "@node_modules/@angular/forms";

@Component({
    selector: 'casos-colaborador',
    templateUrl: './casos-colaborador.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class CasosColaboradorComponent extends PagedListingComponentBase<AtuacaoDto> implements OnInit {
    atuacoes: AtuacaoDto[] = [];
    keyword = '';
    isActive: boolean | null;
    advancedFiltersVisible = false;
    constructor(
        injector: Injector,
        private _casoService: CasoServiceProxy,
        private _modalService: BsModalService,
        private _changeDectetor: ChangeDetectorRef,
        private _router: Router,
        private _route: ActivatedRoute) {
        super(injector)
    }
    private _colaboradorId: number;
    public get colaboradorId(): number  { return this._colaboradorId; }
    @Input()
    public set colaboradorId(v: number) { this._colaboradorId = v; this.refresh(); }

    // @Output() public funcoesChange: EventEmitter<FuncaoOrgDto[]> = new EventEmitter<FuncaoOrgDto[]>();
    // @Output() public historicoFuncoesChange: EventEmitter<ColabFuncaoOrgDto[]> = new EventEmitter<ColabFuncaoOrgDto[]>();
    @Input() public funcoes: FuncaoOrgDto[];
    @Input() public historicoFuncoes: ColabFuncaoOrgDto[] = [];
    @Input() public casos: CasoDto[];
    ngOnInit(): void {
        this.isTableLoading = true;
        this.refresh();
    }
    getFuncao(id: number): FuncaoOrgDto { return this.funcoes.find(f => f.id == id) }
    getNomeFuncao(colabFuncaoId: number): string {
        let cf = this.historicoFuncoes?.find(cf => cf.id == colabFuncaoId);
        return this.getFuncao(cf.funcaoOrgId)?.nome??"-";
    }
    getNomeAlunoCaso(id: number): string {
        return this.casos?.find(c => c.id == id)?.aluno?.nome??"-";
    }
    
    @Input()  
    gerentesCF: ColabFuncaoOrgDto[];
    @Input()  
    gerentes: ColaboradorDto[];
    getGestorString(colabFuncaoId: number): string {
        let cf = this.historicoFuncoes?.find(cf => cf.id == colabFuncaoId);
        var f = this.getFuncao(cf.funcaoOrgId);
        if (f.funcaoGestoraDiretaId == undefined) return "N/A";
        if (cf.gestorId == undefined) return "No atribudo"
        var gcf = this.gerentesCF?.find(gcf => gcf.id == cf?.gestorId);
        var gc = this.gerentes?.find(gc => gc.id == gcf?.colaboradorId);
        if (gc == undefined) return "..."
        return gc.nome;
    }
    createAtuacao() {
        // let createCasoDialog: BsModalRef;
        // createCasoDialog = this._modalService.show(
        //   CreateCasoDialogComponent,
        //   {
        //     class: 'modal-lg',
        //     initialState: {
        //         tiposServico: this.tiposServico
        //     }
        //   }
        // );
    
        // createCasoDialog.content.onSave.subscribe(() => {
        //   this.refresh();
        // });
    }
    protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
        if (this._colaboradorId == undefined) {
            this.atuacoes = [];
            this.showPaging({items: [], totalCount: 0}, 0);
            finishedCallback();
            return;
        }
        this._casoService
        .getAllAtuacoes(
            undefined,
            undefined,
            this._colaboradorId,
            undefined,
            undefined)
        .pipe(finalize(() => { finishedCallback(); }))
        .subscribe((result: AtuacaoDto[]) => {
            this.atuacoes = result;
            this.showPaging({ items: result, totalCount: result.length }, pageNumber);
        });
    }
    clearFilters(): void {
        this.keyword = '';
        this.getDataPage(1);
    }
    protected delete(entity: AtuacaoDto): void {
        // abp.message.confirm(
        //   "O seguinte servio ser excludo do banco",
        //   undefined,
        //   (result: boolean) => {
        //     if (result) {
        //       this._servicoPrestadoService.delete(entity.id).subscribe(() => {
        //         abp.notify.success(this.l('SuccessfullyDeleted'));
        //         this.refresh();
        //       });
        //     }
        //   }
        // );
    }
    detailsCaso(a: AtuacaoDto): void {
        this._router.navigate(['app','alunos','details', this.casos?.find(c => c.id == a.casoId)?.aluno?.id])
    }

}