import { Component, Injector } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  ProtocoloIntervencaoServiceProxy,
  ProtocoloIntervencaoDto,
  ProtocoloIntervencaoDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
// import { CreateProtocoloIntervencaoDialogComponent } from './create-formato-ensino-dialog/create-formato-ensino-dialog.component';
// import { EditProtocoloIntervencaoDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';


class PagedProtocoloIntervencaosRequestDto extends PagedRequestDto {
  keyword: string;
}

@Component({
  templateUrl: './protocolos-intervencao.component.html',
  animations: [appModuleAnimation()]
})
export class ProtocolosIntervencaoComponent extends PagedListingComponentBase<ProtocoloIntervencaoDto> {
  protocolosIntervencao: ProtocoloIntervencaoDto[] = [];
  maxDescLength: number = 250;
  keyword = '';
  isActive: boolean | null;
  advancedFiltersVisible = false;

  constructor(
    injector: Injector,
    private _protocoloIntervencaoService: ProtocoloIntervencaoServiceProxy,
    private _modalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }
  
  createProtocoloIntervencao(): void {
    this._router.navigate(['create'], { relativeTo : this._route });
  }

  detailsProtocoloIntervencao(protocoloIntervencao: ProtocoloIntervencaoDto): void {
    this._router.navigate(['details', protocoloIntervencao.id], { relativeTo : this._route });
  }

  clearFilters(): void {
    this.keyword = '';
    this.getDataPage(1);
  }

  protected list(
    request: PagedProtocoloIntervencaosRequestDto,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    request.keyword = this.keyword;

    this._protocoloIntervencaoService
      .getAll(
        request.skipCount,
        request.maxResultCount
      )
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: ProtocoloIntervencaoDtoPagedResultDto) => {
        this.protocolosIntervencao = result.items;
        this.showPaging(result, pageNumber);
      });
  }

  protected delete(proc: ProtocoloIntervencaoDto): void {
    abp.message.confirm(
      "O seguinte protocolo de interveno ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._protocoloIntervencaoService.delete(proc.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
