import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';
import { 
  AplicacaoProtocoloIntervencaoDto, 
  AtuacaoDto, 
  ColabFuncaoOrgDto, 
  ColaboradorDto, 
  HierarquiaAjudaDto, 
  PassoProtIntervencaoDto, 
  RegistroAtendimentoDto, 
  TipoAjudaDto, 
  UnidadeEnsinoDto, 
  VersaoProtIntervencaoDto 
} from '@shared/service-proxies/service-proxies';

@Component({
  templateUrl: './registro-atendimento-view-dialog.component.html'
})
export class RegistroAtendimentoViewDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean = false;
  atendimento: RegistroAtendimentoDto;
  unidades: UnidadeEnsinoDto[];
  colaboradores: ColaboradorDto[];
  funcoesColaborador: ColabFuncaoOrgDto[];
  staff: AtuacaoDto[];
  protocolosIntervencao: VersaoProtIntervencaoDto[];
  aplicacoes: AplicacaoProtocoloIntervencaoDto[];
  hierarquiasAjuda: HierarquiaAjudaDto[];
  constructor(injector: Injector,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  getAtuacao(id) { return this.staff.find(a => a.id == id) }
  getColabFuncao(id) { return this.funcoesColaborador.find(cf => cf.id == id) }
  getColaborador(id) { return this.colaboradores.find(c => c.id == id) }
  nomeAT: string;
  protocolosByUnidade: VersaoProtIntervencaoDto[] = [];
  passosByUnidade: PassoProtIntervencaoDto[] = [];
  hierarquiasAjudaByUnidade: HierarquiaAjudaDto[] = [];
  ngOnInit() { 
    this.nomeAT = this.getColaborador(this.getColabFuncao(this.getAtuacao(this.atendimento.atuacaoTerapeutaId)?.colabFuncaoId)?.colaboradorId)?.nome??"";
    this.atendimento.unidadesEnsino.forEach((u) => {
      let unidade = this.unidades.find(un => un.id == u.id);
      var passo: PassoProtIntervencaoDto;
      var proto = this.protocolosIntervencao.find((p) => {
        passo = p.passosProtIntervencao.find(pa => pa.id == unidade.passoId);
        return passo != undefined;
      });
      this.protocolosByUnidade.push(proto);
      this.passosByUnidade.push(passo);
      this.hierarquiasAjudaByUnidade.push(this.hierarquiasAjuda.find(h => h.id == proto.hierarquiaAjudaId));
    });
  }
  getNoAjudas(unidade: UnidadeEnsinoDto, tipoAjuda: TipoAjudaDto): number {
    return unidade.ajudas.find(a => a.key == tipoAjuda.id).value;
  }
}
