import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    HierarquiaAjudaServiceProxy,
    HierarquiaAjudaDto
} from '@shared/service-proxies/service-proxies';

@Component({
    templateUrl: './create-hierarquia-ajuda-dialog.component.html'
})
export class CreateHierarquiaAjudaDialogComponent extends AppComponentBase implements OnInit {
    saving = false;
    hierarquiaAjuda: HierarquiaAjudaDto;

    @Output() onSave = new EventEmitter<HierarquiaAjudaDto>();

    constructor(injector: Injector,
        public _hierarquiaAjudaService: HierarquiaAjudaServiceProxy,
        public bsModalRef: BsModalRef) {
        super(injector);
    }

    ngOnInit(): void {
        this.hierarquiaAjuda = new HierarquiaAjudaDto();
        this.hierarquiaAjuda.tiposAjuda = [];
    }

    save(): void {
        this.saving = true;

        this._hierarquiaAjudaService.create(this.hierarquiaAjuda).subscribe(
            (result) => {
                this.saving = false;
                this.notify.info(this.l('SavedSuccessfully'));
                this.bsModalRef.hide();
                this.onSave.emit(result);
            }
        );
    }
}
  