import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { HabilidadeDto } from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './select-habilidade-dialog.component.html'
})
export class SelectHabilidadeDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean = false;
  @Input()
  habilidades: HabilidadeDto[] = [];
  constructor(injector: Injector,
    public bsModalRef: BsModalRef) {
    super(injector);
  }

  ngOnInit() { }

  habilidadeIdx: number;
  get habilidadeSelected(): HabilidadeDto {
    if (this.habilidadeIdx == undefined) return undefined; 
    return this.habilidades[Number(this.habilidadeIdx)] 
  }
  @Output() onSubmit: EventEmitter<HabilidadeDto> = new EventEmitter<HabilidadeDto>();
  save() {
    this.saving = true;
    this.onSubmit.emit(this.habilidadeSelected);
    this.bsModalRef.hide();
    this.saving = false;
  }
}
