import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    FuncaoOrgServiceProxy,
    FuncaoOrgDto
} from '@shared/service-proxies/service-proxies';

@Component({
    templateUrl: './create-funcao-dialog.component.html'
})
export class CreateFuncaoDialogComponent extends AppComponentBase implements OnInit {
    saving = false;
    funcao = new FuncaoOrgDto();

    @Output() onSave = new EventEmitter<FuncaoOrgDto>();

    constructor(injector: Injector,
        public _funcaoService: FuncaoOrgServiceProxy,
        public bsModalRef: BsModalRef) {
        super(injector);
    }

    ngOnInit(): void {
    }

    save(): void {
        this.saving = true;

        console.log(this.funcao);
        this._funcaoService.create(this.funcao).subscribe(
            (result) => {
                this.saving = false;
                this.notify.info(this.l('SavedSuccessfully'));
                this.bsModalRef.hide();
                this.onSave.emit(result);
            }
        );
    }
}
  