import { AppComponentBase } from '@shared/app-component-base';
import { ChangeDetectorRef, Component, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ProtocoloAvaliacaoServiceProxy,
  ProtocoloAvaliacaoDto,
  AlunoServiceProxy,
  TipoConfirmacaoHabilidadeDto,
  RefBibliograficaDto,
  RefBibliograficaServiceProxy,
  DominioDto,
  HabilidadeDto,
  NivelDto
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { CreateResponsavelDialogComponent } from '@app/responsaveis/create-responsavel-dialog/create-responsavel-dialog.component';
import { AddDominioDialogComponent } from './dialogs/add-dominio-dialog/add-dominio-dialog.component';
import { UpdateDominioDialogComponent } from './dialogs/update-dominio-dialog/update-dominio-dialog.component';
import { AddNivelDialogComponent } from './dialogs/add-nivel-dialog/add-nivel-dialog.component';

@Component({
  templateUrl: './protocolo-avaliacao-details.component.html',
  animations: [appModuleAnimation()]
})
export class ProtocoloAvaliacaoDetailsComponent extends AppComponentBase implements OnInit {
  public protocoloAvaliacao: ProtocoloAvaliacaoDto;
  public niveis: NivelDto[];
  public dominios: DominioDto[];
  public habilidades: HabilidadeDto[];
  public refBibliografica: RefBibliograficaDto;
  tiposConfirmacao: TipoConfirmacaoHabilidadeDto[] = [];
  constructor(injector: Injector,
    protected _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    protected _refBibliograficaService: RefBibliograficaServiceProxy,
    protected _alunoService: AlunoServiceProxy,
    protected _changeDetector: ChangeDetectorRef,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
    this._protocoloAvaliacaoService.getAllTiposConfirmacaoHabilidade().subscribe((result) => { 
        this.tiposConfirmacao = result.sort((a, b) => {
          let pred_a = (a?.nome??"").toLowerCase().startsWith("obs");
          let pred_b = (b?.nome??"").toLowerCase().startsWith("obs");
          if (pred_a == pred_b)
            return  0;
          else if (pred_a)
            return -1;
          else
            return  1;
        });
        // console.log(result);
      });
  }

  ngOnInit() { 
    const params = this._route.snapshot.paramMap;
    const id = Number(params.get("protocoloAvaliacaoId"));
    this._protocoloAvaliacaoService.get(id).subscribe((p: ProtocoloAvaliacaoDto) => { 
      this.protocoloAvaliacao = p 
      if (p?.refBibliograficaId != undefined) {
        this._refBibliograficaService.get(p.refBibliograficaId).subscribe((r: RefBibliograficaDto) => {
          this.refBibliografica = r;
        });
      }
    });
    this._protocoloAvaliacaoService.getAllNiveis(undefined, id, undefined, undefined, undefined, undefined)
    .subscribe((result) => {
      this.niveis = result.items;
    });
    this._protocoloAvaliacaoService.getAllDominios(undefined, id, undefined, undefined, undefined, undefined, undefined)
    .subscribe((result) => {
      this.dominios = result.items;
    });
    this._protocoloAvaliacaoService.getAllHabilidades(undefined, undefined, undefined, undefined, undefined, id, undefined, undefined, undefined, undefined)
    .subscribe((result) => {
      this.habilidades = result.items;
    });
  }

  addNivel(){
    let addNivelDialog: BsModalRef;
    addNivelDialog = this._modalService.show(
      AddNivelDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          protocoloAvaliacao: this.protocoloAvaliacao,
          tiposConfirmacao: this.tiposConfirmacao
        }
      }
    );

    addNivelDialog.content.onSave.subscribe(() => {
      this.ngOnInit();
    });
  }
  
  goBack() {
    this._router.navigate(["app", "protocolos-avaliacao"]);
  }

  toRefBibDetails() {
    if (this.protocoloAvaliacao?.refBibliograficaId != undefined)
    this._router.navigate(["app", "ref-bibliograficas", "details", this.protocoloAvaliacao.refBibliograficaId])
  }
}
