import {
  Component,
  Injector,
  OnInit,
  EventEmitter,
  Output,
  Input
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import { FormatoEnsinoDto, FormatoEnsinoServiceProxy } from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { types } from 'util';
import { isMoment, isDate } from 'moment';
import * as moment from 'moment';
import { Observable } from 'rxjs';

@Component({
  templateUrl: './select-formato-ensino-dialog.component.html'
})
export class SelectFormatoEnsinoDialogComponent extends AppComponentBase implements OnInit {
  saving: boolean = false;
  @Input()
  formatosEnsino: FormatoEnsinoDto[] = [];
  constructor(injector: Injector,
    private _formatoEnsinoService: FormatoEnsinoServiceProxy,
    public bsModalRef: BsModalRef) {
    super(injector);
  }
  
  novoFormatoEnsino: FormatoEnsinoDto;
  saved: boolean
  ngOnInit() {
    this.novoFormatoEnsino = new FormatoEnsinoDto();
    this.saved = false;
  }

  private _formatoEnsinoIdx: number;
  get formatoEnsinoIdx(): string  { return this._formatoEnsinoIdx?.toString()??"" }
  set formatoEnsinoIdx(v: string) { this._formatoEnsinoIdx = v!=""?Number(v):undefined }
  get formatoEnsinoSelected(): FormatoEnsinoDto {
    if (this._formatoEnsinoIdx == undefined || this._formatoEnsinoIdx == -1) return undefined; 
    return this.formatosEnsino[this._formatoEnsinoIdx] 
  }
  @Output() onSubmit: EventEmitter<FormatoEnsinoDto> = new EventEmitter<FormatoEnsinoDto>();
  save() {
    if (this.saving || this.saved) return;
    this.saving = true;
    if (this._formatoEnsinoIdx == -1) {
      this._formatoEnsinoService.create(this.novoFormatoEnsino)
      .subscribe((result: FormatoEnsinoDto) => {
        this.onSubmit.emit(result);
        this.bsModalRef.hide();
        this.saved = true;
        this.saving = false;
      });
    } else {
      this.onSubmit.emit(this.formatoEnsinoSelected);
      this.bsModalRef.hide();
      this.saved = true;
      this.saving = false;
    }
  }
}
