import { ChangeDetectorRef, Component, Injector, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { appModuleAnimation } from "@shared/animations/routerTransition";
import { PagedListingComponentBase, PagedRequestDto } from "@shared/paged-listing-component-base";
import { FuncaoOrgDto, FuncaoOrgDtoPagedResultDto, FuncaoOrgServiceProxy } from "@shared/service-proxies/service-proxies";
import { BsModalRef, BsModalService } from "ngx-bootstrap/modal";
import { finalize } from "rxjs";
import { CreateFuncaoDialogComponent } from "./create-funcao-dialog/create-funcao-dialog.component";
import { EditFuncaoDialogComponent } from "./edit-funcao-dialog/edit-funcao-dialog.component";

@Component({
    templateUrl: './funcoes.component.html',
    animations: [appModuleAnimation()]
})
export class FuncoesComponent extends PagedListingComponentBase<FuncaoOrgDto> implements OnInit {
    funcoes: FuncaoOrgDto[] = [];
    keyword = '';
    isActive: boolean | null;
    advancedFiltersVisible = false;
    constructor(
        injector: Injector,
        private _funcaoService: FuncaoOrgServiceProxy,
        private _modalService: BsModalService,
        private _changeDectetor: ChangeDetectorRef,
        private _router: Router,
        private _route: ActivatedRoute) {
        super(injector)
    }
    ngOnInit(): void {
        this.refresh();
    }
    createFuncao() {
        let createFuncaoDialog: BsModalRef;
        createFuncaoDialog = this._modalService.show(
          CreateFuncaoDialogComponent,
          {
            class: 'modal-lg',
            // initialState: { }
          }
        );
    
        createFuncaoDialog.content.onSave.subscribe(() => {
          this.refresh();
        });
    }
    editFuncao(funcao: FuncaoOrgDto) {
        let editFuncaoDialog: BsModalRef;
        editFuncaoDialog = this._modalService.show(
          EditFuncaoDialogComponent,
          {
            class: 'modal-lg',
            initialState: {
              funcao: funcao 
            }
          }
        );
    
        editFuncaoDialog.content.onSave.subscribe(() => {
          this.refresh();
        });
    }
    protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
        console.log(request);
        this._funcaoService
        .getAll(
            undefined,
            request.skipCount,
            request.maxResultCount)
        .pipe(finalize(() => {
            finishedCallback();
        }))
        .subscribe((result: FuncaoOrgDtoPagedResultDto) => {
            console.log(result);
            this.funcoes = result.items;
            this.showPaging(result, pageNumber);
        });
    }
    clearFilters(): void {
        this.keyword = '';
        this.getDataPage(1);
    }
    protected delete(entity: FuncaoOrgDto): void {
        abp.message.confirm(
          "O seguinte tecnica ser excluda do banco",
          undefined,
          (result: boolean) => {
            if (result) {
              this._funcaoService.delete(entity.id).subscribe(() => {
                abp.notify.success(this.l('SuccessfullyDeleted'));
                this.refresh();
              });
            }
          }
        );
    }

}