import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    FormatoEnsinoServiceProxy,
    FormatoEnsinoDto
} from '@shared/service-proxies/service-proxies';

@Component({
    templateUrl: './create-formato-ensino-dialog.component.html'
})
export class CreateFormatoEnsinoDialogComponent extends AppComponentBase implements OnInit {
    saving = false;
    formatoEnsino = new FormatoEnsinoDto();

    @Output() onSave = new EventEmitter<FormatoEnsinoDto>();

    constructor(injector: Injector,
        public _formatoEnsinoService: FormatoEnsinoServiceProxy,
        public bsModalRef: BsModalRef) {
        super(injector);
    }

    ngOnInit(): void {
    }

    save(): void {
        this.saving = true;

        this._formatoEnsinoService.create(this.formatoEnsino).subscribe(
            (result) => {
                this.saving = false;
                this.notify.info(this.l('SavedSuccessfully'));
                this.bsModalRef.hide();
                this.onSave.emit(result);
            }
        );
    }
}
  