import { Component, EventEmitter, Injector, Input, Output } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  TecnicaServiceProxy,
  TecnicaDto,
  TecnicaDtoPagedResultDto,
  RefBibliograficaDto
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
import { TecnicaDetailsDialogComponent } from './tecnica-details-dialog/tecnica-details-dialog.component';
import { CreateTecnicaDialogComponent } from '@app/tecnicas/create-tecnica-dialog/create-tecnica-dialog.component';
// import { EditTecnicaDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';

@Component({
  templateUrl: './tecnicas-ref-bib.component.html',
  selector: 'tecnicas-ref-bib',
  animations: [appModuleAnimation()]
})
export class TecnicasRefBibComponent extends PagedListingComponentBase<TecnicaDto> {
  private _refBibId: number;
  
  @Input()
  set refBibId(v: number) {
    this._refBibId = v;
    this.refresh();
  }
  get refBibId(): number  {
    return this._refBibId;
  }

  public tecnicas: TecnicaDto[]

  constructor(
    injector: Injector,
    private _tecnicaService: TecnicaServiceProxy,
    private _modalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }

  detailsTecnica(tecnica: TecnicaDto): void {
    let tecnicaDetailsDialog: BsModalRef;
    tecnicaDetailsDialog = this._modalService.show(
      TecnicaDetailsDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          tecnica: tecnica
        }
      }
    );
  }

  clearFilters(): void {
    this.getDataPage(1);
  }

  protected list(
    request,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    if (this.refBibId == undefined) {
      this.showPaging({"items": [], "totalCount": 0 }, pageNumber);
      finishedCallback();
      return;
    }
    this._tecnicaService.getAll(undefined, this.refBibId, request.skipCount, request.maxResultCount)
    .subscribe((result: TecnicaDtoPagedResultDto) => {
      this.tecnicas = result.items;
      this.showPaging(result, pageNumber);
      finishedCallback();
      this.isTableLoading = false;
    });
  }

  @Output() onSave = new EventEmitter<undefined>();
  createTecnica() {
    let createTecnicaDialog: BsModalRef;
    createTecnicaDialog = this._modalService.show(
      CreateTecnicaDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          presetRefBib: this._refBibId
        }
      }
    );

    createTecnicaDialog.content.onSave.subscribe(() => {
      this.refresh();
      this.onSave.emit();
    });
  }

  protected delete(t: TecnicaDto): void {
    abp.message.confirm(
      "O seguinte tecnica ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._tecnicaService.delete(t.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
