import { AppComponentBase } from '@shared/app-component-base';
import { Component, Injector, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  RefBibliograficaServiceProxy,
  RefBibliograficaDto
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { EditRefBibliograficaDialogComponent } from './edit-ref-bibliografica-dialog/edit-ref-bibliografica-dialog.component';

@Component({
  templateUrl: './ref-bibliografica-details.component.html',
  animations: [appModuleAnimation()]
})
export class RefBibliograficaDetailsComponent extends AppComponentBase implements OnInit {
  public refBibliografica: RefBibliograficaDto;
  
  constructor(injector: Injector,
    protected _refbibliograficaService: RefBibliograficaServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  public id: number;
  ngOnInit() { 
    const params = this._route.snapshot.paramMap;
    this.id = Number(params.get("refBibId"));
    this.refresh();
  }

  refresh() {
    this._refbibliograficaService.get(this.id).subscribe((r: RefBibliograficaDto) => { 
      this.refBibliografica = r;
    });
  }
  
  goBack() {
    this._router.navigate(["../.."], {relativeTo:this._route})
  }

  editInfo() {
    let createRefBibliograficaDialog: BsModalRef;
    createRefBibliograficaDialog = this._modalService.show(
      EditRefBibliograficaDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          refBibliografica: this.refBibliografica
        }
      }
    );

    createRefBibliograficaDialog.content.onSave.subscribe(() => {
      this.refresh();
    });
  }
}
