import { ChangeDetectorRef, Component, Injector, OnInit } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { appModuleAnimation } from "@shared/animations/routerTransition";
import { PagedListingComponentBase, PagedRequestDto } from "@shared/paged-listing-component-base";
import { ColabFuncaoOrgDto, ColaboradorDto, ColaboradorServiceProxy, ServicoPrestadoDto, ServicoPrestadoDtoPagedResultDto, ServicoPrestadoServiceProxy, TipoServicoPrestadoDto, TipoServicoPrestadoServiceProxy } from "@shared/service-proxies/service-proxies";
import { BsModalRef, BsModalService } from "ngx-bootstrap/modal";
import { finalize } from "rxjs";
import { CreateServicoPrestadoDialogComponent } from "./create-servico-prestado-dialog/create-servico-prestado-dialog.component";

@Component({
    templateUrl: './servicos-prestados.component.html',
    animations: [appModuleAnimation()]
})
export class ServicosPrestadosComponent extends PagedListingComponentBase<ServicoPrestadoDto> implements OnInit {
    servicos: ServicoPrestadoDto[] = [];
    keyword = '';
    isActive: boolean | null;
    advancedFiltersVisible = false;
    constructor(
        injector: Injector,
        private _servicoPrestadoService: ServicoPrestadoServiceProxy,
        private _tipoServicoPrestadoService: TipoServicoPrestadoServiceProxy,
        private _colaboradorService: ColaboradorServiceProxy,
        private _modalService: BsModalService,
        private _changeDectetor: ChangeDetectorRef,
        private _router: Router,
        private _route: ActivatedRoute) {
        super(injector)
    }
    tiposServico: TipoServicoPrestadoDto[];
    funcoesColaborador: ColabFuncaoOrgDto[];
    colaboradores: ColaboradorDto[];
    ngOnInit(): void {
        this.isTableLoading = true;
        this._tipoServicoPrestadoService
        .getAll(undefined, undefined, undefined, undefined, undefined)
        .subscribe((result) => {
            this.tiposServico = result.items;
            this.refresh();
            // this._changeDectetor.detectChanges();
        });
        this._colaboradorService
            .getAll(undefined, undefined, undefined, undefined, undefined, undefined, undefined, undefined)
            .subscribe((result) => { 
                this.colaboradores = result.items;
                this._changeDectetor.detectChanges();
            });
        this._colaboradorService
            .getAllColabFuncoes(undefined, undefined, undefined, undefined, undefined, undefined, undefined, undefined, undefined)
            .subscribe((result) => { 
                this.funcoesColaborador = result.items;
                this._changeDectetor.detectChanges();
            });
    }
    getFuncaoColaborador(id: number): ColabFuncaoOrgDto { return this.funcoesColaborador?.find(cf => cf.id == id) }
    getColaborador(id: number): ColaboradorDto { return this.colaboradores?.find(cf => cf.id == id) }
    getTipoServicoNome(id: number): string {
        return this.tiposServico?.find(t => t.id == id)?.nome??"-";
    }
    createServicoPrestado() {
        let createServicoPrestadoDialog: BsModalRef;
        createServicoPrestadoDialog = this._modalService.show(
          CreateServicoPrestadoDialogComponent,
          {
            class: 'modal-lg',
            initialState: {
                tiposServico: this.tiposServico
            }
          }
        );
    
        createServicoPrestadoDialog.content.onSave.subscribe(() => {
          this.refresh();
        });
    }
    protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
        this._servicoPrestadoService
        .getAll(
            undefined,
            undefined,
            undefined,
            undefined,
            undefined,
            undefined,
            request.skipCount,
            request.maxResultCount
        )
        .pipe(
            finalize(() => {
                finishedCallback();
            })
        )
        .subscribe((result: ServicoPrestadoDtoPagedResultDto) => {
            this.servicos = result.items;
            this.showPaging(result, pageNumber);
        });
    }
    clearFilters(): void {
        this.keyword = '';
        this.getDataPage(1);
    }
    protected delete(entity: ServicoPrestadoDto): void {
        abp.message.confirm(
          "O seguinte tecnica ser excludo do banco",
          undefined,
          (result: boolean) => {
            if (result) {
              this._servicoPrestadoService.delete(entity.id).subscribe(() => {
                abp.notify.success(this.l('SuccessfullyDeleted'));
                this.refresh();
              });
            }
          }
        );
    }

}