import { Component, Injector } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  ColaboradorServiceProxy,
  ColaboradorDto,
  ColaboradorDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
import { CreateColaboradorDialogComponent } from './create-colaborador-dialog/create-colaborador-dialog.component';
// import { EditColaboradorDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';


class PagedColaboradoresRequestDto extends PagedRequestDto {
  keyword: string;
}

@Component({
  templateUrl: './colaboradores.component.html',
  animations: [appModuleAnimation()]
})
export class ColaboradoresComponent extends PagedListingComponentBase<ColaboradorDto> {
  colaboradores: ColaboradorDto[] = [];
  keyword = '';
  isActive: boolean | null;
  advancedFiltersVisible = false;

  constructor(
    injector: Injector,
    private _colaboradoresService: ColaboradorServiceProxy,
    private _modalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }
  
  createColaborador(): void {
    let createColaboradorDialog: BsModalRef;
    createColaboradorDialog = this._modalService.show(
      CreateColaboradorDialogComponent,
      {
        class: 'modal-lg',
      }
    );

    createColaboradorDialog.content.onSave.subscribe(() => {
      this.refresh();
    });
  }

  detailsColaborador(colaboradores: ColaboradorDto): void {
    this._router.navigate(['details', colaboradores.id], { relativeTo : this._route });
  }

  clearFilters(): void {
    this.keyword = '';
    this.isActive = undefined;
    this.getDataPage(1);
  }

  protected list(
    request: PagedColaboradoresRequestDto,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    request.keyword = this.keyword;

    this._colaboradoresService
      .getAll(
        undefined,
        undefined,
        undefined,
        undefined,
        undefined,
        request.keyword,
        request.skipCount,
        request.maxResultCount
      )
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: ColaboradorDtoPagedResultDto) => {
        this.colaboradores = result.items;
        this.showPaging(result, pageNumber);
      });
  }

  protected delete(pesssoa: ColaboradorDto): void {
    abp.message.confirm(
      "O seguinte endereo ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._colaboradoresService.delete(pesssoa.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
