import {
  Component,
  Injector,
  OnInit,
  Input,
  EventEmitter,
  Output,
  ChangeDetectorRef
} from '@angular/core';
import { ControlContainer, NgModelGroup} from '@angular/forms'
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
  CreateAlunoDto,
  CreateResponsavelDto,
  CreateColaboradorDto,
  UpdateAlunoDto,
  UpdateColaboradorDto,
  UpdateResponsavelDto,
  PaisServiceProxy,
  PaisDto,
  UFDto,
  CidadeDto,
  Int64EntityDto
} from '@shared/service-proxies/service-proxies';
import { AbpValidationError } from '@shared/components/validation/abp-validation.api';
import { finalize } from 'rxjs/operators';

import { PagedListingComponentBase } from '@shared/paged-listing-component-base';
import { CustomFormBaseComponent } from '@app/bases/custom-form-base.component';

@Component({
    selector: 'endereco-form',
    templateUrl: './endereco-form.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class EnderecoFormComponent extends CustomFormBaseComponent implements OnInit {
  @Input() pessoa : CreateAlunoDto | UpdateAlunoDto | CreateResponsavelDto | UpdateResponsavelDto | CreateColaboradorDto | UpdateColaboradorDto;

  private _paisId? : number;
  public get paisId(): string | undefined  { return this._paisId?.toString()??""; }
  public set paisId(v: string | undefined) {
    if (v != undefined && v.length > 0) {
      let nv = Number(v);
      if (nv != this._paisId) {
        this.ufId = undefined;
      }
      this._paisId = nv;
    } else {
      this._paisId = undefined;
      this.ufId = undefined;      
    }
  }
  public _ufId?: number;
  public get ufId(): string | undefined  { return this._ufId?.toString()??""; }
  public set ufId(v: string | undefined) {
    if (v != undefined && v.length > 0) {
      let nv = Number(v);
      if (nv != this._ufId) {
        this.pessoa.endereco_CidadeId = undefined;
      }
      this._ufId = nv;
    } else {
      this._ufId = undefined;
      this.pessoa.endereco_CidadeId = undefined;      
    }
  }
  public get cidadeId(): string | undefined  { return this.pessoa?.endereco_CidadeId?.toString()??"" }
  public set cidadeId(v: string | undefined) { 
    var n = Number(v);
    if (Number.isNaN(n)) n = undefined;
    this.pessoa.endereco_CidadeId = n;
  }

  constructor(
    injector: Injector,
    private _paisService : PaisServiceProxy,
    private _changeDetector: ChangeDetectorRef
  ) {
    super(injector);
  }

  paises      : PaisDto[] = [];
  ufsByPais   : { [paisId: number] : UFDto[] } = {};
  ufs         : UFDto[] = [];
  cidadesByUF : { [ufId: number]   : CidadeDto[] } = {};
  cidades     : CidadeDto[] = [];
  ngOnInit(): void {
    this._paisService.getAllPaises().subscribe((paises) => {
      console.log(paises);
      paises.forEach((pais) => {
        this.paises.push(pais);
        this.ufsByPais[pais.id] = [];
      });
      this._paisService.getAllUnidadesFederais(undefined).subscribe((ufs) => {
        this.ufs = ufs;
        ufs.forEach((uf) => {
          this.ufsByPais[uf.paisId].push(uf);
          this.cidadesByUF[uf.id] = [];
        });
        this._paisService.getAllCidades(undefined, undefined).subscribe((cidades) => {
          this.cidades = cidades;
          cidades.forEach((cidade) => { 
            this.cidadesByUF[cidade.ufId].push(cidade);
          });
          if (this.pessoa?.endereco_CidadeId != undefined) {
            let cidade = cidades.find(c => c.id == this.pessoa.endereco_CidadeId);
            if (cidade != undefined) {
              this._ufId = cidade.ufId;
              this._paisId = ufs.find(u => u.id == this._ufId).paisId;
            }
          }
          this._changeDetector.detectChanges();
        });
      });
    });
  }
}
