import { ChangeDetectorRef, Component, EventEmitter, Injector, Input, OnInit, Output } from "@angular/core";
import { ActivatedRoute, Router } from "@angular/router";
import { appModuleAnimation } from "@shared/animations/routerTransition";
import { PagedListingComponentBase, PagedRequestDto } from "@shared/paged-listing-component-base";
import { CasoDto, ColaboradorServiceProxy, ColabFuncaoOrgDto, FuncaoOrgDto, AtuacaoDto, ColaboradorDto, Int64EntityDto } from "@shared/service-proxies/service-proxies";
import { BsModalRef, BsModalService } from "ngx-bootstrap/modal";
import { finalize } from "rxjs";
import { ControlContainer, NgModelGroup } from "@node_modules/@angular/forms";
import { AddGerenciadoDialogComponent } from "./add-gerenciado-dialog/add-gerenciado-dialog.component";

@Component({
    selector: 'gerenciados-colaborador',
    templateUrl: './gerenciados-colaborador.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class GerenciadosColaboradorComponent extends PagedListingComponentBase<ColabFuncaoOrgDto> implements OnInit {
    keyword = '';
    isActive: boolean | null;
    advancedFiltersVisible = false;
    constructor(
        injector: Injector,
        protected _colaboradorService: ColaboradorServiceProxy,
        private _modalService: BsModalService,
        private _changeDectetor: ChangeDetectorRef,
        private _router: Router,
        private _route: ActivatedRoute) {
        super(injector)
    }
    id:number
    @Input() public colaborador: ColaboradorDto;
    @Input() public funcoes: FuncaoOrgDto[];
    @Input() public historicoFuncoes: ColabFuncaoOrgDto[] = [];
    @Input() public gerenciados: ColaboradorDto[] = [];
    @Output() gerenciadosChange: EventEmitter<ColaboradorDto[]> = new EventEmitter<ColaboradorDto[]>();
    @Input() public gerenciadosFuncoes: ColabFuncaoOrgDto[] = [];
    @Output() gerenciadosFuncoesChange: EventEmitter<ColabFuncaoOrgDto[]> = new EventEmitter<ColabFuncaoOrgDto[]>();
    ngOnInit(): void {
        this.isTableLoading = true;
        const params = this._route.snapshot.paramMap;
        var p = params.get("colaboradorId");
        if (p == undefined || p == '')
          this.id = this.appSession.user.colaborador?.id;
        else
          this.id = Number(p);
        this.refresh();
    }
    getFuncao(id: number): FuncaoOrgDto { return this.funcoes?.find(f => f.id == id) }
    getNomeFuncao(colabFuncaoId: number): string {
        let cf = this.gerenciadosFuncoes?.find(cf => cf.id == colabFuncaoId);
        return this.getFuncao(cf.funcaoOrgId)?.nome??"-";
    }
    getColaborador(id: number): ColaboradorDto { return this.gerenciados?.find(f => f.id == id) }
    protected list(request: PagedRequestDto, pageNumber: number, finishedCallback: Function): void {
        this._colaboradorService.getAll(undefined, this.id, undefined, undefined, undefined, undefined, undefined, undefined)
        .subscribe((result) => {
          this.gerenciados = result.items;
          this.gerenciadosChange.emit(this.gerenciados);
        });
        this._colaboradorService.getAllColabFuncoes(undefined, undefined, this.id, undefined, undefined, undefined, undefined, request.skipCount, request.maxResultCount)
        .pipe(finalize(() => { finishedCallback() }))
        .subscribe((result) => {
          this.gerenciadosFuncoes = result.items;
          this.gerenciadosFuncoesChange.emit(this.gerenciadosFuncoes);
          this.showPaging(result, pageNumber);
        });
    }
    clearFilters(): void {
        this.keyword = '';
        this.getDataPage(1);
    }
    protected delete(entity: ColabFuncaoOrgDto): void {
        abp.message.confirm(
          "O colaborador" + this.getColaborador(entity?.colaboradorId)?.nome + " ser removido dos gerenciados",
          undefined,
          (result: boolean) => {
            if (result) {
              this._colaboradorService.removeGestaoColabFuncao(new Int64EntityDto({id: entity.id})).subscribe(() => {
                abp.notify.success(this.l('SuccessfullyDeleted'));
                this.refresh();
              });
            }
          }
        );
    }
    cfGerencia(cf: ColabFuncaoOrgDto): boolean { return (this.getFuncao(cf.funcaoOrgId)?.funcoesGeridasDiretamente?.length??0)>0 }
    podeGerenciar(): boolean { return this.historicoFuncoes.some(this.cfGerencia.bind(this))}
    addGerenciadoPopup() {
        let cfGerentes = this.historicoFuncoes.filter(this.cfGerencia.bind(this));
        
        let addGerenciadoDialog: BsModalRef;
        addGerenciadoDialog = this._modalService.show(
          AddGerenciadoDialogComponent,
          {
            class: 'modal-lg',
            initialState: {
                funcoes: this.funcoes,
                colaboradores: [this.colaborador],
                gestorOptions: cfGerentes,
            }
          }
        );
    
        addGerenciadoDialog.content.onSave.subscribe(() => {
          this.refresh();
        });
    }
}