import {
    Component,
    Injector,
    OnInit,
    EventEmitter,
    Output
} from '@angular/core';
import { BsModalRef } from 'ngx-bootstrap/modal';
import { forEach as _forEach, map as _map } from 'lodash-es';
import { AppComponentBase } from '@shared/app-component-base';
import {
    FuncaoOrgServiceProxy,
    FuncaoOrgDto,
    TipoServicoPrestadoServiceProxy
} from '@shared/service-proxies/service-proxies';

@Component({
    templateUrl: './edit-funcao-dialog.component.html'
})
export class EditFuncaoDialogComponent extends AppComponentBase implements OnInit {
    saving = false;
    funcao: FuncaoOrgDto;

    @Output() onSave = new EventEmitter<FuncaoOrgDto>();

    constructor(injector: Injector,
        public _funcaoService: FuncaoOrgServiceProxy,
        private _tipoServicoPrestadoService: TipoServicoPrestadoServiceProxy,
        public bsModalRef: BsModalRef) {
        super(injector);
    }
    
    ngOnInit(): void {

    }

    save(): void {
        this.saving = true;

        this._funcaoService.update(this.funcao).subscribe(
            (result) => {
                this.saving = false;
                this.notify.info(this.l('SavedSuccessfully'));
                this.bsModalRef.hide();
                this.onSave.emit(result);
            }
        );
    }
}
  