import { AppComponentBase } from '@shared/app-component-base';
import { Component, Injector, Input, Output, OnInit, EventEmitter, ChangeDetectorRef } from "@angular/core";
import { DominioDto, FormatoEnsinoDto, FormatoEnsinoServiceProxy, HabilidadeDto, HierarquiaAjudaDto, HierarquiaAjudaServiceProxy, NivelDto, ProtocoloAvaliacaoDto, ProtocoloAvaliacaoServiceProxy, TecnicaDto, TecnicaServiceProxy, VersaoProtIntervencaoDto } from '@shared/service-proxies/service-proxies';
import { Observable } from 'rxjs';
import { Router } from '@node_modules/@angular/router';

@Component({
    selector:'versao-details',
    templateUrl: './versao-details.component.html'
})
export class VersaoDetailsComponent extends AppComponentBase implements OnInit {
    private _versao: VersaoProtIntervencaoDto;
    @Input() 
    set versao(v: VersaoProtIntervencaoDto) {
      this._versao = v;
      this.refresh();
    }
    get versao(): VersaoProtIntervencaoDto  {
      return this._versao
    }
    @Output() onClickEdit = new EventEmitter<any>();
    constructor(
      private _formatoEnsinoService: FormatoEnsinoServiceProxy,
      private _hierarquiaAjudaService: HierarquiaAjudaServiceProxy,
      private _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
      private _tecnicaService: TecnicaServiceProxy,
      private _changeDetector: ChangeDetectorRef,
      private _router: Router,
      injector: Injector) {
      super(injector);
    }

    ngOnInit() {
        
    }
    getNomeTecnica(id: number): string {
      return this.tecnicas?.find(t => t.id == id)?.nome
    }
    tecnicas: TecnicaDto[];
    formatosEnsino: FormatoEnsinoDto[];
    hierarquiaAjuda: HierarquiaAjudaDto;
    protocolosAvaliacaoRelacionados: ProtocoloAvaliacaoDto[];
    dominios: DominioDto[];
    niveis: NivelDto[];
    habilidades: HabilidadeDto[];
    refresh() {
      if (this.versao == undefined) return;
      this._hierarquiaAjudaService.get(this.versao.hierarquiaAjudaId).subscribe((h) => {
        this.hierarquiaAjuda = h;
        this._changeDetector.detectChanges();
      });

      this.tecnicas = [];
      this.versao.tecnicasUtilizadas.forEach((t) => {
        this._tecnicaService.get(t.id).subscribe((result) => {
          this.tecnicas.push(result);
          this._changeDetector.detectChanges();
        });
      });
      
      this.formatosEnsino = [];
      this.versao.formatosEnsino.forEach((dto) => { this._formatoEnsinoService.get(dto.id).subscribe(this.formatosEnsino.push) });
      this.protocolosAvaliacaoRelacionados = []
      this._protocoloAvaliacaoService
      .getAll(undefined, undefined, this.versao.objetivosAlvo.map(h => h.id), undefined, undefined)
      .subscribe(((result) => {
        this.protocolosAvaliacaoRelacionados = result.items;
        this._changeDetector.detectChanges();
      }));

      this.niveis = [];
      this._protocoloAvaliacaoService
      .getAllNiveis(undefined, undefined, undefined, this.versao.objetivosAlvo.map(h => h.id), undefined, undefined)
      .subscribe((result) => {
        this.niveis = result.items;
        this._changeDetector.detectChanges();
      })
      
      
      this.dominios = [];
      this._protocoloAvaliacaoService
      .getAllDominios(undefined, undefined, undefined, undefined, this.versao.objetivosAlvo.map(h => h.id), undefined, undefined)
      .subscribe((result) => {
        this.dominios = result.items;
        this._changeDetector.detectChanges();
      })
      
      this.habilidades = [];
      this._protocoloAvaliacaoService
      .getAllHabilidades(undefined, undefined, undefined, undefined, undefined, undefined, undefined, this.versao.objetivosAlvo.map(h => h.id), undefined, undefined)
      .subscribe((result) => {
        this.habilidades = result.items;
        this._changeDetector.detectChanges();
      })
    }
    
    getHabilidadeString(id: number): string {
      let hab = this.habilidades?.find(h => h.id == id);
      let s: string = "";
      if (hab?.dominioId != undefined) {
        let domi = this.dominios.find(d => d.id == hab?.dominioId);
        if (domi != undefined) {
          let nivel = this.niveis?.find(p => p.id == domi?.nivelId);
          let prot = this.protocolosAvaliacaoRelacionados?.find(p => p.id == nivel?.protocoloAvaliacaoId);
          s += prot?.nome??"";
          s += " - " + (nivel?.nome??"");
          s += " - " + domi?.nome + " - "
        }
      }
      s += hab?.nome??""
      if (s == "") return undefined;
      return s;
    }
    hierarquiaAjudaDetails() {
      this._router.navigate(['app', 'hierarquias-ajuda', 'details', this.hierarquiaAjuda.id]);
    }
    formatoEnsinoDetails(f: FormatoEnsinoDto) {
      this._router.navigate(['app', 'formatos-ensino', 'details', f.id]);
    }
}