import { Component, Injector } from '@angular/core';
import { finalize } from 'rxjs/operators';
import { BsModalService, BsModalRef } from 'ngx-bootstrap/modal';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  PagedListingComponentBase,
  PagedRequestDto
} from '@shared/paged-listing-component-base';
import {
  ProtocoloAvaliacaoServiceProxy,
  ProtocoloAvaliacaoDto,
  ProtocoloAvaliacaoDtoPagedResultDto
} from '@shared/service-proxies/service-proxies';
import { ActivatedRoute, Router } from '@angular/router';
import { result } from 'lodash-es';
// import { CreateProtocoloAvaliacaoDialogComponent } from './create-protocolo-avaliacao-dialog/create-protocolo-avaliacao-dialog.component';
// import { EditProtocoloAvaliacaoDialogComponent } from './edit-pesssoa/edit-pesssoa-dialog.component';


class PagedProtocolosAvaliacaoRequestDto extends PagedRequestDto {
  keyword: string;
}

@Component({
  templateUrl: './habilidades.component.html',
  selector: 'habilidades',
  animations: [appModuleAnimation()]
})
export class HabilidadesComponent extends PagedListingComponentBase<ProtocoloAvaliacaoDto> {
  protocolosAvaliacao: ProtocoloAvaliacaoDto[] = [];
  keyword = '';
  isActive: boolean | null;
  advancedFiltersVisible = false;

  constructor(
    injector: Injector,
    private _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    private _modalService: BsModalService,
    private _router: Router,
    private _route: ActivatedRoute
  ) {
    super(injector);
  }
  
  createProtocoloAvaliacao(): void {
    // let createProtocoloAvaliacaoDialog: BsModalRef;
    // createProtocoloAvaliacaoDialog = this._modalService.show(
    //   CreateProtocoloAvaliacaoDialogComponent,
    //   {
    //     class: 'modal-lg',
    //   }
    // );

    // createProtocoloAvaliacaoDialog.content.onSave.subscribe(() => {
    //   this.refresh();
    // });
  }

  detailsProtocoloAvaliacao(protocoloAvaliacao: ProtocoloAvaliacaoDto): void {
    this._router.navigate(['details', protocoloAvaliacao.id], { relativeTo : this._route });
  }

  clearFilters(): void {
    this.keyword = '';
    this.isActive = undefined;
    this.getDataPage(1);
  }

  protected list(
    request: PagedProtocolosAvaliacaoRequestDto,
    pageNumber: number,
    finishedCallback: Function
  ): void {
    request.keyword = this.keyword;

    this._protocoloAvaliacaoService
      .getAll(
        request.keyword,
        undefined,
        undefined,
        request.skipCount,
        request.maxResultCount
      )
      .pipe(
        finalize(() => {
          finishedCallback();
        })
      )
      .subscribe((result: ProtocoloAvaliacaoDtoPagedResultDto) => {
        this.protocolosAvaliacao = result.items;
        this.showPaging(result, pageNumber);
      });
  }

  protected delete(pesssoa: ProtocoloAvaliacaoDto): void {
    abp.message.confirm(
      "O seguinte protocolo de avaliao ser excludo do banco",
      undefined,
      (result: boolean) => {
        if (result) {
          this._protocoloAvaliacaoService.delete(pesssoa.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.refresh();
          });
        }
      }
    );
  }
}
