import {
    Component,
    Injector,
    OnInit,
    Input,
    EventEmitter,
    Output
} from '@angular/core';
import { ControlContainer, NgModelGroup} from '@angular/forms'
import { AppComponentBase } from '@shared/app-component-base';
import { CreateDominioDto, CreateHabilidadeDto, DominioDto, HabilidadeDto, ProtocoloAvaliacaoDto, TipoConfirmacaoHabilidadeDto } from '@shared/service-proxies/service-proxies';

@Component({
    selector: 'dominio-form',
    templateUrl: './dominio-form.component.html',
    viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup}]
})
export class DominioFormComponent extends AppComponentBase {
    @Input() disabled: boolean = false;
    @Input() dominio: CreateDominioDto | DominioDto;
    @Input() allHabilidades: HabilidadeDto[];
    @Input() tiposConfirmacao: TipoConfirmacaoHabilidadeDto[] = [];
    public constructor(
        injector: Injector
    ) {
        super(injector);
    }
    get isNovo() { return this.dominio instanceof CreateDominioDto }
    createHabilidade() {
        if (this.dominio instanceof CreateDominioDto)
            this.dominio.habilidades.push(new CreateHabilidadeDto());
    }

    removeHabilidade(i: number) {
        if (this.dominio instanceof CreateDominioDto)
            this.dominio.habilidades.splice(i,1);
    }
    get habilidades(): HabilidadeDto[] | CreateHabilidadeDto[] {
        if (this.dominio instanceof CreateDominioDto)
            return this.dominio.habilidades;
        else if (this.dominio instanceof DominioDto) {
            let domId = this.dominio.id;
            return this.allHabilidades.filter(h => h.dominioId == domId);
        }
    }
}