import { AppComponentBase } from '@shared/app-component-base';
import { ChangeDetectorRef, Component, EventEmitter, Injector, Input, OnInit, Output, ViewChild } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ColaboradorServiceProxy,
  FuncaoOrgServiceProxy,
  FuncaoOrgDto,
  FuncaoOrgDtoPagedResultDto,
  UpdateColaboradorDto,
  ColabFuncaoOrgDto,
  CreateColabFuncaoOrgDto,
  Int64EntityDto,
  ColaboradorDto
} from '@shared/service-proxies/service-proxies';
import { PessoaDetailsBaseComponent } from '@app/pessoas/pessoa-details-base/pessoa-details-base.component';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { ControlContainer, NgModelGroup } from '@angular/forms';
import { SingleSelectorDialogComponent } from '@app/utils/select-habilidade-dialog/single-selector-dialog.component';

@Component({
  selector: 'funcoes-colaborador-details',
  templateUrl: './funcoes-colaborador-details.component.html',
  viewProviders: [{provide: ControlContainer, useExisting: NgModelGroup }]
})
export class FuncoesColaboradorDetailsComponent extends AppComponentBase implements OnInit {
  
  constructor(injector: Injector,
    protected _colaboradorService: ColaboradorServiceProxy,
    protected _funcaoOrgService: FuncaoOrgServiceProxy,
    protected _changeDetector: ChangeDetectorRef,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  loadingFuncoes: boolean = false;
  loadingHistorico: boolean = false;
  get loading(): boolean { return this.loadingFuncoes || this.loadingHistorico }
  
  private _colaboradorId: number;
  public get colaboradorId(): number  { return this._colaboradorId; }
  @Input()
  public set colaboradorId(v: number) { this._colaboradorId = v; this.refresh(); }

  @Input()  public funcoes: FuncaoOrgDto[];
  @Output() public funcoesChange: EventEmitter<FuncaoOrgDto[]> = new EventEmitter<FuncaoOrgDto[]>();
  @Input()  public historicoFuncoes: ColabFuncaoOrgDto[] = [];
  @Output() public historicoFuncoesChange: EventEmitter<ColabFuncaoOrgDto[]> = new EventEmitter<ColabFuncaoOrgDto[]>();
  ngOnInit(): void {
    this.loadingFuncoes = true;
    this.funcoes = [];
    this._funcaoOrgService.getAll(undefined, undefined, undefined).subscribe((funcs: FuncaoOrgDtoPagedResultDto) => {
      this.funcoes = funcs.items;
      this.funcoesChange.emit(this.funcoes);
      this.loadingFuncoes = false;
    });
    this.refresh();
  }
  
  @Input()  gerentes:   ColaboradorDto[];
  @Output() gerentesChange:   EventEmitter<ColaboradorDto[]>    = new EventEmitter<ColaboradorDto[]>();
  @Input()  gerentesCF: ColabFuncaoOrgDto[];
  @Output() gerentesCFChange: EventEmitter<ColabFuncaoOrgDto[]> = new EventEmitter<ColabFuncaoOrgDto[]>();
  refresh() {
    this.loadingHistorico = true;
    this.historicoFuncoes = [];
    this._colaboradorService.getAllColabFuncoes(this.colaboradorId, undefined, undefined, undefined, undefined, undefined, undefined, undefined, undefined)
    .subscribe((result) => {
      this.historicoFuncoes = result.items;
      this.historicoFuncoesChange.emit(this.historicoFuncoes);
      
      this.gerentes = [];
      this.gerentesChange.emit(this.gerentes);
      
      this.gerentesCF = [];
      this.gerentesCFChange.emit(this.gerentesCF);
      
      this.historicoFuncoes.forEach((cf: ColabFuncaoOrgDto) => {
        if (cf.gestorId != undefined) {
          this._colaboradorService.getColabFuncao(cf.gestorId)
          .subscribe((gestorCF: ColabFuncaoOrgDto) => {
            this.gerentesCF.push(gestorCF);
            this._colaboradorService.get(gestorCF.colaboradorId)
            .subscribe((gestor) => {
              this.gerentes.push(gestor);
            });
          });
        }
      });
      this.loadingHistorico = false;
    });
  }

  protected getTipoPessoa(): string {
    return "Colaborador";
  }

  getCFs(f: FuncaoOrgDto | undefined): ColabFuncaoOrgDto[] {
    if (f == undefined) return this.historicoFuncoes;
    return this.historicoFuncoes.filter(cf => cf.funcaoOrgId == f.id);
  }
  getCFsAtivos(f: FuncaoOrgDto | undefined): ColabFuncaoOrgDto[] {
    return this.getCFs(f).filter(cf => cf.dataFim == undefined);
  }
  hasFuncao(f: FuncaoOrgDto): boolean {
    return this.getCFsAtivos(f).length > 0;
  }

  busy: boolean = false;
  rmvFuncao(f: FuncaoOrgDto | ColabFuncaoOrgDto) {
    if (this.busy) return
    this.busy = true;
    let cfId: number;
    if (f instanceof ColabFuncaoOrgDto) {
      cfId = f?.id;
    } else {
      let cfs = this.getCFsAtivos(f);
      if (cfs.length == 0) return null;
      cfId = cfs[0].id;
    }
    this._colaboradorService.deleteColabFuncao(new Int64EntityDto({id: cfId})).subscribe((result) => {
      this.refresh();
      this.notify.info(this.l('SavedSuccessfully'));
      this.busy = false;
    });
  }
  addFuncao(f: FuncaoOrgDto) {
    if (this.busy) return
    this.busy = true;
    let dto = new CreateColabFuncaoOrgDto({ 
      colaboradorId: this.colaboradorId, 
      funcaoOrgId: f.id,
      gestorId: undefined
    });
    this._colaboradorService.createColabFuncao(dto).subscribe((result) => {
      this.refresh();
      this.notify.info(this.l('SavedSuccessfully'));
      this.busy = false;
    });
  }
  
  addFuncaoPopup() {
    let optionsData: FuncaoOrgDto[] = [];
    let options: string[] = [];
    this.funcoes.forEach((f) => {
      if (!this.hasFuncao(f)) {
        optionsData.push(f);
        options.push(f.nome);
      }
    });

    let selectFuncaoDialog: BsModalRef;
    selectFuncaoDialog = this._modalService.show(
      SingleSelectorDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          title:'SelectFuncao',
          options: options
        }
      }
    );

    selectFuncaoDialog.content.onSubmit.subscribe((i: number) => {
      this.addFuncao(optionsData[i]);
    });
  }
  podeAddFuncao() {
    return this.permission.isGranted("Business.Admin.People")
  }
}
