import { AppComponentBase } from '@shared/app-component-base';
import { Component, EventEmitter, Injector, Input, OnInit, Output } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { appModuleAnimation } from '@shared/animations/routerTransition';
import {
  ProtocoloAvaliacaoServiceProxy,
  DominioDto,
  ProtocoloAvaliacaoDto,
  UpdateProtocoloAvaliacaoDto,
  Int64EntityDto,
  TipoConfirmacaoHabilidadeDto,
  HabilidadeDto,
  NivelDto
} from '@shared/service-proxies/service-proxies';
import { BsModalRef, BsModalService } from 'ngx-bootstrap/modal';
import { Observable } from 'rxjs';
import { UpdateDominioDialogComponent } from '../dialogs/update-dominio-dialog/update-dominio-dialog.component';
import { AddHabilidadeDialogComponent } from '../dialogs/add-habilidade-dialog/add-habilidade-dialog.component';

@Component({
  selector: 'dominio-details',
  templateUrl: './dominio-details.component.html',
  animations: [appModuleAnimation()]
})
export class DominioDetailsComponent extends AppComponentBase implements OnInit {
  @Input() public protocoloAvaliacao: ProtocoloAvaliacaoDto;
  @Input() public nivel: NivelDto;
  @Input() public dominio: DominioDto;
  @Input() public habilidades: HabilidadeDto[];
  public habilidadesCodificadas: HabilidadeDto[];
  public habilidadesPersonalizadas: HabilidadeDto[];
  @Input() public tiposConfirmacao: TipoConfirmacaoHabilidadeDto[];
  public deetsHidden: boolean = true;
  constructor(injector: Injector,
    protected _protocoloAvaliacaoService: ProtocoloAvaliacaoServiceProxy,
    protected _modalService: BsModalService,
    protected _router: Router,
    protected _route: ActivatedRoute) {
    super(injector);
  }

  private _splitHabilidades() {
    this.habilidadesCodificadas = this.habilidades.filter(h => !h.isPersonalizada);
    this.habilidadesPersonalizadas = this.habilidades.filter(h =>  h.isPersonalizada);
  }
  ngOnInit() {
    // console.log(this.tiposConfirmacao);
    // console.log(this.dominio);
    if (this.habilidades != undefined) {
      this.habilidades = this.habilidades.filter(h => h.dominioId == this.dominio.id);
      this._splitHabilidades();
    }
    else 
      this._protocoloAvaliacaoService
      .getAllHabilidades(undefined, undefined, undefined, this.dominio.id, undefined, undefined, undefined, undefined, undefined, undefined)
      .subscribe((result) => { this.habilidades = result.items; this._splitHabilidades(); });
  }

  @Output() public onDelete = new EventEmitter();
  @Output() public onUpdate = new EventEmitter();
  
  updateDominio() {
    let updateDominioDialog: BsModalRef;
    updateDominioDialog = this._modalService.show(
      UpdateDominioDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          nivel: this.nivel,
          dominio: this.dominio,
          tiposConfirmacao: this.tiposConfirmacao
        }
      }
    );

    updateDominioDialog.content.onSave.subscribe(() => {
      this.onUpdate.emit();
    });
  }
  deleteDominio() {
    abp.message.confirm(
      "O seguinte dominio ser excludo do banco: " + this.dominio.nome,
      undefined,
      (result: boolean) => {
        if (result) {
          this._protocoloAvaliacaoService.deleteDominio(this.dominio.id).subscribe(() => {
            abp.notify.success(this.l('SuccessfullyDeleted'));
            this.onDelete.emit();
          });
        }
      }
    );
  }

  addHabilidade(codificada: boolean) {
    let addHabilidadeDialog: BsModalRef;
    addHabilidadeDialog = this._modalService.show(
      AddHabilidadeDialogComponent,
      {
        class: 'modal-lg',
        initialState: {
          protocoloAvaliacao: this.protocoloAvaliacao,
          dominio: this.dominio,
          codificada: codificada,
          tiposConfirmacao: this.tiposConfirmacao
        }
      }
    );

    addHabilidadeDialog.content.onSave.subscribe(() => {
      this.onUpdate.emit();
    });

  }
}
